import React, { Component } from "react";
// Redux Connection
import { connect } from "react-redux";
import { TableStripedWrapper, CommonPageLoader } from "component/common";
import { InvoiceManagementListHeader } from "Data/InvoiceManagement";
import { getUnpaidInvoiceList, invoiceStatusChange } from "action/InvoiceAct";
import { bindActionCreators } from "redux";
import { debounceFunction } from "service/debounce";
import "./style.scss";
import ManagementPageTitle from "component/common/ManagementPageTitle";
import search from "assets/svg/search.svg";
import { history } from "service/helpers";
import { getShortData } from "service/helperFunctions";
export class InvoiceManagementListClass extends Component {
  state = {
    searchValue: "",
    headerDetails: InvoiceManagementListHeader,
    list: [],
  };

  //lifecycles
  componentDidMount() {
    this._getInvoiceList(1);
  }

  //get Unpaid Invoice List
  _getInvoiceList = (page) => {
    let { searchValue } = this.state;
    let payload = {
      page,
      search: searchValue,
    };
    this.setState({ isLoading: true });
    this.props
      .getUnpaidInvoiceList(payload)
      .then((data) => {
        this.setState({
          list: data.list,
          pageMetaData: data.pageMetaData,
          isLoading: false,
        });
      })
      .catch(() => {
        this.setState({
          isLoading: false,
        });
      });
  };

  //handle Change
  handleChange = ({ target: { name, value } }) => {
    this.setState({
      searchValue: value,
    });
    debounceFunction(() => this.searchFunction(), 500);
  };

  //search Function
  searchFunction = () => {
    this._getInvoiceList(1);
  };

  //handle Input
  handleInput = (newValue, _id) => {
    this.setState(
      {
        selectedName: newValue.value,
      },
      () => {
        let body = {
          _id,
          status: newValue.value == "activate" ? 2 : 0,
        };
        this.props.invoiceStatusChange(body).then(() => {
          this._getInvoiceList(1);
          this.setState({ selectedName: "" });
        });
      }
    );
  };

  //handle Pagination
  handlePagination = (page) => {
    this._getInvoiceList(page);
  };

  render() {
    let { headerDetails, list, isLoading, pageMetaData, searchValue } =
      this.state;
    if (isLoading) return <CommonPageLoader />;
    return (
      <div className="common-table">
        <div className="d-flex justify-content-between">
          <div className="d-flex align-items-end tab-head">
            <ManagementPageTitle title="INVOICE MANAGEMENT" titleStyle="mb-0" />
          </div>
        </div>
        <div className="bg-white product-inner-table">
          <div className="row px-3 py-2">
            <div className="col-6 text-left my-auto">
              <p className="fs-20 text-black-22 mb-0 fw-500">
                Unpaid Invoices Table
              </p>
            </div>
            <div className="col-6 d-flex justify-content-end text-right">
              <div className="search-container-area">
                <div className="position-relative">
                  <img
                    className="mailIcon"
                    src={search}
                    alt="Logo"
                    height="15px"
                  />
                  <input
                    className="search-input"
                    placeholder="Search"
                    value={searchValue}
                    name="searchValue"
                    onChange={this.handleChange}
                  />
                </div>
              </div>
            </div>
          </div>
          <div className="custom-divider" />
          <div className="table-wrapper ">
            <TableStripedWrapper
              headerDetails={headerDetails}
              pageMeta={pageMetaData}
              placeHolder={""}
              search={this.state.search}
              queryHandler={this.handlePagination}
              handlePageChange={this._getInvoiceList}
              isInvoiceManagement={true}
            >
              {list.length !== 0 ? (
                <>
                  {list.map((item, index) => {
                    let {
                      userDetails = {},
                      created_at,
                      total,
                      paid = false,
                      orderRef,
                    } = item;

                    let { accountType, businessName, _id } = userDetails;

                    return (
                      <tr key={index} className="text-center">
                        <td className="text-black-24 text-center fs-14">
                          {orderRef ? orderRef : "-"}
                        </td>
                        <td className="text-black-24 text-center fs-14">
                          {businessName !== ""
                            ? getShortData(businessName, 30)
                            : "-"}
                        </td>
                        <td className="text-black-24 text-center fs-14 ">
                          {created_at ? created_at : "-"}
                        </td>
                        <td className="text-black-24 text-center fs-14">
                          {accountType && accountType == 1
                            ? "Master Account"
                            : accountType == 2
                            ? "Sub Account"
                            : "-"}
                        </td>
                        <td className="text-black-24 text-center fs-14 ">
                          {total ? "$ " + total : "-"}
                        </td>
                        <td
                          className={`${
                            !paid ? "text-red" : "text-success"
                          } fs-14 fw-700 word-break-normal`}
                        >
                          {!paid ? "Unpaid" : paid ? "Paid" : "-"}
                        </td>
                        <td>
                          <div className="pl-3">
                            <i
                              className="fs-18 icon-invoice pr-3 cursor-pointer fw-500"
                              onClick={() =>
                                history.push(
                                  `/admin/invoice-management/buyer/${_id}`
                                )
                              }
                            />
                          </div>
                        </td>
                      </tr>
                    );
                  })}
                </>
              ) : searchValue !== "" && list.length === 0 ? (
                <tr className="h3 text-center">
                  <td colSpan="8" className="py-4 text-center">
                    {"No search result found"}
                  </td>
                </tr>
              ) : (
                <tr className="h3 text-center">
                  <td colSpan="8" className="py-4 text-center">
                    {"No Record Found"}
                  </td>
                </tr>
              )}
            </TableStripedWrapper>
          </div>
        </div>
      </div>
    );
  }
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators(
    {
      getUnpaidInvoiceList,
      invoiceStatusChange,
    },
    dispatch
  );
};

export const InvoiceManagementList = connect(
  null,
  mapDispatchToProps
)(InvoiceManagementListClass);
