import React, { Component } from "react";
// Redux Connection
import { connect } from "react-redux";
import { TableStripedWrapper, CommonPageLoader } from "component/common";
import {
  subcategoryListHeader,
  customStyles,
} from "Data/SubcategoryManagement";
import { listSubCategory, listAllCategories } from "action/SubcategoryAct";
import { bindActionCreators } from "redux";
import { debounceFunction } from "service/debounce";
import Select, { components } from "react-select";
import "./style.scss";
import ManagementPageTitle from "component/common/ManagementPageTitle";
import { history } from "service/helpers";
export class SubcategoryListClass extends Component {
  state = {
    search: "",
    view: "list",
    headerDetails: subcategoryListHeader,
    placeholder: "",
    adminList: {},
    filter: true,
    isPopupOpen: false,
    isViewPopupOpen: false,
    list: [],
    pageMetaData: {},
    id: "",
    sortCriteria: "",
    sortOrder: "",
    searchValue: "",
    categoryId: "",
  };

  //handle toggle
  handleToggle = (name, id) => {
    if (id) {
      this.setState({ id });
    } else {
      this.setState({ id: "" });
    }
    this.setState((prevState) => ({
      [name]: !prevState[name],
    }));
  };

  //go to update
  gotoUpdateRoute = (_id, item) => {
    history.push({
      pathname: `/admin/subcategoryManagement/editSubcategory/${_id}`,
      state: item,
    });
  };

  //handle search
  handleSearch = (value, index) => {
    let { headerDetails } = this.state;

    headerDetails[index].searchValue = value;

    this.setState({ headerDetails }, () => {
      debounceFunction(() => {
        this._getSubCategoriesList(1);
      }, 1000);
    });
  };

  //lifecycles
  componentDidMount() {
    if (this.props.compType === "list") {
      this.setState({
        view: this.props.compType,
      });
      this._getCategoryOptions(true);
    }
    // this._getSubCategoriesList(1);
    if (this.props.compType === "view") {
      this.setState({
        view: this.props.compType,
      });
      this._getCategoryOptions(true);
    }
  }

  //get sub category list
  _getSubCategoriesList = (page) => {
    let { searchValue } = this.state;
    let payload = {
      page,
      search: searchValue,
    };
    this.setState({ isLoading: true });
    this.props
      .listSubCategory(payload)
      .then((data) => {
        this.setState({
          list: data.list,
          pageMetaData: data.pageMetaData,
          isLoading: false,
        });
      })
      .catch(() => {
        this.setState({
          isLoading: false,
        });
      });
  };

  //handle change
  handleChange = ({ target: { name, value } }) => {
    this.setState({
      [name]: value,
    });
    debounceFunction(() => this.SearchFunction(value), 1000);
  };

  //search function
  SearchFunction = () => {
    this._getSubCategoriesList(1);
  };

  //handle pagination
  handlePagination = (page) => {
    this._getSubCategoriesList(page);
  };

  //handle filter
  handleFilter = (sortKey, sortOrder) => {
    this.setState(
      {
        sortCriteria: sortKey,
        sortOrder,
      },
      () => {
        this._getSubCategoriesList(1);
      }
    );
  };

  //handle input
  handleInput = (newValue) => {
    this.setState(
      {
        sellerId: newValue.value,
        categoryId: newValue.label,
      },
      () => {
        this._getSubCategoriesList({ categoryId: this.state.sellerId });
      }
    );
  };

  //get category option
  _getCategoryOptions = (isFirstTimeApiCall) => {
    this.setState({ isLoading: true });
    this.props.listAllCategories().then((data) => {
      let options = [];
      data.map((list) => {
        options.push({
          value: list._id,
          label: list.name,
        });
      });
      this.setState(
        {
          categoryOptions: options,
        },
        () => {
          let { categoryOptions } = this.state;
          if (isFirstTimeApiCall) {
            if (categoryOptions.length !== 0) {
              this.setState(
                {
                  sellerId: categoryOptions[0].value,
                  categoryId: categoryOptions[0].label,
                },
                () => {
                  this._getSubCategoriesList({
                    categoryId: categoryOptions[0].value,
                  });
                }
              );
            }
          }
        }
      );
    });
  };

  render() {
    let {
      headerDetails,
      placeholder,
      list,
      isLoading,
      pageMetaData,
      view,
      sellerId,
      categoryOptions,
      searchInputData,
      categoryId,
    } = this.state;
    const DropdownIndicator = (props) => {
      return (
        components.DropdownIndicator && (
          <components.DropdownIndicator {...props}>
            <div className="d-flex flex-column">
              <span className={`icon-up fs-6`}></span>
              <span className={`icon-down fs-6`}></span>
            </div>
          </components.DropdownIndicator>
        )
      );
    };
    if (isLoading) return <CommonPageLoader />;
    return (
      <div className="common-table">
        <div className="d-flex justify-content-between">
          <div className="d-flex align-items-end tab-head">
            <ManagementPageTitle
              title="SUB-CATEGORIES MANAGEMENT"
              titleStyle="mb-0"
              titleDesc="Add and Edit Sub-Categories"
              titleDescStyle="mb-0"
            />
          </div>
        </div>
        <div className="bg-white product-inner-table">
          <div className="row px-3 py-2 align-item-center">
            <div className="col-6 text-left my-auto fs-20 text-black">
              <p className="table-head mb-0">
                {view === "list" ? "Sub-Category Table" : "View Sub-Category"}
              </p>
            </div>
          </div>

          <div className="custom-divider" />
          <div className="table-wrapper">
            <div className="row pt-3 pb-4 px-3 ">
              <div className="col-6">
                <div className="d-flex align-items-center">
                  <p className="fs-14 text-black-24 pl-2 mb-0 mr-2 fw-500">
                    Select Category for list
                  </p>
                  <Select
                    classNamePrefix="Select"
                    name={sellerId}
                    options={categoryOptions}
                    onChange={this.handleInput}
                    placeholder={"select category"}
                    styles={customStyles}
                    inputValue={searchInputData}
                    isSearchable={true}
                    value={
                      !!categoryOptions && categoryOptions.length > 0
                        ? categoryOptions.find(
                            (data) => data.value === sellerId
                          )
                          ? categoryOptions.find(
                              (data) => data.value === sellerId
                            )
                          : null
                        : null
                    }
                    components={{ DropdownIndicator }}
                  />
                </div>
              </div>
            </div>
            <TableStripedWrapper
              headerDetails={headerDetails}
              handlePageChange={this._getSubCategoriesList}
              placeHolder={placeholder}
              pageMeta={pageMetaData}
              search={this.state.search}
              handleSearch={this.handleSearch}
              queryHandler={this.handlePagination}
              handleFilter={this.handleFilter}
            >
              {list.length > 0 ? (
                <>
                  {list.map((item, index) => {
                    let { name, _id } = item;
                    return (
                      <tr key={index} className="text-center">
                        <td className="text-black-24 fs-14">
                          {pageMetaData.page === 1
                            ? index + 1
                            : (pageMetaData.page - 1) * 10 + index + 1}
                        </td>
                        <td className="text-black-24 fs-14">
                          {categoryId ? categoryId : "-"}
                        </td>
                        <td className="text-black-24 fs-14">
                          {name ? name : "-"}
                        </td>
                        <td>
                          <div className="d-flex justify-content-center">
                            <i
                              className="fs-18 icon-edit pr-2 cursor-pointer fw-500"
                              onClick={() => this.gotoUpdateRoute(_id, item)}
                            />
                          </div>
                        </td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                      </tr>
                    );
                  })}
                </>
              ) : (
                <tr className="h3 text-center">
                  <td colSpan="8" className="py-4 text-center">
                    {"No Record Found"}
                  </td>
                </tr>
              )}
            </TableStripedWrapper>
          </div>
        </div>
      </div>
    );
  }
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators(
    {
      listSubCategory,
      listAllCategories,
    },
    dispatch
  );
};

export const SubcategoryList = connect(
  null,
  mapDispatchToProps
)(SubcategoryListClass);
