import React, { Component } from "react";
import SimpleReactValidator from "simple-react-validator";
// Redux Connection

import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import {
  getCategoryList,
  updateProducts,
  getDropdownDataofProvince,
  uploadImageAPI,
  getSubCategoryList,
  getProductById,
} from "action/ProductAct";
import "./style.scss";
import {
  NormalInputProduct,
  NormalMultiSelect,
  NormalInputTextBox,
  NormalButton,
  CommonPageLoader,
} from "component/common";
import { Toast } from "service/toast";
import { Modal, ModalBody } from "reactstrap";
export class UpdateSingleProductPopupClass extends Component {
  state = {
    formFields: {
      productName: "",
      sku: "",
      price: "",
      size: "",
      brandName: "",
      gstHst: "",
      description: "",
      category: "",
      province: "",
      subCategory: "",
      commisions: "",
      sellerId: "",
      sellerName: "",
      status: "",
    },
    addImages: [
      { fileUrl: "", isImageAdded: true, isImageUploading: false },
      { fileUrl: "", isImageAdded: false, isImageUploading: false },
      { fileUrl: "", isImageAdded: false, isImageUploading: false },
      { fileUrl: "", isImageAdded: false, isImageUploading: false },
      { fileUrl: "", isImageAdded: false, isImageUploading: false },
    ],
    categoryOptions: [],
    provincesOptions: [],
    taxesOptions: [],
    subCategoryOptions: [],
    isLoading: false,
    isButtonClicked: false,
  };

  //validation
  validator = new SimpleReactValidator({
    element: (message) => (
      <span className="error-message text-danger fs-14">{message}</span>
    ),
    autoForceUpdate: this,
  });

  validatorSubCategory = new SimpleReactValidator({
    element: (message) => (
      <span className="error-message text-danger fs-14">{message}</span>
    ),
    autoForceUpdate: this,
  });

  //get Product By Id
  _getDetilsById = (id) => {
    let body = {
      id,
    };
    let { addImages } = this.state;
    this.setState({ isLoading: true });
    this.props.getProductById(body).then((data) => {
      if (data !== null) {
        if (data.productImg && data.productImg.length !== 0) {
          addImages[0].fileUrl = data.productImg[0];
          if (data.productImg[0]) {
            addImages[1].isImageAdded = true;
          }
          addImages[1].fileUrl = data.productImg[1] ? data.productImg[1] : "";
          if (data.productImg[1]) {
            addImages[2].isImageAdded = true;
          }
          addImages[2].fileUrl = data.productImg[2] ? data.productImg[2] : "";
          if (data.productImg[2]) {
            addImages[3].isImageAdded = true;
          }
          addImages[3].fileUrl = data.productImg[3] ? data.productImg[3] : "";
          if (data.productImg[3]) {
            addImages[4].isImageAdded = true;
          }
          addImages[4].fileUrl = data.productImg[4] ? data.productImg[4] : "";
          this.setState({ addImages });
        }
        let { commisions = "" } = data;
        this.setState({
          formFields: {
            productName: data.productName,
            sku: data.sku ? data.sku.toString() : "",
            price: data.price ? data.price.toString() : "",
            size: data.size ? data.size.toString() : "",
            brandName: data.brand ? data.brand : "",
            gstHst: data.taxes ? data.taxes : "",
            description: data.desc ? data.desc : "",
            category: data.category !== null ? data.category._id : "",
            province: data.province ? data.province : "",
            subCategory: data.subCategory !== null ? data.subCategory._id : "",
            commisions:
              commisions !== null && commisions !== ""
                ? data.commisions.toString()
                : "",
            sellerId: data.seller ? data.seller._id : "",
            sellerName: data.seller
              ? data.seller.firstName + " " + data.seller.lastName
              : "",
            status:
              data.status && data.status == 1
                ? "active"
                : data.status == 0
                ? "inactive"
                : "",
          },
          isLoading: false,
        });
        if (data.category !== null) {
          let body = {
            categoryId: data.category._id,
          };
          this._getSubCategoryList(body);
        }
      }
    });
  };

  //get Category List
  _getCategoryList = () => {
    this.props.getCategoryList().then((data) => {
      let options = [];
      data.map((list) => {
        options.push({ value: list._id, label: list.name });
      });
      this.setState({
        categoryOptions: options,
      });
    });
  };

  //get Dropdown Data of Province
  _getDropdownDataofProvince = () => {
    this.props.getDropdownDataofProvince().then((data) => {
      let { provinces, taxes } = data;
      let provincesOption = [],
        taxesOption = [];
      provinces.map((list) => {
        provincesOption.push({ value: list, label: list });
      });
      taxes.map((list) => {
        taxesOption.push({ value: list, label: list });
      });
      this.setState({
        provincesOptions: provincesOption,
        taxesOptions: taxesOption,
      });
    });
  };

  //get SubCategory List
  _getSubCategoryList = (body) => {
    this.props.getSubCategoryList(body).then((data) => {
      let options = [];
      data.list.map((list) => {
        options.push({ value: list._id, label: list.name });
      });
      this.setState({
        subCategoryOptions: options,
      });
    });
  };

  //handle input
  handleInput = ({ target: { name, value } }) => {
    let { formFields } = this.state;
    formFields[name] = value;

    if (name === "category") {
      let body = {
        categoryId: value,
      };
      formFields["subCategory"] = "";
      this.setState({
        formFields,
      });
      this._getSubCategoryList(body);
    }

    this.setState({
      formFields,
    });
  };

  //upload File
  uploadFile = (e, index) => {
    let file = e.target.files[0];
    if (file !== undefined) {
      let { addImages } = this.state;
      addImages[index].isImageUploading = true;
      addImages[index].fileUrl = URL.createObjectURL(file);
      this.setState({ addImages });
      const config = {
        onUploadProgress: function (progressEvent) {
          var percentCompleted = Math.round(
            (progressEvent.loaded * 100) / progressEvent.total
          );
          document.getElementById(
            `image-upload-${index}`
          ).innerText = `${percentCompleted}%`;
        },
      };

      const formData = new FormData();
      formData.append("image", file);
      this.props.uploadImageAPI(formData, config).then((data) => {
        addImages[index].fileUrl = data.url;
        addImages[index].isImageUploading = false;
        if (index !== 4) {
          addImages[index + 1].isImageAdded = true;
        }
        this.setState({ addImages });
      });
    }
  };

  //handle Delete Image
  handleDeleteImage = (index) => {
    let { addImages } = this.state;
    addImages[index] = {
      fileUrl: "",
      isImageAdded: true,
      isImageUploading: false,
    };
    this.setState({ addImages });
  };

  //handle Cancel
  handleCancel = () => {
    this.validator.hideMessages();
    this.setState({
      formFields: {
        productName: "",
        sku: "",
        price: "",
        size: "",
        brandName: "",
        gstHst: "",
        description: "",
        category: "",
        province: "",
        commisions: "",
        sellerId: "",
        sellerName: "",
        status: "",
      },
      addImages: [
        { fileUrl: "", isImageAdded: true, isImageUploading: false },
        { fileUrl: "", isImageAdded: false, isImageUploading: false },
        { fileUrl: "", isImageAdded: false, isImageUploading: false },
        { fileUrl: "", isImageAdded: false, isImageUploading: false },
        { fileUrl: "", isImageAdded: false, isImageUploading: false },
      ],
    });
    this.props.toggle();
  };

  //handle Submit
  handleSubmit = () => {
    if (this.validator.allValid()) {
      let { formFields, addImages, subCategoryOptions } = this.state;
      let productImgArray = [];
      addImages.map((list) => {
        if (list.fileUrl !== "") {
          productImgArray.push(list.fileUrl);
        }
      });
      if (productImgArray.length !== 0) {
        this.setState({ isButtonClicked: true }, () => {
          let {
            productName,
            sku,
            price,
            size,
            brandName,
            gstHst,
            description,
            category,
            province,
            subCategory,
            commisions,
            sellerId,
            status,
          } = formFields;
          let { updateProducts } = this.props;
          let body = {
            productName,
            price: parseFloat(parseFloat(price).toFixed(2)),
            brand: brandName,
            province: province,
            taxes: gstHst,
            categoryId: category,
            subCategoryId: subCategory !== "" ? subCategory : null,
            desc: description,
            size: parseInt(size),
            sku: sku,
            status: status == "inactive" ? 0 : 1,
            productImg: [],
            commisions: parseFloat(parseFloat(commisions).toFixed(2)),
            sellerId,
          };
          let uploadingIndex = "";

          addImages.map((list, index) => {
            if (list.isImageUploading == true) {
              uploadingIndex = index;
            }
            if (list.fileUrl !== "") {
              body.productImg.push(list.fileUrl);
            }
          });
          let payload = {
            id: this.props.productId,
          };
          if (addImages[uploadingIndex] == undefined) {
            if (subCategoryOptions.length !== 0) {
              if (this.validatorSubCategory.allValid()) {
                updateProducts(payload, body)
                  .then(() => {
                    this.props._getProductsList();
                    this.setState({ isButtonClicked: false });
                    this.handleCancel();
                    this.validator.hideMessages();
                  })
                  .catch(() => {
                    this.setState({ isButtonClicked: false });
                  });
              } else {
                this.setState({ isButtonClicked: false });
                this.validatorSubCategory.showMessages();
              }
            } else {
              updateProducts(payload, body)
                .then(() => {
                  this.props._getProductsList();
                  this.setState({ isButtonClicked: false });
                  this.handleCancel();
                  this.validator.hideMessages();
                })
                .catch(() => {
                  this.setState({ isButtonClicked: false });
                });
            }
          } else {
            this.setState({ isButtonClicked: false });
            Toast({
              type: "error",
              message: "Please wait image is uploading",
            });
          }
        });
      } else {
        Toast({
          type: "error",
          message: "Please upload atleast one product image.",
        });
      }
    } else {
      this.validator.showMessages();
    }
  };

  //life cycles
  componentDidUpdate(prevProps) {
    if (prevProps !== this.props) {
      if (this.props.productId !== "" && this.props.isOpenPopup) {
        this._getDetilsById(this.props.productId);
        this._getCategoryList();
        this._getDropdownDataofProvince();
      }
    }
  }

  // handle CheckBox
  handleCheckBox = (name, value) => {
    let { formFields } = this.state;
    formFields[name] = value;
    this.setState({ formFields });
  };

  render() {
    let {
      formFields,
      categoryOptions,
      addImages,
      provincesOptions,
      taxesOptions,
      subCategoryOptions,
      isLoading,
      isButtonClicked,
    } = this.state;
    let {
      productName,
      sku,
      price,
      size,
      brandName,
      gstHst,
      category,
      subCategory,
      province,
      description,
      commisions,
      sellerName,
      status,
    } = formFields;

    let {
      className = "modal-dialog-centered updating-product-popup",
      isOpenPopup = true,
    } = this.props;

    if (isLoading) return <CommonPageLoader />;

    return (
      <Modal isOpen={isOpenPopup} className={className} size="lg">
        <ModalBody>
          <div className="d-flex justify-content-between w-100 align-items-center mb-4 mt-3">
            <h1 className="fs-30 text-black-22 fw-400 mb-0 text-uppercase">
              Edit Product
            </h1>
            <i
              className="fs-22 icon-close-round cursor-pointer"
              onClick={() => this.handleCancel()}
            ></i>
          </div>
          <>
            <div className="row">
              <div className="col-12">
                <div className="row">
                  <div className="col-6 pr-md-4">
                    <NormalInputProduct
                      label="Seller Name"
                      type="text"
                      value={sellerName}
                      placeholder="Seller Name"
                      name="sellerName"
                      disabled={true}
                    />
                  </div>
                  <div className="col-6 pl-md-4 pr-3">
                    <NormalInputProduct
                      label="Commissions*"
                      type="number"
                      onChange={this.handleInput}
                      value={commisions}
                      placeholder="Enter Commissions"
                      name="commisions"
                    />
                    {this.validator.message(
                      "commisions",
                      commisions,
                      "required|max:6"
                    )}
                  </div>
                </div>
              </div>
            </div>
            <div className="row">
              <div className="col-12">
                <div className="row">
                  <div className="col-6 pr-4">
                    <NormalInputProduct
                      label="Product Name"
                      type="text"
                      onChange={this.handleInput}
                      value={productName}
                      placeholder="Product Name"
                      name="productName"
                    />
                    {this.validator.message(
                      "Product Name",
                      productName,
                      "required|max:30"
                    )}
                  </div>
                  <div className="col-3 px-4">
                    <NormalInputProduct
                      label="SKU #"
                      max={10}
                      type="text"
                      onChange={this.handleInput}
                      value={sku}
                      placeholder="SKU #"
                      name="sku"
                    />
                    {this.validator.message("sku", sku, "required|alpha_num")}
                  </div>
                  <div className="col-3 pl-4">
                    <NormalInputProduct
                      label="Price"
                      type="number"
                      onChange={this.handleInput}
                      value={price}
                      placeholder="Price"
                      name="price"
                    />
                    {this.validator.message("price", price, "required|max:10")}
                  </div>
                </div>
              </div>
            </div>
            <div className="row">
              <div className="col-12">
                <div className="row">
                  <div className="col-6 pr-4">
                    <NormalInputProduct
                      label="Brand Name"
                      type="text"
                      onChange={this.handleInput}
                      value={brandName}
                      placeholder="Brand Name"
                      name="brandName"
                    />
                    {this.validator.message(
                      "brandName",
                      brandName,
                      "required|max:30"
                    )}
                  </div>
                  <div className="col-3 px-4">
                    <NormalInputProduct
                      label="Size"
                      type="number"
                      onChange={this.handleInput}
                      value={size}
                      placeholder="Size"
                      name="size"
                    />
                    {this.validator.message("size", size, "required|max:10")}
                  </div>
                  <div className="col-3 pl-md-4">
                    <NormalMultiSelect
                      label="Province"
                      placeholder="Province"
                      value={province}
                      arrow={true}
                      name="province"
                      options={provincesOptions}
                      handleChange={this.handleInput}
                    />
                    {this.validator.message("province", province, "required")}
                  </div>
                </div>
              </div>
            </div>
            <div className="row">
              <div className="col-12">
                <div className="row">
                  <div className="col-6 pr-md-4">
                    <NormalMultiSelect
                      label="Category"
                      placeholder="Category Name"
                      value={category}
                      arrow={true}
                      name="category"
                      options={categoryOptions}
                      handleChange={this.handleInput}
                    />
                    {this.validator.message("category", category, "required")}
                  </div>
                  <div className="col-3 px-md-4">
                    <NormalMultiSelect
                      label="Sub-Category"
                      placeholder="Sub-Category"
                      value={subCategory}
                      arrow={true}
                      name="subCategory"
                      options={subCategoryOptions}
                      handleChange={this.handleInput}
                    />
                    {subCategoryOptions.length !== 0
                      ? this.validatorSubCategory.message(
                          "sub category",
                          subCategory,
                          "required"
                        )
                      : null}
                  </div>
                  <div className="col-3 pl-md-4">
                    <NormalMultiSelect
                      label="GST, HST %"
                      placeholder="GST, HST %"
                      value={gstHst}
                      arrow={true}
                      name="gstHst"
                      options={taxesOptions}
                      handleChange={this.handleInput}
                    />
                    {this.validator.message("gstHst", gstHst, "required")}
                  </div>
                </div>
              </div>
            </div>
            <div className="row mt-4 pt-2">
              <div className="col-12">
                <div className="row">
                  <div className="col-6 pr-md-5">
                    <div>
                      <label className="font-weight-normal fs-30 text-black-22 mb-3 text-uppercase">
                        Add Images
                      </label>
                    </div>
                    <div className="row">
                      <div className="col-11 px-3">
                        <div className="d-flex flex-wrap">
                          {addImages.map((list, index) => (
                            <div className="upload-image-card" key={index}>
                              {list.isImageUploading ? (
                                <div className="image-card d-flex justify-content-center align-items-center">
                                  <div id={`image-upload-${index}`}></div>
                                </div>
                              ) : list.fileUrl !== "" ? (
                                <div className="image-card position-relative">
                                  <img
                                    src={list.fileUrl}
                                    alt="file"
                                    className="w-100 h-100"
                                  />
                                  <div
                                    className="image-delete-area"
                                    onClick={() =>
                                      this.handleDeleteImage(index)
                                    }
                                  >
                                    <i className="icon-Delete fs-16 text-white cursor-pointer"></i>
                                  </div>
                                </div>
                              ) : list.isImageAdded ? (
                                <div className="image-card btn-file-input">
                                  <button>
                                    {"+"}
                                    <input
                                      type="file"
                                      className=""
                                      accept="image/png, image/jpeg, image/jpg"
                                      onChange={(e) =>
                                        this.uploadFile(e, index)
                                      }
                                    ></input>
                                  </button>
                                </div>
                              ) : (
                                <div className="image-card disabled d-flex justify-content-center align-items-center">
                                  {"+"}
                                </div>
                              )}
                            </div>
                          ))}
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="col-6 pr-md-4">
                    <div>
                      <label className="font-weight-normal fs-30 text-black-22 mb-3 text-uppercase">
                        {"Description"}
                      </label>
                    </div>
                    <NormalInputTextBox
                      className="form-control mb-2 mt-1"
                      value={description}
                      showCount={true}
                      name="description"
                      onChange={this.handleInput}
                      maxlength={500}
                    />
                    {this.validator.message(
                      "description",
                      description,
                      "required|max:500"
                    )}
                  </div>
                </div>
              </div>
            </div>
          </>
          <div className="row mt-5 mb-4 py-3">
            <div className="col-12 d-flex pr-4">
              <div className="d-flex flex-column">
                <div className="d-flex">
                  <div className="custom-control custom-checkbox mt-2">
                    <label className="position-checkbox">
                      <input
                        type="checkbox"
                        name="status"
                        value="active"
                        checked={status == "active" ? true : false}
                        onChange={(e) =>
                          this.handleCheckBox(e.target.name, e.target.value)
                        }
                      />
                      <span className="checkbox-tick ">
                        <i className="icon-tick-transparent "></i>
                      </span>
                      <span className="label-txt fs-16">Active</span>
                    </label>
                  </div>
                  <div className="custom-control custom-checkbox mt-2">
                    <label className="position-checkbox">
                      <input
                        type="checkbox"
                        name="status"
                        value="inactive"
                        checked={status == "inactive" ? true : false}
                        onChange={(e) =>
                          this.handleCheckBox(e.target.name, e.target.value)
                        }
                      />
                      <span className="checkbox-tick ">
                        <i className="icon-tick-transparent "></i>
                      </span>
                      <span className="label-txt fs-16">Inactive</span>
                    </label>
                  </div>
                </div>
                <div className="">
                  {this.validator.message("status", status, "required")}
                </div>
              </div>
              <div className="d-flex btn-container-one">
                <div className="">
                  <NormalButton
                    label="Modify Product"
                    mainbg={true}
                    onClick={() => this.handleSubmit()}
                    className="ml-4"
                    disabled={isButtonClicked}
                  />
                </div>
              </div>
            </div>
          </div>
        </ModalBody>
      </Modal>
    );
  }
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators(
    {
      getCategoryList,
      updateProducts,
      uploadImageAPI,
      getDropdownDataofProvince,
      getSubCategoryList,
      getProductById,
    },
    dispatch
  );
};

export const UpdateSingleProductPopup = connect(
  null,
  mapDispatchToProps
)(UpdateSingleProductPopupClass);
