import React, { Component } from "react";
// Redux Connection
import { connect } from "react-redux";
import { TableStripedWrapper, CommonPageLoader } from "component/common";
import { LoyaltyPointsListHeader } from "Data/LoyaltyPoints";
import { getLoyaltyPoints } from "action/LoyaltyPointsAct";
import { bindActionCreators } from "redux";
import { debounceFunction } from "service/debounce";
import "./style.scss";
import ManagementPageTitle from "component/common/ManagementPageTitle";
import search from "assets/svg/search.svg";
import { history } from "service/helpers";
import { getShortData } from "service/helperFunctions";
export class LoyaltyPointsListClass extends Component {
  state = {
    searchValue: "",
    headerDetails: LoyaltyPointsListHeader,
    list: [],
  };

  //lifecycles
  componentDidMount() {
    this.setState({ isLoading: true });
    this._getLoyaltyPointsList(1);
  }

  //get Loyalty Points
  _getLoyaltyPointsList = (page) => {
    let { searchValue } = this.state;
    let payload = {
      page,
      search: searchValue,
    };
    this.props
      .getLoyaltyPoints(payload)
      .then((data) => {
        this.setState({
          list: data.loyaltyData,
          pageMetaData: data.pageMetaData,
          isLoading: false,
        });
      })
      .catch(() => {
        this.setState({ isLoading: false });
      });
  };

  //handle Change
  handleChange = ({ target: { value } }) => {
    this.setState({
      searchValue: value,
    });
    debounceFunction(() => this.searchFunction(), 500);
  };

  //search Function
  searchFunction = () => {
    this._getLoyaltyPointsList(1);
  };

  //handle Pagination
  handlePagination = (page) => {
    this.setState({ isLoading: true });
    this._getLoyaltyPointsList(page);
  };

  render() {
    let { headerDetails, list, isLoading, pageMetaData, searchValue } =
      this.state;
    if (isLoading) return <CommonPageLoader />;
    return (
      <>
        <div className="common-table">
          <div className="d-flex justify-content-between">
            <div className="d-flex align-items-end tab-head">
              <ManagementPageTitle
                title="LOYALTY POINTS MANAGEMENT"
                titleStyle="mb-0"
              />
            </div>
          </div>
          <div className="bg-white product-inner-table">
            <div className="row px-3 py-2">
              <div className="col-6 text-left my-auto">
                <p className="fs-20 text-black-22 mb-0 fw-500">Points Table</p>
              </div>
              <div className="col-6 d-flex justify-content-end text-right">
                <div className="search-container-area">
                  <div className="position-relative">
                    <img
                      className="mailIcon"
                      src={search}
                      alt="Logo"
                      height="15px"
                    />
                    <input
                      className="search-input"
                      placeholder="Search"
                      value={searchValue}
                      name="searchValue"
                      onChange={this.handleChange}
                    />
                  </div>
                </div>
              </div>
            </div>
            <div className="custom-divider" />
            <div className="table-wrapper ">
              <TableStripedWrapper
                headerDetails={headerDetails}
                pageMeta={pageMetaData}
                placeHolder={""}
                search={this.state.search}
                queryHandler={this.handlePagination}
                handlePageChange={this._getLoyaltyPointsList}
                isInvoiceManagement={true}
              >
                {list.length !== 0 ? (
                  <>
                    {list.map((item, index) => {
                      let {
                        businessName = "",
                        email,
                        businessLicense,
                        status,
                        loyaltyPoints,
                        _id,
                        request,
                      } = item;

                      return (
                        <tr key={index} className="text-center">
                          <td className="text-black-24 text-center fs-14">
                            {index + 1}
                          </td>
                          <td className="text-black-24 text-center fs-14">
                            {businessName !== ""
                              ? getShortData(businessName, 30)
                              : "-"}
                          </td>
                          <td className="text-black-24 text-center fs-14 ">
                            {email}
                          </td>
                          <td className="text-black-24 text-center fs-14">
                            {businessLicense ? businessLicense : "-"}
                          </td>
                          <td className="text-black-24 text-center fs-14 ">
                            {loyaltyPoints ? loyaltyPoints : "-"}
                          </td>
                          <td
                            className={`text-yellow fs-14 fw-700 word-break-normal`}
                          >
                            {request ? "Request" : "-"}
                          </td>
                          <td
                            className={`${
                              status == 2 ? "text-red" : "text-success"
                            } fs-14 fw-700 word-break-normal`}
                          >
                            {status == 1
                              ? "Active"
                              : status == 2
                              ? "Inactive"
                              : "-"}
                          </td>
                          <td>
                            <div className="pl-3">
                              <i
                                className="fs-18 icon-edit pr-3 cursor-pointer fw-500"
                                onClick={() =>
                                  history.push(
                                    `/admin/loyalty-points-Management/${_id}`
                                  )
                                }
                              />
                            </div>
                          </td>
                        </tr>
                      );
                    })}
                  </>
                ) : searchValue !== "" && list.length === 0 ? (
                  <tr className="h3 text-center">
                    <td colSpan="8" className="py-4 text-center">
                      {"No search result found"}
                    </td>
                  </tr>
                ) : (
                  <tr className="h3 text-center">
                    <td colSpan="8" className="py-4 text-center">
                      {"No Record Found"}
                    </td>
                  </tr>
                )}
              </TableStripedWrapper>
            </div>
          </div>
        </div>
      </>
    );
  }
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators(
    {
      getLoyaltyPoints,
    },
    dispatch
  );
};

export const LoyaltyPointsList = connect(
  null,
  mapDispatchToProps
)(LoyaltyPointsListClass);
