import React, { Component } from "react";
import "./style.scss";
import SimpleReactValidator from "simple-react-validator";
import { getReferalList, addReferals } from "action/SellerAct";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import { NormalButton } from "component/common";
import ManagementPageTitle from "component/common/ManagementPageTitle";
import { NormalInput } from "component/common/NormalInput";

export class AddEditReferalManagementClass extends Component {
  constructor(props) {
    super(props);
    this.state = {
      view: "add",
      isButtonClicked: false,
      referalData: [
        { name: "Referral", percentage: "" },
        { name: "Referral", percentage: "" },
        { name: "Referral", percentage: "" },
        { name: "Referral", percentage: "" },
        { name: "Referral", percentage: "" },
        { name: "Referral", percentage: "" },
        { name: "Referral", percentage: "" },
      ],
    };

    this.handleChange = this.handleChange.bind(this);
  }

  //validation
  validator = new SimpleReactValidator({
    element: (message) => (
      <span className="error-message text-danger fs-14">{message}</span>
    ),
    autoForceUpdate: this,
  });

  //lifecycles
  componentDidMount = () => {
    this._getReferalList();
  };

  //get Referal List
  _getReferalList = () => {
    let { getReferalList } = this.props;
    getReferalList().then((data) => {
      const { referralPercentageList } = data;
      let referalDataList = [];
      referralPercentageList.map((list) => {
        referalDataList.push({
          name: "Referral",
          percentage: list.percentage.toString(),
        });
      });
      this.setState({
        referalData: [...referalDataList],
      });
    });
  };

  //handle Change
  handleChange = ({ target: { name, value } }, index) => {
    let { referalData } = this.state;
    referalData[index][name] = value;
    this.setState({
      referalData,
    });
  };

  //handle submit
  handleSubmit = (_id) => {
    if (this.validator.allValid()) {
      let { referalData } = this.state;
      this.setState({
        isButtonClicked: true,
      });
      let body = {
        referralPercentageList: [],
      };
      referalData.map((list) => {
        body.referralPercentageList.push({
          percentage: parseFloat(parseFloat(list.percentage).toFixed(2)),
        });
      });
      if (referalData.length === body.referralPercentageList.length) {
        this.props
          .addReferals(body)
          .then(() => {
            this.setState({
              isButtonClicked: false,
            });
          })
          .catch(() => {
            this.setState({
              isButtonClicked: false,
            });
          });
      }
    } else {
      this.validator.showMessages();
    }
  };

  //handle close
  handleClose = () => {
    this.validator.hideMessages();
    this.setState({
      referalData: [
        { name: "Referral", percentage: "" },
        { name: "Referral", percentage: "" },
        { name: "Referral", percentage: "" },
        { name: "Referral", percentage: "" },
        { name: "Referral", percentage: "" },
        { name: "Referral", percentage: "" },
        { name: "Referral", percentage: "" },
      ],
    });
  };

  render() {
    let { referalData, isButtonClicked } = this.state;
    return (
      <div className="outer">
        <div className="w-100 referal-form">
          <ManagementPageTitle
            title="Referral MANAGEMENT"
            titleStyle="mb-0"
            titleDesc=""
            titleDescStyle="mb-0"
          />
          <div className="referal-form-box">
            <p className="my-0 px-3 pt-3 pb-2 fs-20 text-black-22 fw-500">
              {"Referrals"}
            </p>
            <div className="custom-divider" />
            <div className="px-4">
              <div className="row">
                <div className="col-12 mt-4 mb-1">
                  <div className="row align-items-center">
                    <div className="col-2">
                      <h5 className="mb-0 fs-16 fw-600 text-black-24">
                        Referrals
                      </h5>
                    </div>
                    <div className="col-3">
                      <h5 className="mb-0 fs-16 fw-600 text-black-22">
                        Enter % Amount
                      </h5>
                    </div>
                  </div>
                </div>
                {referalData.map((list, index) => (
                  <div className="col-12 mt-4 mb-1" key={index}>
                    <div className="row align-items-center">
                      <div className="col-2">
                        <h5 className="mb-0 fs-14 fw-500 text-black-24">
                          {index + 1} {list.name}
                        </h5>
                      </div>
                      <div className="col-3">
                        <NormalInput
                          type="number"
                          onChange={(e) => this.handleChange(e, index)}
                          value={list.percentage}
                          placeholder="Enter %"
                          name="percentage"
                        />
                        {this.validator.message(
                          "Percentage",
                          list.percentage,
                          "required|max:6"
                        )}
                      </div>
                    </div>
                  </div>
                ))}
              </div>
              <div className="px-4 py-3 my-2">
                <div className="row">
                  <div className="col-12 d-flex pr-1">
                    <div className="d-flex btn-container-one">
                      <span className="mx-3">
                        <NormalButton
                          label="Reset"
                          greyBg
                          disabled={isButtonClicked}
                          onClick={() => this.handleClose()}
                        />
                      </span>
                      <span className="mx-3">
                        <NormalButton
                          label={"Apply"}
                          disabled={isButtonClicked}
                          mainbg
                          onClick={() => this.handleSubmit()}
                        />
                      </span>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators(
    {
      getReferalList,
      addReferals,
    },
    dispatch
  );
};

export const AddEditReferalManagement = connect(
  null,
  mapDispatchToProps
)(AddEditReferalManagementClass);
