import React, { Component } from "react";

import { connect } from "react-redux";

import { bindActionCreators } from "redux";

import { CommonPageLoader, NormalButton, NormalEditor } from "component/common";

import SimpleReactValidator from "simple-react-validator";

import {
  pageManagementDetailsAdd,
  getPageManagementDetails,
} from "action/AuthAct";

export class AddFormsClass extends Component {
  state = {
    descriptions: "",
    isLoading: false,
  };

  //validation
  validator = new SimpleReactValidator({
    element: (message) => (
      <span className="error-message text-danger fs-16 pb-3">{message}</span>
    ),
    autoForceUpdate: this,
  });

  //lifecycles
  componentDidMount() {
    this._getPageManagementDetails();
  }

  //on Editor Change
  onEditorChange = ({ data }) => {
    let { descriptions } = this.state;
    descriptions = data;
    this.setState({
      descriptions,
    });
  };

  //lifecycles
  componentDidUpdate(prevProps) {
    if (prevProps.selectedHeader !== this.props.selectedHeader) {
      this._getPageManagementDetails();
    }
  }

  //get Page Management Details
  _getPageManagementDetails = () => {
    let { getPageManagementDetails, selectedHeader } = this.props;
    let { descriptions } = this.state;
    this.validator.hideMessages();
    this.setState({
      isLoading: true,
      descriptions: "",
    });
    getPageManagementDetails(selectedHeader)
      .then((data) => {
        if (data.pageContent) {
          descriptions = data.pageContent;
        }
        this.setState({
          isLoading: false,
          descriptions,
        });
      })
      .catch(() => {
        this.setState({
          isLoading: false,
        });
      });
  };

  //handle Submit
  handleSubmit = () => {
    if (this.validator.allValid()) {
      let { descriptions } = this.state;
      let { selectedHeader, pageManagementDetailsAdd } = this.props;
      let body = {
        pageContent: descriptions,
      };
      this.setState({
        isButtonClicked: true,
      });
      pageManagementDetailsAdd(selectedHeader, body)
        .then(() => {
          this.setState({
            isButtonClicked: false,
          });
          this._getPageManagementDetails();
        })
        .catch(() => {
          this.setState({
            isButtonClicked: false,
          });
        });
    } else {
      this.validator.showMessages();
    }
  };

  render() {
    let { descriptions, isLoading } = this.state;

    if (isLoading) return <CommonPageLoader />;
    return (
      <>
        <div className="mt-1">
          <div className="row">
            <div className="col-12">
              <NormalEditor
                data={descriptions}
                onChange={(e) => this.onEditorChange(e)}
                required={true}
              />
              {this.validator.message(
                `descriptions `,
                descriptions,
                "required"
              )}
            </div>
          </div>
        </div>
        <div className="d-flex align-items-center mt-4">
          <div className="ml-auto d-flex">
            <NormalButton
              label={"Save"}
              mainbg={true}
              className="px-4"
              onClick={this.handleSubmit.bind(this, true)}
            />
          </div>
        </div>
      </>
    );
  }
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators(
    {
      pageManagementDetailsAdd,
      getPageManagementDetails,
    },
    dispatch
  );
};

let component = AddFormsClass;

export const AddForms = connect(null, mapDispatchToProps)(component);
