import React from "react";
import "./ManagementPageTitle.scss";
const ManagementPageTitle = ({
  title,
  titleStyle = "",
  titleDesc,
  titleDescStyle = "",
}) => {
  return (
    <div className={`title ${titleStyle}`}>
      {title}
      <span>
        <small className={`titleDesc ${titleDescStyle}`}>{titleDesc}</small>
      </span>
    </div>
  );
};

export default ManagementPageTitle;
