import React from "react";
import "./style.scss";

function DisplayChip({ className, name, onDelete, idx }) {
  return (
    <span className="chip">
      <span className="chip-value">{name}</span>
      <button
        className="chip-delete-button"
        onClick={() => onDelete(idx)}
        name={idx}
      >
        <i
          className="fs-16 icon-close-round cursor-pointer"
          name={idx}
          onClick={() => onDelete(idx)}
        />
      </button>
    </span>
  );
}

export default DisplayChip;
