import { invoiceManagement } from "../service/apiVariables";

import { addQuery } from "service/helperFunctions";

//get invoice List
export const getInvoiceList = (body) => (
  dispatch,
  getState,
  { api, Toast }
) => {
  return new Promise((resolve, reject) => {
    addQuery(body, invoiceManagement.getInvoiceList);
    api({ ...invoiceManagement.getInvoiceList })
      .then(({ data }) => {
        resolve(data);
      })
      .catch(({ message }) => {
        reject(Toast({ type: "error", message }));
      });
  });
};

