import React, { Component } from "react";
// Redux Connection
import { connect } from "react-redux";
import { TableStripedWrapper, CommonPageLoader } from "component/common";
import { categoryListHeader } from "Data/CategoryManagement";
import { listCategory } from "action/CategoryAct";
import { getSellerList } from "action/ProductAct";
import { bindActionCreators } from "redux";
import { debounceFunction } from "service/debounce";
import "./style.scss";
import ManagementPageTitle from "component/common/ManagementPageTitle";
import { history } from "service/helpers";
export class CategoryListClass extends Component {
  state = {
    search: "",
    view: "list",
    headerDetails: categoryListHeader,
    list: [],
    pageMetaData: {},
    id: "",
    searchValue: "",
  };

  //handle Toggle
  handleToggle = (name, id) => {
    if (id) {
      this.setState({ id });
    } else {
      this.setState({ id: "" });
    }
    this.setState((prevState) => ({
      [name]: !prevState[name],
    }));
  };

  //go to Update Route
  gotoUpdateRoute = (_id, item) => {
    history.push({
      pathname: `/admin/categoryManagement/editCategory/${_id}`,
      state: item,
    });
  };

  //handle Search
  handleSearch = (value, index) => {
    let { headerDetails } = this.state;

    headerDetails[index].searchValue = value;

    this.setState({ headerDetails }, () => {
      debounceFunction(() => {
        this._getCategoriesList(1);
      }, 1000);
    });
  };

  //life cycles
  componentDidMount() {
    if (this.props.compType === "list") {
      this.setState({
        view: this.props.compType,
      });
      this._getCategoriesList(1);
    }
    if (this.props.compType === "view") {
      this.setState({
        view: this.props.compType,
      });
      this._getCategoriesList(1);
    }
  }

  //list Category
  _getCategoriesList = (page) => {
    let { searchValue } = this.state;
    let payload = {
      page,
      search: searchValue,
    };
    this.setState({ isLoading: true });
    this.props
      .listCategory(payload)
      .then((data) => {
        this.setState({
          list: data.list,
          pageMetaData: data.pageMetaData,
          isLoading: false,
        });
      })
      .catch(() => {
        this.setState({
          isLoading: false,
        });
      });
  };

  //handle Delete
  handleDelete = (id) => {
    let body = {
      _id: id,
    };
    this.props.deleteBuyer(body).then(() => {
      this._getCategoriesList(1);
    });
  };

  //handle Pagination
  handlePagination = (page) => {
    this._getCategoriesList(page);
  };

  render() {
    let { headerDetails, list, isLoading, pageMetaData, view } = this.state;

    if (isLoading) return <CommonPageLoader />;
    return (
      <div className="common-table">
        <div className="d-flex justify-content-between">
          <div className="d-flex align-items-end tab-head">
            <ManagementPageTitle
              title="CATEGORIES MANAGEMENT"
              titleStyle="mb-0"
              titleDesc="Add and Edit Categories"
              titleDescStyle="mb-0"
            />
          </div>
        </div>
        <div className="bg-white product-inner-table">
          <div className="row px-3 py-2 align-item-center">
            <div className="col-6 text-left my-auto fs-20 text-black">
              <p className="table-head mb-0">
                {view === "list" ? "Category Table" : "View Category"}
              </p>
            </div>
          </div>
          <div className="custom-divider" />
          <div className="table-wrapper">
            <TableStripedWrapper
              headerDetails={headerDetails}
              handlePageChange={this._getCategoriesList}
              placeHolder={""}
              pageMeta={pageMetaData}
              search={this.state.search}
              handleSearch={this.handleSearch}
              queryHandler={this.handlePagination}
            >
              {list.length > 0 ? (
                <>
                  {list.map((item, index) => {
                    let { name, _id } = item;
                    return (
                      <tr key={index} className="text-center">
                        <td className="text-black-24 fs-14">
                          {pageMetaData.page === 1
                            ? index + 1
                            : (pageMetaData.page - 1) * 10 + index + 1}
                        </td>
                        <td className="text-black-24 fs-14">
                          {name ? name : "-"}
                        </td>
                        <td>
                          <div className="d-flex justify-content-center">
                            <i
                              className="fs-18 icon-edit pr-2 cursor-pointer fw-500"
                              onClick={() => this.gotoUpdateRoute(_id, item)}
                            />
                          </div>
                        </td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                      </tr>
                    );
                  })}
                </>
              ) : (
                <tr className="h3 text-center">
                  <td colSpan="8" className="py-4 text-center">
                    {"No Record Found"}
                  </td>
                  <td></td>
                  <td></td>
                  <td></td>
                  <td></td>
                  <td></td>
                  <td></td>
                  <td></td>
                  <td></td>
                  <td></td>
                </tr>
              )}
            </TableStripedWrapper>
          </div>
        </div>
      </div>
    );
  }
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators(
    {
      listCategory,
      getSellerList,
    },
    dispatch
  );
};

export const CategoryList = connect(
  null,
  mapDispatchToProps
)(CategoryListClass);
