import React, { Component } from "react";
import "./style.scss";
export class NormalInputTextBox extends Component {
  render() {
    let {
      className = "form-control",
      placeholder = "",
      onChange,
      value = "",
      name,
      disabled = false,
      maxlength,
      showCount,
      resize = "",
      rows = 4,
    } = this.props;
    return (
      <div className="normal-text-area">
        <textarea
          // maxLength={maxlength}
          rows={rows}
          className={`${className} ${!resize ? "resize-none" : ""}`}
          name={name}
          disabled={disabled}
          value={value}
          placeholder={placeholder}
          onChange={(e) => {
            let body = {};

            body = {
              target: {
                name: e.target.name,
                value: e.target.value,
              },
            };
            onChange(body);
          }}
        ></textarea>
        {showCount ? (
          <div className="d-flex justify-content-end">
            <div className="text-count w-100 pb-1">
              <p className="count mb-0 text-right">{maxlength} max</p>
            </div>
          </div>
        ) : null}
      </div>
    );
  }
}
