import React, { Component } from "react";
import "./style.scss";
import SimpleReactValidator from "simple-react-validator";
import {
  addsubcategory,
  updatesubcategory,
} from "../../../action/SubcategoryAct";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import { history } from "service/helpers";
import {
  CommonPageLoader,
  NormalButton,
  NormalMultiSelect,
} from "component/common";
import ManagementPageTitle from "component/common/ManagementPageTitle";
import { NormalInput } from "component/common/NormalInput";
import DisplayChip from "component/common/DisplayChip/DisplayChip";
import { listAllCategories } from "action/SubcategoryAct";
export class AddEditSubcategoryClass extends Component {
  constructor(props) {
    super(props);
    this.state = {
      categoryId: "",
      view: "add",
      name: "",
      categoryList: [],
      subCategoryList: [],
      isButtonClicked: false,
      searchInputData: "",
      isLoading: false,
    };

    this.handleChange = this.handleChange.bind(this);

    this.validator = new SimpleReactValidator({
      element: (message) => (
        <span className="error-message text-danger fs-14">{message}</span>
      ),
      autoForceUpdate: this,
    });
    this.validator2 = new SimpleReactValidator({
      element: (message) => (
        <span className="error-message text-danger fs-14">{message}</span>
      ),
      autoForceUpdate: this,
    });
  }

  //lifecycles
  componentDidMount = () => {
    this._getCategoryList("");
    if (this.props.compType === "edit" && history.location.state) {
      this.setState({
        ...history.location.state,
        view: this.props.compType,
      });
    }
  };

  //handle Change
  handleChange = ({ target: { name, value } }) => {
    this.setState({
      [name]: value,
    });
  };

  //add To List
  addToList = () => {
    let { name, subCategoryList } = this.state;
    if (name === "") return;

    if (this.validator2.allValid()) {
      let listItem = name;
      this.setState({
        subCategoryList: subCategoryList.concat([{ name: listItem }]),
        name: "",
      });
      this.validator2.hideMessages()
    } else {
      this.validator2.showMessages();
    }
  };

  //remove From List
  removeFromList = (idx) => {
    let { subCategoryList } = this.state;
    idx = parseInt(idx);
    this.setState({
      subCategoryList: [
        ...subCategoryList.slice(0, idx),
        ...subCategoryList.slice(idx + 1),
      ],
    });
  };

  //get Category List
  _getCategoryList = (search) => {
    let payload = {
      search,
    };
    this.setState({ isLoading: true });
    this.props
      .listAllCategories(payload)
      .then((data) => {
        let options = [];
        data.map((list) => {
          options.push({
            value: list._id,
            label: list.name,
          });
        });
        this.setState({
          categoryList: options,
          isLoading: false,
        });
      })
      .catch(() => {
        this.setState({
          isLoading: false,
        });
      });
  };

  //handle Update
  handleUpdate = (_id) => {
    let { categoryId, name } = this.state;
    if (this.validator2.allValid()) {
      this.setState({ isButtonClicked: true });
      if (this.state.view === "edit") {
        let body = {
          categoryId,
          name,
        };
        this.props
          .updatesubcategory(body, _id)
          .then(() => {
            this.setState(
              { isButtonClicked: false },
              history.push("/admin/subcategoryManagement")
            );
          })
          .catch(() => {
            this.setState({ isButtonClicked: false });
          });
      }
    } else {
      this.validator2.showMessages();
    }
  };

  //handle Submit
  handleSubmit = () => {
    if (this.validator.allValid()) {
      let { categoryId, subCategoryList } = this.state;
      this.setState({ isButtonClicked: true });
      let body = {
        categoryId,
        subCategoryList,
      };
      if (this.state.view === "add") {
        this.props
          .addsubcategory(body)
          .then(() => {
            this.setState({ isButtonClicked: false }, () =>
              history.push("/admin/subcategoryManagement")
            );
          })
          .catch(() => {
            this.setState({ isButtonClicked: false });
          });
      }
    } else {
      this.validator.showMessages();
    }
  };

  //handle input change
  handleinputChange = ({ target: { value } }) => {
    this.setState({
      searchInputData: value,
    });
    // debounceFunction(() => this.SearchFunction(value), 500);
  };

  SearchFunction = (value) => {
    this._getCategoryList(value);
  };

  render() {
    let {
      categoryId,
      view,
      name,
      subCategoryList,
      categoryList,
      _id,
      isButtonClicked,
      searchInputData,
      isLoading,
    } = this.state;

    if (isLoading) return <CommonPageLoader />;

    return (
      <div className="outer">
        <div className="w-100 subcategory-form">
          <ManagementPageTitle
            title="SUB-CATEGORIES MANAGEMENT"
            titleStyle="mb-0"
            titleDesc="Add and Edit Sub-Categories"
            titleDescStyle="mb-0"
          />
          <div className="subcategory-form-box">
            <p className="my-0 px-3 pt-3 pb-3 fs-20 text-black-22">
              {view === "add" ? "Add New Sub-Category" : "Edit Sub-Category"}
            </p>
            <div className="custom-divider" />
            <div className="p-0">
              <div className="px-4 py-2 my-2">
                <NormalMultiSelect
                  isBoldLabel={true}
                  label="Select Category"
                  type="text"
                  options={categoryList.length > 0 ? categoryList : []}
                  handleChange={this.handleChange}
                  value={categoryId}
                  keyName="name"
                  valueName="_id"
                  disabled={view === "edit"}
                  placeholder="Select Category"
                  name="categoryId"
                  arrow={true}
                  isSearchable={true}
                  handleinputChange={this.handleinputChange}
                  searchInputData={searchInputData}
                />
                {this.validator.message("categoryId", categoryId, "required", {
                  className: "text-danger",
                })}
                {view === "add" ? (
                  <>
                    <div className="d-flex">
                      <div className="col-11 ">
                        <NormalInput
                          label="Sub-Category Name"
                          placeholder="Enter Sub-Category Name"
                          name="name"
                          value={name}
                          onChange={this.handleChange}
                        />
                      </div>
                      <div className="col-1 pt-5">
                        <button
                          className="AddItemButton"
                          disabled={name == ""}
                          onClick={this.addToList}
                        >
                          +
                        </button>
                      </div>
                    </div>
                    {this.validator2.message("name", name, "required|max:50|alpha_space", {
                      className: "text-danger",
                    })}
                  </>
                ) : (
                  <div>
                    <NormalInput
                      label="Sub-Category Name"
                      placeholder="Enter Sub-Category Name"
                      name="name"
                      value={name}
                      onChange={this.handleChange}
                    />
                    <div className="d-block">
                      {this.validator2.message("name", name, "required|max:50|alpha_space", {
                        className: "text-danger",
                      })}
                    </div>
                  </div>
                )}
                {view === "add" && (
                  <div>
                    {subCategoryList.length > 0
                      ? subCategoryList.map((item, idx) => (
                        <span key={idx}>
                          <DisplayChip
                            name={item.name}
                            onDelete={this.removeFromList}
                            idx={idx}
                          />
                        </span>
                      ))
                      : null}
                    {this.validator.message(
                      "subCategory",
                      subCategoryList,
                      "required",
                      {
                        className: "text-danger",
                      }
                    )}
                  </div>
                )}
              </div>
              <div className="px-4 py-3">
                <div className="row">
                  <div className="col-12 d-flex pr-1">
                    <div className="d-flex btn-container-one">
                      <span className="mx-2">
                        <NormalButton
                          label="Cancel"
                          blackBg
                          disabled={isButtonClicked}
                          onClick={() =>
                            history.push("/admin/subcategoryManagement")
                          }
                        />
                      </span>
                      <span className="mx-2">
                        <NormalButton
                          label={view === "add" ? "Create" : "Apply"}
                          mainbg
                          disabled={isButtonClicked}
                          onClick={() =>
                            view === "add"
                              ? this.handleSubmit()
                              : this.handleUpdate(_id)
                          }
                        />
                      </span>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators(
    {
      addsubcategory,
      updatesubcategory,
      listAllCategories,
    },
    dispatch
  );
};

let component = AddEditSubcategoryClass;

export const AddEditSubcategoryComp = connect(
  null,
  mapDispatchToProps
)(component);
