import React, { Component } from "react";
// Redux Connection
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import { getSellerById } from "action/SellerAct";
import { CommonPageLoader } from "component/common";
import { Modal, ModalBody } from "reactstrap";
import "./style.scss";
import { phoneFormat } from "service/helperFunctions";

export class ViewSellerPopupClass extends Component {
  state = {
    formFields: {
      firstName: "",
      lastName: "",
      email: "",
      businessName: "",
      businessLicense: "",
      businessPhone: "",
    },
    isLoading: false,
  };

  //get Seller By Id
  _getDetailsById = (id) => {
    let body = {
      id,
    };
    this.setState({ isLoading: true });
    this.props.getSellerById(body).then((data) => {
      if (data !== null) {
        this.setState({
          formFields: {
            firstName: data.firstName,
            lastName: data.lastName,
            email: data.email,
            businessName: data.businessName,
            businessLicense: data.businessLicense,
            businessPhone: data.businessPhone,
          },
          isLoading: false,
        });
      }
    });
  };

  //life cycles
  componentDidUpdate(prevProps) {
    if (prevProps !== this.props) {
      if (this.props.PersonId !== "" && this.props.isOpen) {
        this._getDetailsById(this.props.PersonId);
      }
    }
  }

  //check Data
  checkData = (data) => {
    if (data) {
      if (data.length > 0) {
        return data;
      }
    }
    return "----------";
  };

  render() {
    let { formFields, isLoading } = this.state;
    let {
      firstName,
      lastName,
      email,
      businessName,
      businessLicense,
      businessPhone,
    } = formFields;

    let {
      className = "modal-dialog-centered view-product-popup",
      isOpen = true,
      toggle,
    } = this.props;

    return (
      <Modal isOpen={isOpen} toggle={toggle} className={className} size="lg">
        <ModalBody>
          <div className="d-flex justify-content-between w-100 align-items-center mb-5">
            <h1 className="fs-30 text-black-22 fw-400 mb-0 text-uppercase">
              View Seller
            </h1>
            <i
              className="fs-22 icon-close-round cursor-pointer"
              onClick={() => toggle()}
            />
          </div>
          {!isLoading ? (
            <>
              <div className="row">
                <div className="col-6">
                  <div classname="header">First Name *</div>
                  <p className="info">{this.checkData(firstName)}</p>
                </div>
              </div>
              <div className="row">
                <div className="col-6">
                  <div classname="header">Last Name *</div>
                  <p className="info">
                    {this.checkData(lastName) ? lastName : "-"}
                  </p>
                </div>
              </div>
              <div className="row">
                <div className="col-6">
                  <div classname="header">Email ID *</div>
                  <p className="info">{this.checkData(email)}</p>
                </div>
              </div>
              <div className="row">
                <div className="col-6">
                  <div classname="header">Business Name *</div>
                  <p className="info">{this.checkData(businessName)}</p>
                </div>
              </div>
              <div className="row">
                <div className="col-6">
                  <div classname="header">
                    Corporation license number(9 digits) *
                  </div>
                  <p className="info">{this.checkData(businessLicense)}</p>
                </div>
                <div className="col-6">
                  <div classname="header">Business Phone *</div>
                  <p className="info">
                    {this.checkData(phoneFormat(businessPhone))}
                  </p>
                </div>
              </div>
              {/* <div className="row">
                <div className="col-6">
                  <div classname="header">Referral Code(Optional) *</div>
                  <p className="info">{this.checkData(referralCode)}</p>
                </div>
              </div> */}
            </>
          ) : (
            <CommonPageLoader />
          )}
        </ModalBody>
      </Modal>
    );
  }
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators(
    {
      getSellerById,
    },
    dispatch
  );
};

export const ViewSellerPopup = connect(
  null,
  mapDispatchToProps
)(ViewSellerPopupClass);
