import React, { Component } from "react";
import SimpleReactValidator from "simple-react-validator";
import { login } from "../../../../action/AuthAct";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import { history } from "service/helpers";
import "./style.scss";

export class LoginClass extends Component {
  constructor(props) {
    super(props);
    this.state = {
      email: "",
      password: "",
      passwordVisible: false,
      reEnterPasswordVisible: false,
      isButtonClicked: false,
    };
    this.handleChange = this.handleChange.bind(this);
  }

  //validation
  validator = new SimpleReactValidator({
    messages: {
      email: "Invalid Email address",
    },
    element: (message) => (
      <span className="error-message text-danger fs-14">{message}</span>
    ),
    autoForceUpdate: this,
    validators: {
      email: {
        message: "must be a valid Email.",
        rule: (val, params, validator) => {
          return validator.helpers.testRegex(
            val,
            /^[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,3}$/
          );
        },
        messageReplace: (message, params) =>
          message.replace("", this.helpers.toSentence(params)),
        required: true,
      },
    },
  });

  //handle Change
  handleChange = ({ target: { name, value } }) => {
    this.setState({
      [name]: value,
    });
  };

  //toggle
  toggle = (key) => {
    this.setState((prevState) => ({
      [key]: !prevState[key],
    }));
  };

  //handle Submit
  handleSubmit = () => {
    if (this.validator.allValid()) {
      this.setState({ isButtonClicked: true }, () => {
        let { email, password } = this.state;
        let body = {
          email,
          password,
        };
        this.props
          .login(body)
          .then(() => {
            this.setState({ isButtonClicked: false });
            history.push("/admin/buyerManagement");
          })
          .catch(() => {
            this.setState({ isButtonClicked: false });
          });
      });
    } else {
      this.validator.showMessages();
    }
  };

  render() {
    let { email, password, passwordVisible, isButtonClicked } = this.state;
    return (
      <div className="w-100 login-area">
        <div>
          <h2
            className="fw-800 text-center text-black-22 fs-30"
            htmlFor="mobileOrEmail"
          >
            Log In
          </h2>
          <div>
            <label className="fw-600 mb-4">{"Email"}</label>
            <div className="input-group">
              <input
                type="text"
                className="form-control email rounded-0"
                name="email"
                value={email}
                maxLength={100}
                onChange={this.handleChange}
                placeholder="Enter email id"
                aria-label="Username"
                aria-describedby="basic-addon1"
                onKeyUp={(e) => {
                  if (e.keyCode == 13) {
                    this.handleSubmit();
                  }
                }}
              />
              <div className="icon-login">
                <i className="icon-email fs-16"></i>
              </div>
            </div>
            {this.validator.message("email", email, "required|email")}
          </div>
          <div className="pb-3">
            <label className="fw-600 my-4 pt-1">{"Password"}</label>
            <div className="input-group">
              <input
                type={passwordVisible ? "text" : "password"}
                className="form-control rounded-0"
                name="password"
                value={password}
                onChange={this.handleChange}
                placeholder="Enter your password"
                onKeyUp={(e) => {
                  if (e.keyCode == 13) {
                    this.handleSubmit();
                  }
                }}
                maxLength={15}
              />
              <div className="icon-login">
                <i className="icon-password fs-20"></i>
              </div>
              <div className="input-group-addon right">
                <span
                  onClick={(e) => this.toggle("passwordVisible")}
                  className={`cursor-pointer icon-${
                    passwordVisible ? "eye" : "eye-hide"
                  } fs-18`}
                ></span>
              </div>
            </div>
            {this.validator.message("password", password, "required|password")}
          </div>
          <div className="d-flex justify-content-center mt-5 pt-4">
            <input
              className="btn w-50 py-2 login-btn rounded-0 text-white bg-black "
              type="button"
              value="Login"
              onClick={this.handleSubmit}
              disabled={isButtonClicked}
            />
          </div>
        </div>
      </div>
    );
  }
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators(
    {
      login,
    },
    dispatch
  );
};

let component = LoginClass;

export const LoginComp = connect(null, mapDispatchToProps)(component);
