import { sellerManagement, referralManagement } from "../service/apiVariables";

import { addQuery } from "service/helperFunctions";

//add Seller
export const addSeller = (body) => (dispatch, getState, { api, Toast }) => {
  return new Promise((resolve, reject) => {
    api({ ...sellerManagement.addSeller, body })
      .then(({ data, message }) => {
        resolve(data);
        Toast({ type: "success", message, time: 5000 });
      })
      .catch(({ message }) => {
        reject(Toast({ type: "error", message }));
      });
  });
};

//get Seller List
export const getSellerList = (query) => (
  dispatch,
  getState,
  { api, Toast }
) => {
  return new Promise((resolve, reject) => {
    addQuery(query, sellerManagement.getSellerList);

    api({ ...sellerManagement.getSellerList })
      .then((data) => {
        resolve(data);
      })
      .catch(({ message }) => {
        reject(Toast({ type: "error", message }));
      });
  });
};

