import React, { Component } from "react";
// Redux Connection
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import { getBuyerById } from "action/BuyerAct";
import { CommonPageLoader } from "component/common";
import { Modal, ModalBody } from "reactstrap";
import { phoneFormat } from "service/helperFunctions";
import "./style.scss";

export class ViewBuyerPopupClass extends Component {
  state = {
    formFields: {
      firstName: "",
      accountType: "",
      lastName: "",
      email: "",
      businessName: "",
      businessLicense: "",
      businessPhone: "",
      referralCode: "",
      adminRef: {
        refCount: "",
      },
    },
    isLoading: false,
  };

  //get Details By Id
  _getDetailsById = (id) => {
    let body = {
      id,
    };
    this.setState({ isLoading: true });
    this.props.getBuyerById(body).then((data) => {
      if (data !== null) {
        this.setState({
          formFields: {
            firstName: data.firstName,
            accountType: data.accountType,
            lastName: data.lastName,
            email: data.email,
            businessName: data.businessName,
            businessLicense: data.businessLicense,
            businessPhone: data.businessPhone,
            referralCode: data.referralCode,
            adminRef: data.adminRef,
          },
          isLoading: false,
        });
      }
    });
  };

  //lifecycles
  componentDidUpdate(prevProps) {
    if (prevProps !== this.props) {
      if (this.props.PersonId !== "" && this.props.isOpen) {
        this._getDetailsById(this.props.PersonId);
      }
    }
  }

  //check Data
  checkData = (data) => {
    if (data) {
      if (data.length > 0) {
        return data;
      }
    }
    return "----------";
  };

  render() {
    let { formFields, isLoading } = this.state;
    let {
      firstName,
      accountType,
      lastName,
      email,
      businessName,
      businessLicense,
      businessPhone,
      referralCode,
      adminRef = {},
    } = formFields;

    let { refCount } = adminRef || {};

    let {
      className = "modal-dialog-centered view-product-popup",
      isOpen = true,
      toggle,
    } = this.props;

    return (
      <Modal isOpen={isOpen} toggle={toggle} className={className} size="lg">
        <ModalBody>
          <div className="d-flex justify-content-between w-100 align-items-center mb-5">
            <h1 className="fs-30 text-black-22 fw-400 mb-0 text-uppercase">
              View Customer
            </h1>
            <i
              className="fs-22 icon-close-round cursor-pointer"
              onClick={() => toggle()}
            ></i>
          </div>
          {!isLoading ? (
            <>
              <div className="row">
                <div className="col-6">
                  <div classname="header">First Name *</div>
                  <p className="info">{this.checkData(firstName)}</p>
                </div>
                <div className="col-6">
                  <div classname="header">Account Type *</div>
                  <p className="info">
                    {accountType == 1 ? "Master Account" : "Sub Account"}
                  </p>
                </div>
              </div>
              <div className="row">
                <div className="col-6">
                  <div classname="header">Last Name *</div>
                  <p className="info">{this.checkData(lastName)}</p>
                </div>
              </div>
              <div className="row">
                <div className="col-6">
                  <div classname="header">Email ID *</div>
                  <p className="info">{this.checkData(email)}</p>
                </div>
              </div>
              <div className="row">
                <div className="col-6">
                  <div classname="header">Business Name *</div>
                  <p className="info">{this.checkData(businessName)}</p>
                </div>
              </div>
              <div className="row">
                <div className="col-6">
                  <div classname="header">
                    Corporation license number(9 digits) *
                  </div>
                  <p className="info">{this.checkData(businessLicense)}</p>
                </div>
                <div className="col-6">
                  <div classname="header">Business Phone *</div>
                  <p className="info">
                    {this.checkData(phoneFormat(businessPhone))}
                  </p>
                </div>
              </div>
              <div className="row">
                <div className="col-6">
                  <div classname="header">Referral Code(Optional) *</div>
                  <p className="info">{this.checkData(referralCode)}</p>
                </div>
                <div className="col-6">
                  <div classname="header">Referral Count *</div>
                  <p className="info">{refCount ? refCount : "----"}</p>
                </div>
              </div>
            </>
          ) : (
            <CommonPageLoader />
          )}
        </ModalBody>
      </Modal>
    );
  }
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators(
    {
      getBuyerById,
    },
    dispatch
  );
};

export const ViewBuyerPopup = connect(
  null,
  mapDispatchToProps
)(ViewBuyerPopupClass);
