import React, { Component } from "react";
import SimpleReactValidator from "simple-react-validator";
// Redux Connection

import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import { cancelOrder } from "action/OrderAct";
import { NormalButton, NormalInputTextBox } from "component/common";
import { Modal, ModalBody } from "reactstrap";
import "./style.scss";
import { history } from "service/helpers";

export class OrderCancellationClass extends Component {
  state = {
    reason: "",
    isButtonClicked: false,
  };

  //validation
  validator = new SimpleReactValidator({
    element: (message) => (
      <span className="error-message text-danger fs-14">{message}</span>
    ),
    autoForceUpdate: this,
  });

  //handle change
  handleChange = ({ target: { name, value } }) => {
    this.setState({
      [name]: value,
    });
  };

  //handle submit
  handleSubmit = () => {
    if (this.validator.allValid()) {
      this.setState({ isButtonClicked: true }, () => {
        let { reason } = this.state;
        let { cancelOrder, singleProductId } = this.props;
        let body = {
          reason,
          _id: singleProductId,
        };
        cancelOrder(body)
          .then(() => {
            this.handleCancel();
          })
          .catch(() => {
            this.setState({
              isButtonClicked: false,
            });
          });
      });
    } else {
      this.validator.showMessages();
    }
  };

  //handle cancel
  handleCancel = () => {
    this.setState(
      {
        reason: "",
      },
      () => {
        this.props.toggle();
        history.push("/admin/order-management");
      }
    );
  };

  render() {
    let { reason } = this.state;

    let {
      className = "modal-dialog-centered order-cancellation-popup",
      isOpen = true,
      toggle,
    } = this.props;

    return (
      <Modal isOpen={isOpen} toggle={toggle} className={className} size="lg">
        <ModalBody>
          <div className="d-flex justify-content-between w-100 align-items-center mb-5">
            <h1 className="fs-30 text-black-22 fw-400 mb-0 text-uppercase">
              Reason For Cancellation
            </h1>
            <i
              className="fs-22 icon-close-round cursor-pointer"
              onClick={() => toggle()}
            />
          </div>
          <div className="">
            <NormalInputTextBox
              className="form-control mt-1"
              value={reason}
              showCount={true}
              name="reason"
              onChange={this.handleChange}
              maxlength={1000}
            />
            {this.validator.message("reason", reason, "required|max:1000")}
          </div>
          <div className="d-flex float-right mt-4">
            <NormalButton
              label="Cancel Order"
              blackBg
              onClick={() => this.handleSubmit()}
            />
          </div>
        </ModalBody>
      </Modal>
    );
  }
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({ cancelOrder }, dispatch);
};

export const OrderCancellation = connect(
  null,
  mapDispatchToProps
)(OrderCancellationClass);
