import { ProductManagement } from "../service/apiVariables";

import { addQuery } from "service/helperFunctions";

// get Category List
export const getCategoryList =
  () =>
  (dispatch, getState, { api, Toast }) => {
    return new Promise((resolve, reject) => {
      api({ ...ProductManagement.getCategoryList })
        .then(({ data }) => {
          resolve(data);
        })
        .catch(({ message }) => {
          reject(Toast({ type: "error", message }));
        });
    });
  };

//get Seller List
export const getSellerList =
  (body) =>
  (dispatch, getState, { api, Toast }) => {
    return new Promise((resolve, reject) => {
      addQuery(body, ProductManagement.getSellerList);
      api({ ...ProductManagement.getSellerList })
        .then(({ data }) => {
          resolve(data);
        })
        .catch(({ message }) => {
          reject(Toast({ type: "error", message }));
        });
    });
  };

