//update Products
export const updateProducts =
  (payload, body) =>
  (dispatch, getState, { api, Toast }) => {
    let { updateProducts } = ProductManagement;

    updateProducts.id = payload.id;

    return new Promise((resolve, reject) => {
      api({ ...ProductManagement.updateProducts, body })
        .then(({ data, message }) => {
          resolve(data);

          Toast({ type: "success", message });
        })
        .catch(({ message }) => {
          reject(Toast({ type: "error", message }));
        });
    });
  };

