import React, { Component } from "react";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import ManagementPageTitle from "component/common/ManagementPageTitle";
import { NormalButton, NormalDate } from "component/common";
import Select, { components } from "react-select";
import { debounceFunction } from "service/debounce";
import SimpleReactValidator from "simple-react-validator";
import { getInvoiceReport } from "action/ReportsAct";
import { getCustomerList } from "action/ProductAct";
import moment from "moment";
import "./style.scss";

export class InvoiceReportCompClass extends Component {
  state = {
    customer: "",
    fromDate: "",
    toDate: "",
    customerOptions: [],
    searchInputData: "",
    isButtonClicked: false,
    newValue: {},
  };

  //validator
  validator = new SimpleReactValidator({
    element: (message) => (
      <span className="error-message text-danger fs-14">{message}</span>
    ),
    autoForceUpdate: this,
  });

  //life cycles
  componentDidMount() {
    this._getCustomerOptions();
  }

  //handle date change
  handleInputChange = ({ target: { name, value } }) => {
    this.setState({
      [name]: value,
    });
  };

  //handle Submit
  handleSubmit = () => {
    if (this.validator.allValid()) {
      this.setState({ isButtonClicked: true });
      let { customer, fromDate, toDate } = this.state;
      let body = {
        buyer: customer,
        fromDate: moment(fromDate).format("DD-MM-YYYY"),
        toDate: moment(toDate).format("DD-MM-YYYY"),
      };
      this.props
        .getInvoiceReport(body)
        .then(() => {
          this.validator.hideMessages();
          this.setState({
            fromDate: "",
            toDate: "",
            customer: "",
            isButtonClicked: false,
          });
        })
        .catch(() => {
          this.setState({ isButtonClicked: false });
        });
    } else {
      this.validator.showMessages();
    }
  };

  //handle input Change
  handleinputChange = (value) => {
    this.setState({
      searchInputData: value,
    });
    debounceFunction(() => this.SearchFunction(value), 500);
  };

  //Search Function
  SearchFunction = (value) => {
    this._getCustomerOptions(value);
  };

  //get Customer Options
  _getCustomerOptions = (search = "") => {
    let body = {
      search,
    };
    let { newValue } = this.state;
    this.props.getCustomerList(body).then((data) => {
      let options = [];
      data.map((list) => {
        options.push({
          value: list._id,
          label: list.firstName + list.lastName,
        });
      });
      if (newValue.value) {
        options.push(newValue);
      }
      this.setState({
        customerOptions: options,
      });
    });
  };

  //handle Input
  handleInput = (newValue) => {
    let { searchInputData } = this.state;
    if (searchInputData !== "") {
      this.setState({
        newValue,
      });
    }
    this.setState({
      customer: newValue.value,
    });
  };

  render() {
    let {
      customer,
      fromDate,
      toDate,
      customerOptions,
      searchInputData,
      isButtonClicked,
    } = this.state;

    const DropdownIndicator = (props) => {
      return (
        components.DropdownIndicator && (
          <components.DropdownIndicator {...props}>
            <div className="d-flex flex-column">
              <span className={`icon-up fs-6`}></span>
              <span className={`icon-down fs-6`}></span>
            </div>
          </components.DropdownIndicator>
        )
      );
    };

    const customStyles = {
      placeholder: (base) => ({
        ...base,
        fontSize: 12,
        color: "#24243a",
        fontWeight: 600,
      }),
      indicatorSeparator: (base) => ({
        ...base,
        display: "none",
      }),
      dropdownIndicator: (base) => ({
        ...base,
        padding: 2,
        height: "18px",
        width: "18px",
        color: "black",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
      }),
      singleValue: (base) => ({
        ...base,
        color: "#24243a",
        fontWeight: 600,
        fontSize: 12,
      }),
      control: (base) => ({
        ...base,
        borderRadius: 18,
        border: "1px solid #e5e5e5",
        boxShadow: "0 !important",
        outline: "0 ",
        background: "#fcfcfc",
        height: 31,
        minHeight: 31,
      }),
      valueContainer: (base) => ({
        ...base,
        height: 31,
        padding: "0 6px 0px 10px",
      }),
      indicatorsContainer: (base) => ({
        ...base,
        paddingRight: 5,
      }),
      container: (base) => ({
        ...base,
        width: 150,
      }),
    };

    return (
      <div className="report">
        <div className="d-flex justify-content-between">
          <div className="d-flex align-items-end tab-head">
            <ManagementPageTitle
              title="INVOICE REPORT"
              titleStyle="mb-0"
              titleDescStyle="mb-0"
            />
          </div>
        </div>
        <div className="row pt-4 pb-4 px-2 ">
          <div className="col-4">
            <div className="d-flex align-items-center mt-5 pt-2">
              <p className="fs-16 text-black-24 mb-0 mr-2 fw-500">Customer</p>
              <div className="d-flex flex-wrap">
                <Select
                  classNamePrefix="Select"
                  name={"customer"}
                  options={customerOptions}
                  onChange={this.handleInput}
                  placeholder={"Customer Name"}
                  styles={customStyles}
                  inputValue={searchInputData}
                  isSearchable={true}
                  onInputChange={this.handleinputChange}
                  value={
                    !!customerOptions && customerOptions.length > 0
                      ? customerOptions.find((data) => data.value === customer)
                        ? customerOptions.find(
                            (data) => data.value === customer
                          )
                        : null
                      : null
                  }
                  components={{ DropdownIndicator }}
                />
                {this.validator.message("customer", customer, "required")}
              </div>
            </div>
          </div>
          <div className="col-8">
            <h6 className="px-2">Select Invoice Date range</h6>
            <div className="row d-flex px-2 mt-4">
              <div className="col-6 mt-1 d-flex">
                <span className="mr-3 mt-3">From</span>
                <div>
                  <NormalDate
                    name="fromDate"
                    value={fromDate}
                    onChange={this.handleInputChange}
                    maxdate={toDate == "" ? new Date() : new Date(toDate)}
                  />
                  {this.validator.message("from date", fromDate, "required")}
                </div>
              </div>
              <div className="col-6 mt-1 d-flex">
                <span className="mr-3 mt-3">To</span>
                <div>
                  <NormalDate
                    name="toDate"
                    value={toDate}
                    onChange={this.handleInputChange}
                    maxdate={new Date()}
                    mindate={new Date(fromDate)}
                  />
                  {this.validator.message("to date", toDate, "required")}
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className="row">
          <div className="col-12 d-flex justify-content-end">
            <div className="d-flex mt-5 mr-2">
              <NormalButton
                label="Request Report"
                mainbg
                className="ml-auto"
                onClick={() => this.handleSubmit()}
                disabled={isButtonClicked}
              />
            </div>
          </div>
        </div>
      </div>
    );
  }
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators(
    {
      getInvoiceReport,
      getCustomerList,
    },
    dispatch
  );
};

export const InvoiceReportComp = connect(
  null,
  mapDispatchToProps
)(InvoiceReportCompClass);
