// add loyalty points
export const addLoyaltyPoints = (body, _id) => (
  dispatch,
  getState,
  { api, Toast }
) => {
  let { addLoyaltyPoints } = loyaltyPoints;
  addLoyaltyPoints.id = _id;
  return new Promise((resolve, reject) => {
    api({ ...loyaltyPoints.addLoyaltyPoints, body })
      .then(({ data, message }) => {
        resolve(data);

        Toast({ type: "success", message });
      })
      .catch(({ message }) => {
        reject(Toast({ type: "error", message }));
      });
  });
};

