import { buyerManagement } from "../service/apiVariables";

import { addQuery } from "service/helperFunctions";

//add Buyer
export const addBuyer = (body) => (dispatch, getState, { api, Toast }) => {
  return new Promise((resolve, reject) => {
    api({ ...buyerManagement.addBuyer, body })
      .then(({ data, message }) => {
        resolve(data);
        Toast({ type: "success", message, time: 5000 });
      })
      .catch(({ message }) => {
        reject(Toast({ type: "error", message }));
      });
  });
};

//get Buyer List
export const getBuyerList = (query) => (dispatch, getState, { api, Toast }) => {
  return new Promise((resolve, reject) => {
    addQuery(query, buyerManagement.getBuyerList);

    api({ ...buyerManagement.getBuyerList })
      .then((data) => {
        resolve(data);
      })
      .catch(({ message }) => {
        reject(Toast({ type: "error", message }));
      });
  });
};

