import React, { Component } from "react";
import { history } from "service/helpers";
import { ResetPassword } from "../../component/auth/login/index";
export class ResetPass extends Component {
  render() {
    return (
      <>
        <form>
          <div className="shadow-lg rounded mt-1 p-3 d-flex align-items-center flex-column">
            <ResetPassword
              token={new URLSearchParams(this.props.location.search).get(
                "token"
              )}
            />
          </div>
        </form>
        <div>
          <p
            className="mb-0 mt-5 fs-16 font-weight-bold text-center cursor-pointer forget-password"
            onClick={() => history.push("/")}
          >
            Back to login
          </p>
        </div>
      </>
    );
  }
}
