import React, { Component } from "react";
import SimpleReactValidator from "simple-react-validator";
// Redux Connection

import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import { getProfileDetails, updateProfileDetails } from "action/AuthAct";
import ManagementPageTitle from "component/common/ManagementPageTitle";
import "./style.scss";
import {
  NormalInput,
  NormalButton,
  CommonPageLoader,
  PasswordStrengthMeter,
} from "component/common";
import { phoneFormat } from "service/helperFunctions";
export class ProfileClass extends Component {
  state = {
    firstName: "",
    lastName: "",
    email: "",
    businessPhone: "",
    password: "",
    isLoading: false,
    reenterPassword: "",
    userNameStatus: false,
    phoneNumberStatus: false,
    passwordStatus: false,
  };

  //validator
  validator = new SimpleReactValidator({
    element: (message) => (
      <span className="error-message text-danger fs-14">{message}</span>
    ),
    autoForceUpdate: this,
  });
  // validator Phone
  validatorPhone = new SimpleReactValidator({
    element: (message) => (
      <span className="error-message text-danger fs-14">{message}</span>
    ),
    autoForceUpdate: this,
  });

  //validation
  validatorPassword = new SimpleReactValidator({
    element: (message) => (
      <span className="error-message text-danger fs-14">{message}</span>
    ),
    autoForceUpdate: this,
    validators: {
      password: {
        message:
          "New Password shall be minimum of 8 characters minimum with at least: 1 small letter, 1 capital letter, 1 special character and 1 number",
        rule: (val, params, validator) => {
          return (
            validator.helpers.testRegex(
              val,
              /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[#$^+=!*()@%&]).{8,20}$/
            ) && params.indexOf(val) === -1
          );
        },
        messageReplace: (message, params) =>
          message.replace("", this.helpers.toSentence(params)),
        required: true,
      },
      checkPassword: {
        message: "Given :attribute does not match",
        rule: function (val, params) {
          return val === params[0];
        },
      },
    },
  });

  //handle Edit
  handleEdit = (name) => {
    this.setState((prevState) => ({
      [name]: !prevState[name],
    }));
  };

  //handle update
  handleupdate = (name) => {
    let { firstName, lastName, businessPhone, password } = this.state;
    let { updateProfileDetails } = this.props;
    if (name === "userNameStatus") {
      if (this.validator.allValid()) {
        let body = {
          firstName,
          lastName,
        };
        updateProfileDetails(body).then(() => {
          this._getProfileDetails();
          this.handleEdit("userNameStatus");
          this.validator.hideMessages();
        });
      } else {
        this.validator.showMessages();
      }
    } else if (name === "phoneNumberStatus") {
      if (this.validatorPhone.allValid()) {
        let body = {
          businessPhone,
        };
        updateProfileDetails(body).then(() => {
          this._getProfileDetails();
          this.handleEdit("phoneNumberStatus");
          this.validatorPhone.hideMessages();
        });
      } else {
        this.validatorPhone.showMessages();
      }
    } else if (name === "passwordStatus") {
      if (this.validatorPassword.allValid()) {
        let body = {
          password,
        };
        updateProfileDetails(body).then(() => {
          this._getProfileDetails();
          this.validatorPassword.hideMessages();
          this.handleEdit("passwordStatus");
        });
      } else {
        this.validatorPassword.showMessages();
      }
    }
  };
  //life cycles
  componentDidMount() {
    this._getProfileDetails();
  }

  //get Profile Details
  _getProfileDetails = () => {
    this.setState({ isLoading: true });
    this.props.getProfileDetails().then((data) => {
      this.setState({
        firstName: data.firstName,
        lastName: data.lastName,
        email: data.email,
        businessPhone: data.businessPhone,
        isLoading: false,
        password: "",
        reenterPassword: "",
      });
    });
  };

  //handle input
  handleInput = ({ target: { name, value } }) => {
    this.setState({
      [name]: value,
    });
  };

  //handle Undo
  handleUndo = () => {
    this.setState(
      {
        phoneNumberStatus: false,
        userNameStatus: false,
        passwordStatus: false,
      },
      () => {
        this._getProfileDetails();
      }
    );
  };

  render() {
    let {
      firstName,
      lastName,
      businessPhone,
      password,
      reenterPassword,
      email,
      isLoading,
      userNameStatus,
      phoneNumberStatus,
      passwordStatus,
    } = this.state;
    if (isLoading) return <CommonPageLoader />;
    return (
      <section className="pr-5 mr-3">
        <div className="profile-page">
          <div className="pb-4 mb-3">
            <div className="d-flex justify-content-between ">
              <div className="d-flex align-items-end tab-head">
                <ManagementPageTitle
                  title="PROFILE MANAGEMENT"
                  titleStyle="mb-1"
                  titleDesc=""
                  titleDescStyle="mb-0"
                />
              </div>
            </div>
            <div className="border-bottom-yellow"></div>
          </div>
          {passwordStatus || phoneNumberStatus || userNameStatus ? (
            <div className="row mb-4 pt-2">
              <div className="col-2 ml-auto mr-4">
                <NormalButton
                  label={"Undo"}
                  mainbg={true}
                  onClick={() => this.handleUndo()}
                  className="ml-4"
                />
              </div>
            </div>
          ) : null}
          <div className="w-100" id="hr-line"></div>
          <div className="profile-infomation-area ">
            <div className="row w-100">
              <div className="col-10">
                <p className="fs-22 fw-600 mb-1">Name :</p>
                {userNameStatus ? (
                  <div className="row">
                    <div className="col-6">
                      <NormalInput
                        placeholder="First name"
                        type="text"
                        name="firstName"
                        value={firstName}
                        onChange={this.handleInput}
                      />
                      {this.validator.message(
                        "firstName",
                        firstName,
                        "required|max:30|alpha_space"
                      )}
                    </div>
                    <div className="col-6">
                      <NormalInput
                        type="text"
                        placeholder="Last name"
                        name="lastName"
                        value={lastName}
                        onChange={this.handleInput}
                      />
                      {this.validator.message(
                        "lastName",
                        lastName,
                        "required|max:30|alpha_space"
                      )}
                    </div>
                  </div>
                ) : (
                  <p className="fs-18 fw-500 mb-0">
                    {firstName} {lastName}
                  </p>
                )}
              </div>
              <div className="col-2">
                <NormalButton
                  label={userNameStatus ? "Update" : "Edit"}
                  mainbg={true}
                  onClick={() =>
                    userNameStatus
                      ? this.handleupdate("userNameStatus")
                      : this.handleEdit("userNameStatus")
                  }
                  className="ml-4"
                />
              </div>
            </div>
          </div>
          <div className="w-100" id="hr-line"></div>
          <div className="profile-infomation-area">
            <div>
              <p className="fs-22 fw-600 mb-1">Email :</p>
              <p className="fs-18 fw-500 mb-0">{email}</p>
            </div>
          </div>
          <div className="w-100" id="hr-line"></div>
          <div className="profile-infomation-area">
            <div className="row w-100">
              <div className="col-10">
                <p className="fs-22 fw-600 mb-1">Mobile/Phone Number :</p>
                {phoneNumberStatus ? (
                  <div className="row">
                    <div className="col-6">
                      <NormalInput
                        name="businessPhone"
                        placeholder="Phone number"
                        value={phoneFormat(businessPhone)}
                        onChange={this.handleInput}
                        max={14}
                      />
                      {this.validatorPhone.message(
                        "businessPhone",
                        businessPhone,
                        "required|phone|min:10"
                      )}
                    </div>
                  </div>
                ) : (
                  <p className="fs-18 fw-500 mb-0">
                    {phoneFormat(businessPhone)}
                  </p>
                )}
              </div>
              <div className="col-2">
                <NormalButton
                  label={phoneNumberStatus ? "Update" : "Edit"}
                  mainbg={true}
                  onClick={() =>
                    phoneNumberStatus
                      ? this.handleupdate("phoneNumberStatus")
                      : this.handleEdit("phoneNumberStatus")
                  }
                  className="ml-4"
                />
              </div>
            </div>
          </div>
          <div className="w-100" id="hr-line"></div>
          <div className="profile-infomation-area">
            <div className="row w-100 align-items-start">
              <div className="col-10">
                <p className="fs-22 fw-600 mb-1">Password :</p>
                {passwordStatus ? (
                  <div className="row">
                    <div className="col-6">
                      <NormalInput
                        type="text"
                        name="password"
                        label="New Password"
                        value={password}
                        placeholder="New password"
                        onChange={this.handleInput}
                        max={15}
                      />
                      {this.validatorPassword.message(
                        "password",
                        password,
                        "required|password|min:8|max:15"
                      )}
                      <PasswordStrengthMeter data={password} />
                    </div>
                    <div className="col-6">
                      <NormalInput
                        type="text"
                        label="Re-enter Password"
                        name="reenterPassword"
                        value={reenterPassword}
                        onChange={this.handleInput}
                        placeholder="Re enter password"
                        max={15}
                      />
                      {this.validatorPassword.message(
                        "password",
                        reenterPassword,
                        `required|checkPassword:${password}|min:8|max:15`
                      )}
                    </div>
                  </div>
                ) : (
                  <p className="fs-18 fw-500 mb-0">************</p>
                )}
              </div>
              <div className="col-2">
                <NormalButton
                  label={passwordStatus ? "Update" : "Edit"}
                  mainbg={true}
                  onClick={() =>
                    passwordStatus
                      ? this.handleupdate("passwordStatus")
                      : this.handleEdit("passwordStatus")
                  }
                  className="ml-4"
                />
              </div>
            </div>
          </div>
          <div className="w-100" id="hr-line"></div>
        </div>
      </section>
    );
  }
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators(
    {
      getProfileDetails,
      updateProfileDetails,
    },
    dispatch
  );
};

export const Profile = connect(null, mapDispatchToProps)(ProfileClass);
