import { axiosInstance } from "./utilities";

import { logout } from "./utilities";

//api
export var api = async function ({
  method = "get",
  api,
  body,
  status = false,
  token = "",
  baseURL = "normal",
}) {
  return await new Promise((resolve, reject) => {
    // setting token
    if (localStorage.getItem("AuthToken")) {
      axiosInstance.defaults.headers.common["x-access-token"] =
        localStorage.getItem("AuthToken");
    }
    axiosInstance.defaults.headers.common["user-type"] = 4;

    axiosInstance[method](`${getServiceUrl(baseURL)}${api}`, body ? body : "")
      .then((data) => {
        resolve(statusHelper(status, data));
      })
      .catch((error) => {
        try {
          if (error.response) {
            reject(statusHelper(status, error.response));
          } else {
            reject(error);
          }
        } catch (err) {
          reject(err);
        }
      });
  });
};

//status Helper
var statusHelper = (status, data) => {
  if (data.status == 401 || data.status == 403) {
    logout();
  }
  if (status) {
    return {
      status: data.status,
      ...data.data,
    };
  } else {
    return data.data;
  }
};

