import React, { Component } from "react";
import "./navbar.scss";
import { history } from "service/helpers";
import { logout } from "service/utilities";
import { SignOutPopup } from "component/auth/SignOutPopup";
import { getProfileDetails } from "action/AuthAct";
import { getNotificationCount } from "action/Notification";
// Redux Connection
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
export class NavbarClass extends Component {
  state = {
    active: false,
    isPopupOpen: false,
    notifyCount: 0,
  };

  //lifecycles
  componentDidMount() {
    this._getProfileDetails();
    this._getNotificationCount();
  }

  //get Notification Count
  _getNotificationCount = () => {
    let { getNotificationCount } = this.props;
    getNotificationCount().then((data) => {
      this.setState({
        notifyCount: data.unReadCount,
      });
    });
  };
  //get Profile Details
  _getProfileDetails = () => {
    this.props.getProfileDetails();
  };

  //handle Dropdown
  handleDropdown = () => {
    if (!this.state.active) {
      document.addEventListener(
        "click",
        this.handleOutsideClickForProfile,
        false
      );
    } else {
      document.removeEventListener(
        "click",
        this.handleOutsideClickForProfile,
        false
      );
    }
    this.setState((prevState) => ({
      active: !prevState.active,
    }));
  };

  //handle Outside Click For Profile
  handleOutsideClickForProfile = (e) => {
    if (this.node != null) {
      if (this.node.contains(e.target)) {
        return;
      }
    }
    this.handleDropdown();
  };

  //handle Logout
  handleLogout = () => {
    logout();
    this.setState({
      isPopupOpen: false,
    });
  };

  //handle Toggle
  handleToggle = (name) => {
    this.setState((prevState) => ({
      [name]: !prevState[name],
    }));
  };

  render() {
    let { active, isPopupOpen, notifyCount } = this.state;

    let { profileDetails = {}, location } = this.props;

    let { firstName = "" } = profileDetails;
    return (
      <div className="navbar fixed-top p-3">
        <div className="d-flex justify-content-end w-100 align-items-center pr-5">
          <div className="d-flex align-items-center notification-point mr-4">
            <Link to="/admin/notifications" className="text-decoration-none">
              <div className="position-relative cursor-pointer">
                {notifyCount !== 0 ? (
                  <span className="bell-notification"></span>
                ) : null}
                {location.pathname.includes("notifications") ? (
                  <span className="icon-notification fs-24 text-primaryColor"></span>
                ) : (
                  <span className="icon-notification fs-24 text-black-24"></span>
                )}
              </div>
            </Link>
          </div>
          <div
            className="cursor-pointer profile profile-align profile-dropdown"
            onClick={this.handleDropdown}
          >
            <i className="icon-profile fs-23"></i>
            <ul className={`dropdown ${active ? "active" : ""}`}>
              <li className="pb-1">{`Hello ${firstName} !`}</li>
              <div id="hr-line" className="w-100 m-0"></div>
              <li
                className="cursor-pointer"
                onClick={() => history.push("/admin/ProfileManagement")}
              >
                {"Profile Management"}
              </li>
              <li
                onClick={() => this.handleToggle("isPopupOpen")}
                className="cursor-pointer"
              >
                {"Log Out"}
              </li>
            </ul>
          </div>
        </div>
        <SignOutPopup
          isOpen={isPopupOpen}
          toggle={() => this.handleToggle("isPopupOpen")}
          handleLogout={() => this.handleLogout()}
        />
      </div>
    );
  }
}

const mapStateToProps = (state) => ({
  profileDetails: state.profile.profileDetails,
});

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators(
    {
      getProfileDetails,
      getNotificationCount,
    },
    dispatch
  );
};

export const Navbar = connect(mapStateToProps, mapDispatchToProps)(NavbarClass);
