//add Query
export const addQuery = (dataObject, apiObject) => {
  if (!dataObject) {
    return "";
  }

  const keys = [
    "page",
    "limit",
    "search",
    "productName",
    "brand",
    "categoryId",
    "sku",
    "sortCriteria",
    "sortOrder",
    "sellerId",
    "seller",
    "fromDate",
    "toDate",
    "buyer",
  ];

  keys.map((key) => {
    if (dataObject.hasOwnProperty(key) && typeof dataObject[key] != "object") {
      if (apiObject.query.hasOwnProperty(key)) {
        apiObject.addQuery = { key, payload: dataObject[key] };
      }
    } else {
      dataObject[key] &&
        Object.keys(dataObject[key]).map((keyName) => {
          if (apiObject.query.hasOwnProperty(keyName)) {
            apiObject.addQuery = {
              key: keyName,
              payload: dataObject[key][keyName],
            };
          }
        });
    }
  });
};

//scroll Top
export const scrollTop = (id = "root", selector = "getElementById") => {
  var contentPageEle = document[selector](id);

  if (selector != "getElementById") {
    contentPageEle = contentPageEle[0];
  }

  contentPageEle &&
    contentPageEle.scrollTo({
      top: 0,
      behavior: "smooth",
    });
};

//generate Query
export const generateQuery = (query) => {
  let url = "";

  if (query.hasOwnProperty("url_id")) {
    url = `/${query.url_id}`;
  }

  return (
    url +
    Object.keys(query).reduce((accumulator, key, index) => {
      if (
        query[key] == "" ||
        query[key] == null ||
        key == "url_id" ||
        (query[key] !== null && query[key].toString().trim() == "")
      ) {
        return accumulator;
      } else {
        return accumulator + `${index != 0 ? "&" : "?"}${key}=${query[key]}`;
      }
    }, "")
  );
};

export const getSNo = ({ totalPages, page, chunk }, index) => {
  return parseInt((page - 1) * chunk) + index + 1;
};

//phone format
export const phoneFormat = (data) => {
  return data
    ? data
        .toString()
        .replace(/[^\d]/g, "")
        .replace(/[-|+]/g, "")
        .replace(/(\d{3})(\d{3})(\d{4})/, "($1)-$2-$3")
    : data;
};

//number format
export const numberFormat = (data) => {
  return data
    ? data.toString().replace(/[^\d]/g, "").replace(/[-|+]/g, "")
    : data;
};

//amount format
export const amountFormat = (data) => {
  return data
    ? data
        .toString()
        .replace(/[^\d]/g, "")
        .replace(/^(\d+)(\.?\d\d)$/g, "$1.$2")
        .trim()
    : data;
};

//currency Format
export const currencyFormat = (val) => {
  let data = val.toString();
  if (!data.includes(".")) {
    return data
      ? parseFloat(data)
          .toFixed(2)
          .replace(/([0-9])([0-9]{2})$/, "$1.$2")
          .replace(/\B(?=(\d{3})+(?!\d)\.?)/g, ",")
      : data;
  } else {
    return data
      ? parseFloat(data)
          .toFixed(2)
          .replace(/([0-9])([0-9]{2})$/, "$1.$2")
          .replace(/\B(?=(\d{3})+(?!\d)\.?)/g, ",")
      : data;
  }
};

//get Short Data
export const getShortData = (payload, charLength) => {
  let data = payload.toString();
  if (data.length < charLength) {
    return data;
  } else {
    let detail = data.substring(0, charLength - 1) + "...";
    return detail;
  }
};
