import React, { Component } from "react";
import "./style.scss";
import SimpleReactValidator from "simple-react-validator";
import { addSeller } from "../../../action/SellerAct";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import { history } from "service/helpers";
import { NormalButton, NormalInput } from "component/common";
import ManagementPageTitle from "component/common/ManagementPageTitle";
import { phoneFormat, numberFormat } from "service/helperFunctions";

export class AddSellerClass extends Component {
  constructor(props) {
    super(props);
    this.state = {
      id: "",
      firstName: "",
      lastName: "",
      businessName: "",
      businessPhone: "",
      businessLicense: "",
      email: "",
      isButtonClicked: false,
    };

    this.handleChange = this.handleChange.bind(this);
  }

  //validation
  validator = new SimpleReactValidator({
    messages: {
      email: "Invalid Email address",
    },
    element: (message) => (
      <span className="error-message text-danger fs-14">{message}</span>
    ),
    autoForceUpdate: this,
    validators: {
      email: {
        message: "must be a valid Email.",
        rule: (val, params, validator) => {
          return validator.helpers.testRegex(
            val,
            /^[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,3}$/
          );
        },
        messageReplace: (message, params) =>
          message.replace("", this.helpers.toSentence(params)),
        required: true,
      },
    },
  });

  //handle Change
  handleChange = ({ target: { name, value } }) => {
    this.setState({
      [name]: value,
    });
  };

  // toggle
  toggle = (key) => {
    this.setState((prevState) => ({
      [key]: !prevState[key],
    }));
  };

  //handle Submit
  handleSubmit = () => {
    if (this.validator.allValid()) {
      this.setState({ isButtonClicked: true });
      let {
        firstName,
        lastName,
        businessName,
        businessPhone,
        businessLicense,
        email,
      } = this.state;
      let body = {
        firstName,
        lastName,
        businessName,
        businessPhone,
        businessLicense,
        email,
      };
      this.props
        .addSeller(body)
        .then(() => {
          history.push("/admin/sellerManagement");
        })
        .catch(() => {
          this.setState({ isButtonClicked: false });
        });
    } else {
      this.validator.showMessages();
    }
  };

  render() {
    let {
      firstName,
      lastName,
      businessName,
      businessPhone,
      businessLicense,
      email,
      isButtonClicked,
    } = this.state;

    return (
      <div className="outer">
        <div className="w-100 buyer-form">
          <ManagementPageTitle
            title="SELLER MANAGEMENT"
            titleStyle="mb-0"
            titleDesc="Add, Edit and Delete Sellers"
            titleDescStyle="mb-0"
          />
          <div className="buyer-form-box">
            <p className="table-head mb-0 px-3 py-2">Add New Seller</p>
            <div className="custom-divider" />
            <div className="p-0">
              <div className="px-4">
                <NormalInput
                  label="First Name *"
                  type="text"
                  onChange={this.handleChange}
                  value={firstName}
                  placeholder="Enter First Name"
                  name="firstName"
                />
                {this.validator.message(
                  "firstName",
                  firstName,
                  "required|max:30|alpha_space",
                  {
                    className: "text-danger",
                  }
                )}
                <NormalInput
                  label="Last Name *"
                  type="text"
                  onChange={this.handleChange}
                  value={lastName}
                  placeholder="Enter Last Name"
                  name="lastName"
                />
                {this.validator.message(
                  "lastName",
                  lastName,
                  "required|max:30|alpha_space",
                  {
                    className: "text-danger",
                  }
                )}
                <NormalInput
                  label="Email ID*"
                  type="text"
                  onChange={this.handleChange}
                  value={email}
                  placeholder="Enter Email"
                  name="email"
                  max={100}
                />
                {this.validator.message("email", email, "required|email", {
                  className: "text-danger",
                })}
                <NormalInput
                  label="Business Name *"
                  type="text"
                  onChange={this.handleChange}
                  value={businessName}
                  placeholder="Enter Business Name "
                  name="businessName"
                />
                {this.validator.message(
                  "businessName",
                  businessName,
                  "required",
                  {
                    className: "text-danger",
                  }
                )}
                <div className="row">
                  <div className="col-6">
                    <NormalInput
                      label="Corporation License Number(9 Digits) *"
                      max={9}
                      onChange={this.handleChange}
                      value={numberFormat(businessLicense)}
                      placeholder="Enter Corporation License Number"
                      name="businessLicense"
                    />
                    {this.validator.message(
                      "corportion license",
                      businessLicense,
                      "required|numeric|min:9|max:9",
                      {
                        className: "text-danger",
                      }
                    )}
                  </div>
                  <div className="col-6">
                    <NormalInput
                      label="Business Phone Number *"
                      name="businessPhone"
                      placeholder="Enter Business Phone Number"
                      value={phoneFormat(businessPhone)}
                      onChange={this.handleChange}
                      max={14}
                    />
                    {this.validator.message(
                      "businessPhone",
                      businessPhone,
                      "required|phone|min:10",
                      {
                        className: "text-danger",
                      }
                    )}
                  </div>
                </div>
              </div>
              <div className="custom-divider mt-5 mb-2 px-0 mx-0" />
              <div className="px-4 py-3">
                <div className="row">
                  <div className="col-12 d-flex pr-1">
                    <div className="d-flex btn-container-one">
                      <div>
                        <NormalButton
                          label="Cancel"
                          blackBg
                          className="mr-4"
                          onClick={() =>
                            history.push("/admin/sellerManagement")
                          }
                        />
                      </div>
                      <div>
                        <NormalButton
                          label="Add"
                          mainbg
                          className="ml-3"
                          onClick={() => this.handleSubmit()}
                          disabled={isButtonClicked}
                        />
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators(
    {
      addSeller,
    },
    dispatch
  );
};

let component = AddSellerClass;

export const AddSellerComp = connect(null, mapDispatchToProps)(component);
