import React, { Component } from "react";
import { connect } from "react-redux";
import { Modal, ModalBody } from "reactstrap";
import { NormalButton } from "component/common";
import "./style.scss";

export class DeletePopupClass extends Component {
  render() {
    let {
      className = "modal-dialog-centered delete-popup",
      isOpen = true,
      toggle,
      handleChange,
      isPopupButtonDisabled = false,
    } = this.props;

    return (
      <Modal isOpen={isOpen} toggle={toggle} className={className} size="md">
        <ModalBody className="p-0">
          <div className="d-flex justify-content-end">
            <i className="icon-close fs-16 cursor-pointer" onClick={toggle}></i>
          </div>
          <div className="">
            <p className="text-green-18 fs-22 line-height-31 font-weight-bold text-center">
              Are you sure you want to delete ?
            </p>
            <div className="d-flex justify-content-end mt-4 py-3 w-100">
              <div className="mx-auto d-flex">
                <NormalButton
                  label="No"
                  blackBg={true}
                  onClick={() => toggle()}
                  className=""
                  disabled={isPopupButtonDisabled}
                />
                <NormalButton
                  label="Yes"
                  mainbg={true}
                  onClick={() => handleChange()}
                  className="ml-4"
                  disabled={isPopupButtonDisabled}
                />
              </div>
            </div>
          </div>
        </ModalBody>
      </Modal>
    );
  }
}

let component = DeletePopupClass;

export const DeletePopup = connect(null, null)(component);
