import { notification } from "../service/apiVariables";

import { addQuery } from "service/helperFunctions";

//get notification List
export const getNotificationList = (body) => (
  dispatch,
  getState,
  { api, Toast }
) => {
  return new Promise((resolve, reject) => {
    addQuery(body, notification.getNotificationList);
    api({ ...notification.getNotificationList })
      .then((data) => {
        resolve(data);
      })
      .catch(({ message }) => {
        reject(Toast({ type: "error", message }));
      });
  });
};

//delete Notifications
export const deleteNotifications = (id) => (
  dispatch,
  getState,
  { api, Toast }
) => {
  return new Promise((resolve, reject) => {
    let { deleteNotifications } = notification;
    deleteNotifications.id = id;
    api({ ...notification.deleteNotifications })
      .then(({ data }) => {
        resolve(data);
      })
      .catch(({ message }) => {
        reject(Toast({ type: "error", message }));
      });
  });
};

