import React, { Component } from "react";
import "./style.scss";
import SimpleReactValidator from "simple-react-validator";
import { addBuyer } from "../../../action/BuyerAct";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import { history } from "service/helpers";
import { NormalButton, NormalInput } from "component/common";
import ManagementPageTitle from "component/common/ManagementPageTitle";
import { phoneFormat, numberFormat } from "service/helperFunctions";
import { Modal, ModalBody } from "reactstrap";

export class AddBuyerClass extends Component {
  constructor(props) {
    super(props);
    this.state = {
      id: "",
      firstName: "",
      lastName: "",
      businessName: "",
      businessPhone: "",
      businessLicense: "",
      email: "",
      referralCode: "",
      isButtonClicked: false,
      isPopupOpen: false,
    };

    this.handleChange = this.handleChange.bind(this);
  }

  //validation
  validator = new SimpleReactValidator({
    messages: {
      email: "Invalid Email address",
    },
    element: (message) => (
      <span className="error-message text-danger fs-14">{message}</span>
    ),
    autoForceUpdate: this,
    validators: {
      email: {
        message: "must be a valid Email.",
        rule: (val, params, validator) => {
          return validator.helpers.testRegex(
            val,
            /^[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,3}$/
          );
        },
        messageReplace: (message, params) =>
          message.replace("", this.helpers.toSentence(params)),
        required: true,
      },
    },
  });

  //handle Change
  handleChange = ({ target: { name, value } }) => {
    this.setState({
      [name]: value,
    });
  };

  //toggle
  toggle = (key) => {
    this.setState((prevState) => ({
      [key]: !prevState[key],
    }));
  };

  //handle Submit
  handleSubmit = () => {
    if (this.validator.allValid()) {
      let {
        firstName,
        lastName,
        businessName,
        referralCode,
        businessPhone,
        businessLicense,
        email,
        refCount,
      } = this.state;
      let body = {
        firstName,
        lastName,
        businessName,
        businessPhone,
        businessLicense,
        referralCode,
        email,
        refCount,
      };
      if (refCount <= 7) {
        this.setState({
          isButtonClicked: true,
        });
        this.props
          .addBuyer(body)
          .then(() => {
            history.push("/admin/buyerManagement");
          })
          .catch(() => {
            this.setState({
              isButtonClicked: false,
            });
          });
      } else {
        this.toggle("isPopupOpen");
      }
    } else {
      this.validator.showMessages();
    }
  };

  render() {
    let {
      firstName,
      lastName,
      referralCode,
      businessName,
      businessPhone,
      businessLicense,
      email,
      isButtonClicked,
      refCount,
      isPopupOpen,
    } = this.state;

    return (
      <div className="outer">
        <div className="w-100 buyer-form">
          <ManagementPageTitle
            title="CUSTOMER MANAGEMENT"
            titleStyle="mb-0"
            titleDesc="Add, Edit and Delete Customers"
            titleDescStyle="mb-0"
          />
          <div className="buyer-form-box">
            <p className="table-head mb-0 px-3 py-2">Add New Customer</p>
            <div className="custom-divider" />
            <div className="p-0">
              <div className="px-4">
                <NormalInput
                  label="First Name *"
                  type="text"
                  onChange={this.handleChange}
                  value={firstName}
                  placeholder="Enter First Name"
                  name="firstName"
                />
                {this.validator.message(
                  "firstName",
                  firstName,
                  "required|max:30|alpha_space",
                  {
                    className: "text-danger",
                  }
                )}
                <NormalInput
                  label="Last Name *"
                  type="text"
                  onChange={this.handleChange}
                  value={lastName}
                  placeholder="Enter Last Name"
                  name="lastName"
                />
                {this.validator.message(
                  "lastName",
                  lastName,
                  "required|max:30|alpha_space",
                  {
                    className: "text-danger",
                  }
                )}
                <NormalInput
                  label="Email ID*"
                  type="text"
                  onChange={this.handleChange}
                  value={email}
                  placeholder="Enter Email"
                  name="email"
                  max={100}
                />
                {this.validator.message("email", email, "required|email", {
                  className: "text-danger",
                })}
                <NormalInput
                  label="Business Name *"
                  type="text"
                  onChange={this.handleChange}
                  value={businessName}
                  placeholder="Enter Business Name "
                  name="businessName"
                />
                {this.validator.message(
                  "businessName",
                  businessName,
                  "required",
                  {
                    className: "text-danger",
                  }
                )}
                <div className="row">
                  <div className="col-6">
                    <NormalInput
                      label="Corporation License Number(9 Digits) *"
                      max={9}
                      onChange={this.handleChange}
                      value={numberFormat(businessLicense)}
                      placeholder="Enter Corporation License Number"
                      name="businessLicense"
                    />
                    {this.validator.message(
                      "corportion license",
                      businessLicense,
                      "required|numeric|max:9|min:9",
                      {
                        className: "text-danger",
                      }
                    )}
                  </div>
                  <div className="col-6">
                    <NormalInput
                      label="Business Phone Number *"
                      name="businessPhone"
                      placeholder="Enter Business Phone Number"
                      value={phoneFormat(businessPhone)}
                      onChange={this.handleChange}
                      max={14}
                    />
                    {this.validator.message(
                      "businessPhone",
                      businessPhone,
                      "required|phone|min:10",
                      {
                        className: "text-danger",
                      }
                    )}
                  </div>
                </div>
                <div className="row">
                  <div className="col-12">
                    <NormalInput
                      label="Referral count"
                      max={2}
                      onChange={this.handleChange}
                      value={numberFormat(refCount)}
                      placeholder="Enter Referral count"
                      name="refCount"
                    />
                  </div>
                </div>
                <NormalInput
                  label="Referral Code (Optional) "
                  type="text"
                  onChange={this.handleChange}
                  value={referralCode}
                  placeholder="Enter Referral Code"
                  name="referralCode"
                />
              </div>
              <div className="custom-divider mt-5 mb-2 px-0 mx-0" />
              <div className="px-4 py-3">
                <div className="row">
                  <div className="col-12 d-flex pr-1 justify-content-between">
                    <div className="d-flex btn-container-one">
                      <div className="">
                        <NormalButton
                          label="Cancel"
                          blackBg
                          onClick={() => history.push("/admin/buyerManagement")}
                          className="mr-4"
                        />
                      </div>
                      <div className="">
                        <NormalButton
                          label="Add"
                          mainbg
                          className="ml-3"
                          onClick={() => this.handleSubmit()}
                          disabled={isButtonClicked}
                        />
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <Modal
          isOpen={isPopupOpen}
          className={"modal-dialog-centered error-popup"}
          size="md"
        >
          <ModalBody>
            <h4 className="text-black fw-600 fs-18 text-center">
              You cannot exceed the limit of 7 referrals per user.
            </h4>
            <div className="d-flex justify-content-end mt-5 pt-3 w-100">
              <div className="mx-auto d-flex">
                <NormalButton
                  label="Got it"
                  mainbg={true}
                  onClick={() => this.toggle("isPopupOpen")}
                />
              </div>
            </div>
          </ModalBody>
        </Modal>
      </div>
    );
  }
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators(
    {
      addBuyer,
    },
    dispatch
  );
};

let component = AddBuyerClass;

export const AddBuyerComp = connect(null, mapDispatchToProps)(component);
