import React, { Component } from "react";
import { connect } from "react-redux";
import {
  TableStripedWrapper,
  CommonPageLoader,
  NormalButton,
} from "component/common";
import { orderViewHeader } from "Data/OrderManagement";

import {
  getOrdersList,
  getOrderById,
  markOrderAsComplete,
  acceptOrder,
  getOrdersProductList,
} from "action/OrderAct";
import { getSellerList } from "action/ProductAct";
import { bindActionCreators } from "redux";
import { currencyFormat, phoneFormat } from "service/helperFunctions";
import "./style.scss";
import ManagementPageTitle from "component/common/ManagementPageTitle";
import search from "assets/svg/search.svg";
import { history } from "service/helpers";
import { ModifyProductPopup } from "../ModifyProductPopup/ModifyProductPopup";
import { OrderCancellation } from "../OrderCancellation/OrderCancellation";
import { Toast } from "service/toast";

export class ViewOrderClass extends Component {
  state = {
    search: "",
    headerDetails: orderViewHeader,
    id: "",
    sortCriteria: "",
    sortOrder: "",
    searchValue: "",
    sellerId: "",
    isCancellationOpen: false,
    orderData: {
      buyerDetails: [],
      deliveryAddress: "",
      products: [],
      status: "",
      totalItems: "",
      _id: "",
    },
    isModifyPopupOpen: false,
  };

  //lifecycles
  componentDidMount() {
    let { params } = this.props;
    let { orderId, type } = params;
    let { headerDetails } = this.state;

    let body = {
      _id: orderId,
      search: "",
    };
    this._getOrderById(body);
    if (type !== "Pending") {
      headerDetails[headerDetails.length - 1].isButtonRequired = false;
    }
    headerDetails[headerDetails.length - 1].onClickAction = () =>
      this.handleModifyToggle("isModifyPopupOpen", orderId);
    this.setState({
      _id: orderId,
      headerDetails,
    });
  }

  //get order details by id
  _getOrderById = ({ _id, search }) => {
    let { sortCriteria, sortOrder } = this.state;
    this.setState({ isLoading: true });
    let query = {
      search,
      sortCriteria,
      sortOrder,
    };
    this.props
      .getOrderById(_id, query)
      .then((data) => {
        this.setState({
          orderData: data,
          isLoading: false,
        });
      })
      .catch(() => {
        this.setState({
          isLoading: false,
        });
      });
  };

  //handle Change
  handleChange = ({ target: { name, value } }) => {
    this.setState({
      [name]: value,
    });
  };

  //handle filter
  handleFilter = (sortKey, sortOrder) => {
    let { _id, search } = this.state;
    this.setState(
      {
        sortCriteria: sortKey,
        sortOrder,
      },
      () => {
        let body = {
          _id,
          search,
        };
        this._getOrderById(body);
      }
    );
  };

  //calculate total
  calculateTotal = (array, type) => {
    if (array.length === 0) {
      return "0";
    }
    let sum = 0;
    if (type === "price") {
      for (let x = 0; x < array.length; x++) {
        sum += array[x].price * array[x].quantity;
        console.log(array[x], "array[x]array[x]array[x]");
      }
      return parseFloat(sum).toFixed(2);
    }
    if (type === "quantity") {
      for (let x = 0; x < array.length; x++) {
        sum += array[x].quantity;
      }
      return sum;
    }
  };

  //handle toggle
  handleToggle = (name, id) => {
    if (id) {
      this.setState({ id });
    } else {
      this.setState({ id: "" });
    }
    this.setState((prevState) => ({
      [name]: !prevState[name],
    }));
  };

  // handle modify toggle
  handleModifyToggle = (name, id) => {
    if (id) {
      this.setState({
        id: id,
      });
    } else {
      this.setState({ id: "" });
    }
    this.setState((prevState) => ({
      [name]: !prevState[name],
    }));
  };

  //add project to list
  addProductToList = (body) => {
    let { orderData } = this.state;
    if (Array.isArray(body)) {
      orderData.products = body;
      this.setState({
        orderData: { ...orderData },
      });
    } else {
      orderData.products.push(body);
      this.setState({
        orderData: { ...orderData },
      });
    }
  };

  //handle delete
  handleDelete = (index) => {
    let { orderData } = this.state;
    let products = orderData.products;
    products.splice(index, 1);
    this.setState({
      orderData: {
        ...orderData,
        products: [...orderData.products],
      },
    });
  };

  //update quantity
  updateQuantity = (_id, quantity, operator, index) => {
    let { orderData } = this.state;
    let products = orderData.products;
    let item = products.find((x) => x._id === _id);
    switch (operator) {
      case "-":
        item = {
          ...item,
          quantity: quantity - 1,
        };
        break;
      case "+":
        item = {
          ...item,
          quantity: quantity + 1,
        };
        break;
      default:
        return;
    }
    this.setState({
      orderData: {
        ...orderData,
        products: [
          ...orderData.products.slice(0, index),
          item,
          ...orderData.products.slice(index + 1),
        ],
      },
    });
  };

  //handle submit
  handleSubmit = (_id, func) => {
    this.setState({ isButtonClicked: true }, () => {
      let body = {
        _id,
      };
      func(body)
        .then(() => {
          this.handleCancel();
        })
        .catch(() => {
          this.setState({
            isButtonClicked: false,
          });
        });
    });
  };

  //handle cancel
  handleCancel = () => {
    history.push("/admin/order-management");
  };

  //handle accept
  handleAccept = (_id) => {
    let { orderData } = this.state;
    if (orderData.products.length !== 0) {
      this.setState({ isButtonClicked: true }, () => {
        let { acceptOrder } = this.props;
        let payload = {
          products: orderData.products,
        };
        acceptOrder(_id, payload)
          .then(() => {
            this.handleCancel();
            this.setState({ isButtonClicked: false });
          })
          .catch(() => {
            this.setState({
              isButtonClicked: false,
            });
          });
      });
    } else {
      Toast({ type: "error", message: "Please select atleast one products" });
    }
  };

  //handle marking
  handleMarking = (_id) => {
    this.setState({ isButtonClicked: true }, () => {
      let { markOrderAsComplete } = this.props;
      let body = {
        _id,
      };
      markOrderAsComplete(body)
        .then(() => {
          this.handleCancel();
        })
        .catch(() => {
          this.setState({
            isButtonClicked: false,
          });
        });
    });
  };

  render() {
    let { params, getOrdersProductList } = this.props;
    let { type = "Pending" } = params;
    let {
      headerDetails,
      orderData,
      isLoading,
      searchValue,
      selectedHeader,
      isModifyPopupOpen,
      isCancellationOpen,
      id,
      _id,
      isButtonClicked,
    } = this.state;
    let { products = [], orderRef = "", status } = orderData;

    return (
      <>
        {isLoading ? (
          <CommonPageLoader />
        ) : (
          <div className="common-table">
            <div className="d-flex justify-content-between">
              <div className="d-flex align-items-end tab-head">
                <ManagementPageTitle
                  title="ORDER MANAGEMENT"
                  titleStyle="mb-0"
                />
              </div>
            </div>
            <div className="bg-white product-inner-table">
              <div className="row pb-2 px-2">
                <div className="col-7 text-left my-auto">
                  <span>
                    <div className="fs-24 d-flex align-items-center">
                      <span className="pt-2 fs-18 mr-2 pr-3">
                        <span className="pt-2">
                          <i
                            className="icon-back fs-20 cursor-pointer"
                            onClick={() =>
                              history.push("/admin/order-management")
                            }
                          />
                        </span>
                      </span>
                      <span className="fs-19">
                        ORDER:{" "}
                        <span className="fw-500">
                          {orderRef ? "#" + orderRef : "-"}
                        </span>
                      </span>
                      <span
                        className={`${
                          status == 2
                            ? "text-grey"
                            : status == 3 || status == 4
                            ? "text-success"
                            : "text-danger"
                        } fs-19 fw-700 pl-2 ml-1`}
                      >
                        {" "}
                        {status == 2
                          ? "Pending"
                          : status == 3
                          ? "Accepted"
                          : status == 4
                          ? "Completed"
                          : "Cancelled"}
                      </span>
                    </div>
                  </span>
                </div>
                <div className="col-5 d-flex justify-content-end text-right pr-5">
                  <div className="search-container-area">
                    <div className="position-relative">
                      <img
                        className="mailIcon"
                        src={search}
                        alt="Logo"
                        height="15px"
                      />
                      <input
                        className="search-input"
                        placeholder="Search"
                        value={searchValue}
                        name="searchValue"
                        onChange={this.handleChange}
                      />
                    </div>
                  </div>
                </div>
              </div>
              <div className="custom-divider" />
              {selectedHeader >= 2 ? (
                <div className="w-100 small-text fs-12 margin-set py-1">
                  04 - 08 - 2020 / 12:34 hrs
                </div>
              ) : null}
              <div className="row px-1 my-3 d-flex align-items-center">
                <div className="fs-14 fw-500 col-3 data-title">
                  DELIVERY ADDRESS
                </div>
                <div className="view-order-address fs-14 ml-2 col-3">
                  {orderData.deliveryAddress
                    ? orderData.deliveryAddress.address
                    : "-"}
                </div>
                <div className="col-2 fs-14 fw-500 ml-0 pl-0 text-uppercase text-right">
                  {orderData.buyer
                    ? `${orderData.buyer.firstName} ${orderData.buyer.lastName}`
                    : "-"}
                </div>
                <div className="fs-14 ml-3">
                  Contact -{" "}
                  {orderData.buyer
                    ? phoneFormat(orderData.buyer.businessPhone)
                    : "-"}
                </div>
              </div>
              <div className="custom-divider" />
              <div className="table-wrapper mt-4">
                <TableStripedWrapper
                  headerDetails={headerDetails}
                  placeHolder={""}
                  search={this.state.search}
                  handleFilter={this.handleFilter}
                >
                  {products.length !== 0 ? (
                    products
                      .filter((data) => {
                        if (searchValue === "") {
                          return data;
                        } else if (
                          (data.productName || data.sku) &&
                          data.sku
                            .toLocaleLowerCase()
                            .includes(searchValue.toLocaleLowerCase())
                        ) {
                          return data;
                        } else if (
                          (data.productName || data.sku) &&
                          data.productName
                            .toLocaleLowerCase()
                            .includes(searchValue.toLocaleLowerCase())
                        ) {
                          return data;
                        }
                      })
                      .map((item, index) => {
                        let {
                          _id,
                          sku,
                          productImg = [],
                          price,
                          quantity,
                        } = item;

                        return (
                          <tr key={index} className="">
                            <td className="text-black-24 text-center fs-14">
                              {sku ? sku : "-"}
                            </td>
                            <td className="text-black-24 text-center fs-14 image-card">
                              {productImg.length !== 0 ? (
                                <img
                                  src={productImg[0]}
                                  alt="productImage"
                                  height="40"
                                  width="40"
                                />
                              ) : (
                                "-"
                              )}
                            </td>
                            <td className="text-black-24 text-center fs-14">
                              {price ? "$" + currencyFormat(price) : "-"}
                            </td>
                            <td className="text-center">
                              {type === "Pending" && (
                                <span
                                  className={`icon-minus cursor-pointer fw-400 fs-14 pr-3 ${
                                    quantity !== 0
                                      ? "substract"
                                      : "text-grey-c7"
                                  }`}
                                  onClick={() =>
                                    this.updateQuantity(
                                      _id,
                                      quantity,
                                      quantity > 0 && "-",
                                      index
                                    )
                                  }
                                ></span>
                              )}
                              {quantity ? quantity : "0"}
                              {type === "Pending" && (
                                <span
                                  className="cursor-pointer fw-800 icon-plus fs-14 pl-3 add"
                                  onClick={() =>
                                    this.updateQuantity(
                                      _id,
                                      quantity,
                                      "+",
                                      index
                                    )
                                  }
                                />
                              )}
                            </td>
                            <td />
                            <td className="text-center">
                              <div className="d-flex justify-content-end">
                                {type === "Pending" ? (
                                  <span
                                    className="fs-12 cursor-pointer fw-600 text-danger px-3 d-flex align-items-center"
                                    onClick={() => this.handleDelete(index)}
                                  >
                                    <i className="icon-delete-danger fs-16 pr-2" />
                                    Delete Product
                                  </span>
                                ) : null}
                              </div>
                            </td>
                          </tr>
                        );
                      })
                  ) : (
                    <tr className="h3 text-center">
                      <td colSpan="8" className="py-4 text-center">
                        {"No Record Found"}
                      </td>
                    </tr>
                  )}
                  <tr className="">
                    <td>
                      <div className="text-black-22 fw-500 fs-18 d-flex align-items-center justify-content-center">
                        TOTAL: {products.length}
                        <span className="fs-14 pl-1 small-text">Items</span>
                      </div>
                    </td>
                    <td />
                    <td>
                      <div
                        className="text-black-22 fw-500 fs-18 text-center"
                        id="totatPrice"
                      >
                        $ {this.calculateTotal(orderData.products, "price")}
                      </div>
                    </td>
                    <td>
                      <div
                        className="text-black-22 fw-500 fs-18 d-flex align-items-center justify-content-center"
                        id="totalProducts"
                      >
                        {this.calculateTotal(orderData.products, "quantity")}
                        <span className="fs-14 pl-1 small-text">
                          Quantities
                        </span>
                      </div>
                    </td>
                    <td colSpan="2">
                      {orderData.totalTaxes ? (
                        <div className="text-black-22 fw-500 fs-18 d-flex align-items-center justify-content-center">
                          Total Tax: {orderData.totalTaxes}
                        </div>
                      ) : (
                        ""
                      )}
                    </td>
                  </tr>
                </TableStripedWrapper>
              </div>
              {type !== "Cancelled" ? (
                <div className="row">
                  <div className="col-12 d-flex pt-3 pb-4 pr-4">
                    <div className="d-flex btn-container-one">
                      <span className="ml-2 mr-4">
                        <NormalButton
                          label="Cancel Order"
                          blackBg
                          disabled={isButtonClicked}
                          onClick={() =>
                            this.handleToggle("isCancellationOpen")
                          }
                        />
                      </span>
                      {type === "Pending" ? (
                        <span className="ml-2">
                          <NormalButton
                            label="Accept Order"
                            mainbg
                            disabled={isButtonClicked}
                            onClick={() => this.handleAccept(_id)}
                          />
                        </span>
                      ) : type === "Accepted" ? (
                        <span className="ml-2">
                          <NormalButton
                            label="Mark as Completed"
                            successBg
                            disabled={isButtonClicked}
                            onClick={() => this.handleMarking(_id)}
                          />
                        </span>
                      ) : null}
                    </div>
                  </div>
                </div>
              ) : null}
            </div>
            <ModifyProductPopup
              isOpen={isModifyPopupOpen}
              toggle={() => this.handleModifyToggle("isModifyPopupOpen")}
              singleProductId={_id}
              addProductToList={this.addProductToList}
              getOrdersProductList={getOrdersProductList}
              sellerId={orderData.sellerId}
              products={products}
            />
            <OrderCancellation
              isOpen={isCancellationOpen}
              toggle={() => this.handleToggle("isCancellationOpen")}
              singleProductId={_id}
              _getProductsList={() => this._getProductsList(1)}
            />
          </div>
        )}
      </>
    );
  }
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators(
    {
      getOrdersList,
      getOrderById,
      getSellerList,
      markOrderAsComplete,
      acceptOrder,
      getOrdersProductList,
    },
    dispatch
  );
};

export const ViewOrderComp = connect(null, mapDispatchToProps)(ViewOrderClass);
