import React from "react";
import "./NormalInput.scss";

export const NormalInput = ({
  className = "form-control w-100",
  placeholder = "",
  label = "",
  onChange,
  value = "",
  name,
  disabled = false,
  type = "text",
  max = "",
  min = "",
  requried = false,
  readOnly = false,
  ismarginZero = false,
}) => {
  return (
    <div className={`normal-input ${ismarginZero ? " mt-1" : ""}`}>
      {label !== "" && !readOnly ? (
        <div>
          <label className="mb-1">{label}</label>
          {requried ? <span>*</span> : null}
        </div>
      ) : null}
      {readOnly ? (
        <div className="text-black-22">
          <span className="mb-1 fs-21 fw-600">{label}</span>
          <p className="fw-400 fs-18 mb-0">{value}</p>
        </div>
      ) : (
        <input
          className={className}
          name={name}
          type={type}
          disabled={disabled}
          value={value}
          min={0}
          placeholder={placeholder}
          minLength={min}
          maxLength={max}
          onChange={(e) => {
            let body = {};
            let tempVal = e.target.value;

            body = {
              target: {
                name: e.target.name,
                value: tempVal,
              },
            };

            onChange(body);
          }}
        />
      )}
    </div>
  );
};
