import React, { Component } from "react";
import { NormalButton } from "component/common";
// Redux Connection
import { connect } from "react-redux";
import { Modal, ModalBody } from "reactstrap";
import "./style.scss";

export class SignOutPopupClass extends Component {
  render() {
    let {
      className = "modal-dialog-centered sign-out-popup",
      isOpen = true,
      toggle,
      handleLogout = {},
    } = this.props;

    return (
      <Modal isOpen={isOpen} toggle={toggle} className={className} size="lg">
        <ModalBody>
          <div>
            <h4 className="text-black-22 fw-700 fs-24 text-center">
              Are you sure you want to log out !!!
            </h4>
            <div className="d-flex justify-content-end mt-4 py-3 w-100">
              <div className="mx-auto d-flex">
                <NormalButton
                  label="No"
                  blackBg={true}
                  onClick={() => toggle()}
                  className=""
                />
                <NormalButton
                  label="Yes"
                  mainbg={true}
                  onClick={() => handleLogout()}
                  className="ml-4"
                />
              </div>
            </div>
          </div>
        </ModalBody>
      </Modal>
    );
  }
}

export const SignOutPopup = connect(null, null)(SignOutPopupClass);
