import React, { Component } from "react";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import ManagementPageTitle from "component/common/ManagementPageTitle";
import { NormalDate } from "../common/NormalDate/index";
import { NormalButton } from "component/common";
import SimpleReactValidator from "simple-react-validator";
import { getReferralReport } from "action/ReportsAct";
import "./style.scss";
import moment from "moment";

export class ReferralsReportCompClass extends Component {
  state = {
    fromDate: "",
    toDate: "",
    isButtonClicked: false,
  };

  //validator
  validator = new SimpleReactValidator({
    element: (message) => (
      <span className="error-message text-danger fs-14">{message}</span>
    ),
    autoForceUpdate: this,
  });

  //handle submit
  handleSubmit = () => {
    if (this.validator.allValid()) {
      this.setState({ isButtonClicked: true });
      let { fromDate, toDate } = this.state;
      let body = {
        fromDate: moment(fromDate).format("DD-MM-YYYY"),
        toDate: moment(toDate).format("DD-MM-YYYY"),
      };
      this.props
        .getReferralReport(body)
        .then(() => {
          this.validator.hideMessages();
          this.setState({
            fromDate: "",
            toDate: "",
            isButtonClicked: false,
          });
        })
        .catch(() => {
          this.setState({ isButtonClicked: false });
        });
    } else {
      this.validator.showMessages();
    }
  };

  //handle Input Change
  handleInputChange = ({ target: { name, value } }) => {
    this.setState({
      [name]: value,
    });
  };

  render() {
    let { fromDate, toDate, isButtonClicked } = this.state;

    return (
      <div className="report">
        <div className="d-flex justify-content-between">
          <div className="d-flex align-items-end tab-head">
            <ManagementPageTitle
              title="REFERRALS REPORT"
              titleStyle="mb-0"
              titleDescStyle="mb-0"
            />
          </div>
        </div>
        <h6 className="px-2 mt-4">Select Referral Activation Date range</h6>
        <div className="row  d-flex px-2 mt-4">
          <div className="col-5 d-flex">
            <span className="mr-3 mt-3">From</span>
            <div>
              <NormalDate
                name="fromDate"
                value={fromDate}
                onChange={this.handleInputChange}
                maxdate={toDate == "" ? new Date() : new Date(toDate)}
              />
              {this.validator.message("from date", fromDate, "required")}
            </div>
          </div>
          <div className="col-5 d-flex">
            <span className="mr-3 mt-3">To</span>
            <div>
              <NormalDate
                name="toDate"
                value={toDate}
                onChange={this.handleInputChange}
                maxdate={new Date()}
                mindate={new Date(fromDate)}
              />
              {this.validator.message("to date", toDate, "required")}
            </div>
          </div>
        </div>
        <div className="d-flex mt-5 mr-3">
          <NormalButton
            label="Request Report"
            mainbg
            className="ml-auto"
            onClick={() => this.handleSubmit()}
            disabled={isButtonClicked}
          />
        </div>
      </div>
    );
  }
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators(
    {
      getReferralReport,
    },
    dispatch
  );
};

export const ReferralsReportComp = connect(
  null,
  mapDispatchToProps
)(ReferralsReportCompClass);
