import { orderManagement } from "../service/apiVariables";

import { addQuery } from "service/helperFunctions";

//get Orders List
export const getOrdersList = (status, query) => (
  dispatch,
  getState,
  { api, Toast }
) => {
  return new Promise((resolve, reject) => {
    let { getOrderList } = orderManagement;
    getOrderList.status = status;
    addQuery(query, getOrderList);
    api({ ...getOrderList })
      .then(({ data }) => {
        resolve(data);
      })
      .catch(({ message }) => {
        reject(Toast({ type: "error", message }));
      });
  });
};

//get Order By Id
export const getOrderById = (id, query) => (
  dispatch,
  getState,
  { api, Toast }
) => {
  return new Promise((resolve, reject) => {
    let { getOrderById } = orderManagement;
    getOrderById.id = id;
    addQuery(query, getOrderById);
    api({
      ...getOrderById,
    })
      .then(({ data }) => {
        resolve(data);
      })
      .catch(({ message }) => {
        reject(Toast({ type: "error", message }));
      });
  });
};

//mark Order As Complete
export const markOrderAsComplete = (body) => (
  dispatch,
  getState,
  { api, Toast }
) => {
  let { markOrderAsComplete } = orderManagement;
  return new Promise((resolve, reject) => {
    markOrderAsComplete.id = body._id;
    api({ ...markOrderAsComplete, body })
      .then(({ data, message }) => {
        resolve(data);
        Toast({ type: "success", message });
      })
      .catch(({ message }) => {
        reject(Toast({ type: "error", message }));
      });
  });
};

//accept Order
export const acceptOrder = (id, body) => (
  dispatch,
  getState,
  { api, Toast }
) => {
  let { acceptOrder } = orderManagement;
  return new Promise((resolve, reject) => {
    acceptOrder.id = id;
    api({ ...acceptOrder, body })
      .then(({ data, message }) => {
        resolve(data);
        Toast({ type: "success", message });
      })
      .catch(({ message }) => {
        reject(Toast({ type: "error", message }));
      });
  });
};

//cancel Order
export const cancelOrder = (body) => (dispatch, getState, { api, Toast }) => {
  let { cancelOrder } = orderManagement;
  return new Promise((resolve, reject) => {
    cancelOrder.id = body._id;
    body = {
      reason: body.reason,
    };
    api({ ...cancelOrder, body })
      .then(({ data, message }) => {
        resolve(data);
        Toast({ type: "success", message });
      })
      .catch(({ message }) => {
        reject(Toast({ type: "error", message }));
      });
  });
};

//get Orders Product List
