import React from "react";
import "./style.scss";

export const NormalCheckbox = ({
  className = "custom-checkbox",
  label = "",
  value = "",
  name = "",
  onChange,
  checked,
  disable = false,
}) => {
  return (
    <label className={className}>
      <input
        type="checkbox"
        name={name}
        value={value}
        checked={checked}
        disabled={disable}
        onChange={({ target: { name, checked: Checked } }) => {
          onChange && onChange({ target: { name, value: Checked } });
        }}
      />
      <span className="checkbox-tick ">
        <i className="icon-tick-transparent "></i>
      </span>
      {label ? <span className="label-txt fs-16">{label}</span> : ""}
    </label>
  );
};
