import React, { Component } from "react";
import "./resetpassword.scss";
import SimpleReactValidator from "simple-react-validator";
import { forgotPasswordTokeValidation, changePassword } from "action/AuthAct";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import { history } from "service/helpers";
import { PasswordStrengthMeter } from "component/common";
import forgot_pass from "assets/svg/forgot_pass.svg";
export class ResetPasswordClass extends Component {
  state = {
    password: "",
    reEnterPassword: "",
    status: "",
    passwordVisible: false,
    reEnterPasswordVisible: false,
    isPasswordResetted: false,
    isButtonClicked: false,
  };

  //validation
  validator = new SimpleReactValidator({
    element: (message) => (
      <span className="error-message text-danger fs-14">{message}</span>
    ),
    autoForceUpdate: this,
    validators: {
      password: {
        message: "The :attribute must be a valid format.",
        rule: (val, params, validator) => {
          return (
            validator.helpers.testRegex(
              val,
              /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[#$^+=!*()@%&]).{8,20}$/
            ) && params.indexOf(val) === -1
          );
        },
        messageReplace: (message, params) =>
          message.replace("", this.helpers.toSentence(params)),
        required: true,
      },
      checkPassword: {
        message: "Given :attribute does not match",
        rule: function (val, params) {
          return val === params[0];
        },
      },
    },
  });

  //handle Change
  handleChange = ({ target: { name, value } }) => {
    this.setState({
      [name]: value,
    });
  };

  //handle Submit
  handleSubmit = (e) => {
    e.preventDefault();
    if (this.validator.allValid()) {
      this.setState({ isButtonClicked: true }, () => {
        let { password } = this.state;
        let body = {
          password: password,
        };

        this.props
          .changePassword(body)
          .then((status) => {
            if (status == 200) {
              localStorage.removeItem("AuthToken");
              this.setState({
                isPasswordResetted: true,
                isButtonClicked: false,
              });
            } else {
              this.setState({
                isButtonClicked: false,
              });
            }
          })
          .catch(() => {
            this.setState({
              isButtonClicked: false,
            });
          });
      });
    } else {
      this.validator.showMessages();
      this.forceUpdate();
    }
  };

  //lifecycles
  componentDidMount() {
    let { token } = this.props;
    if (!token) {
      history.push("/");
    } else {
      localStorage.setItem("AuthToken", token);
      this.props.forgotPasswordTokeValidation().then((status) => {
        if (status == 200) {
          this.setState({ status });
        } else {
          history.push("/");
        }
      });
    }
  }

  //toggle
  toggle = (key) => {
    this.setState((prevState) => ({
      [key]: !prevState[key],
    }));
  };

  render() {
    const {
      password,
      reEnterPassword,
      passwordVisible,
      reEnterPasswordVisible,
      isPasswordResetted,
      isButtonClicked,
    } = this.state;

    return (
      <>
        {isPasswordResetted ? (
          <div>
            <h4 className="font-weight-bold fs-24 text-black-22 mt-4 mb-3 text-center">
              {"Your password has been reset successfully!"}
            </h4>
            <div className="w-100 d-flex justify-content-center mb-3">
              <img
                src={forgot_pass}
                alt="forgot"
                className="password-reset-image"
              />
            </div>
          </div>
        ) : (
          <div className="w-100 forgot-password">
            <div className="w-100 mt-4 d-flex align-items-center flex-column">
              <h4 className="font-weight-bold fs-24 text-primaryColor mb-md-4 mb-3">
                {"Forgot Password"}
              </h4>
              <div className="w-100 mb-4">
                <label className="fw-600 mb-2">{"Enter New Password"}</label>
                <div className="input-group">
                  <input
                    type={reEnterPasswordVisible ? "text" : "password"}
                    className="form-control w-100"
                    name="password"
                    value={password}
                    onChange={this.handleChange}
                    placeholder="Password"
                    maxLength={15}
                  />
                  <div className="input-group-addon right">
                    <span
                      onClick={(e) => this.toggle("reEnterPasswordVisible")}
                      className={`cursor-pointer icon-${
                        reEnterPasswordVisible ? "eye" : "eye-hide"
                      } fs-18`}
                    ></span>
                  </div>
                </div>
                {this.validator.message(
                  "new password",
                  password,
                  "required|min:8|max:15|password"
                )}
                <PasswordStrengthMeter isLogin={true} data={password} />
              </div>
              <div className="w-100">
                <label className="fw-600 mb-2">{"Confirm Password"}</label>
                <div className="input-group">
                  <input
                    type={passwordVisible ? "text" : "password"}
                    className="form-control w-100"
                    id="reEnterPassword"
                    name="reEnterPassword"
                    value={reEnterPassword}
                    onChange={this.handleChange}
                    placeholder="Re-enter password"
                    maxLength={15}
                  />
                  <div className="input-group-addon right">
                    <span
                      onClick={(e) => this.toggle("passwordVisible")}
                      className={`cursor-pointer icon-${
                        passwordVisible ? "eye" : "eye-hide"
                      } fs-18`}
                    ></span>
                  </div>
                </div>
                {this.validator.message(
                  "confirm password",
                  reEnterPassword,
                  `required|checkPassword:${password}`
                )}
              </div>
              <div className="d-flex justify-content-center mt-5 pt-2">
                <input
                  className="btn py-2 login-btn rounded-0 text-white-f4 bg-black"
                  type="submit"
                  value="Submit"
                  onClick={this.handleSubmit}
                  disabled={isButtonClicked}
                />
              </div>
            </div>
          </div>
        )}
      </>
    );
  }
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators(
    {
      forgotPasswordTokeValidation,
      changePassword,
    },
    dispatch
  );
};

let component = ResetPasswordClass;

export const ResetPassword = connect(null, mapDispatchToProps)(component);
