import React, { Component } from "react";
// Redux Connection
import { connect } from "react-redux";
import SimpleReactValidator from "simple-react-validator";
import { CommonPageLoader, NormalButton } from "component/common";
import { bindActionCreators } from "redux";
import "./style.scss";
import { getInvoiceById, updateInvoiceById } from "action/InvoiceAct";
import ManagementPageTitle from "component/common/ManagementPageTitle";
import { history } from "service/helpers";
import Select, { components } from "react-select";
export class ViewDetailCompClass extends Component {
  state = {
    isLoading: false,
    statusOptions: [
      {
        label: "Paid",
        value: "paid",
      },
      {
        label: "Un-paid",
        value: "unpaid",
      },
    ],
    paymentStatus: "",
    invoiceDetails: {},
  };

  //validation
  validator = new SimpleReactValidator({
    element: (message) => (
      <span className="error-message text-danger fs-14">{message}</span>
    ),
    autoForceUpdate: this,
  });

  //life cycles
  componentDidMount() {
    this._getInvoiceDetails();
  }

  //get invoice details
  _getInvoiceDetails = () => {
    let {
      params: { id = "" },
    } = this.props;
    let payload = {
      id,
    };
    this.setState({ isLoading: true });
    this.props
      .getInvoiceById(payload)
      .then((data) => {
        this.setState({
          invoiceDetails: data,
          isLoading: false,
          paymentStatus: data.paid ? "paid" : "unpaid",
        });
      })
      .catch(() => {
        this.setState({
          isLoading: false,
        });
      });
  };

  //handle input
  handleInput = ({ value }, _id) => {
    this.setState({
      paymentStatus: value,
    });
  };

  //handle page change
  handlePagination = (page) => {
    // this._getInvoiceDetails(page);
  };

  //handle Submit
  handleSubmit = () => {
    let {
      params: { id = "" },
      updateInvoiceById,
    } = this.props;
    let body = {
      id,
    };
    if (this.validator.allValid()) {
      updateInvoiceById(body).then(() => {
        this._getInvoiceDetails();
      });
    } else {
      this.validator.showMessages();
    }
  };

  render() {
    let { isLoading, statusOptions, paymentStatus, invoiceDetails } =
      this.state;
    let {
      total = "",
      paid = false,
      totalLoyalty = 0,
      created_at = "",
      products = [],
    } = invoiceDetails;
    const DropdownIndicator = (props) => {
      return (
        components.DropdownIndicator && (
          <components.DropdownIndicator {...props}>
            <div className="d-flex flex-column">
              <span className={`icon-up fs-6`}></span>
              <span className={`icon-down fs-6`}></span>
            </div>
          </components.DropdownIndicator>
        )
      );
    };
    const customStyles = {
      placeholder: (base) => ({
        ...base,
        fontSize: "1em",
        color: "#b3b3b3",
        fontWeight: 500,
      }),
      indicatorSeparator: (base) => ({
        ...base,
        display: "none",
      }),
      dropdownIndicator: (base) => ({
        ...base,
        padding: 0,
        height: 26,
        width: 26,
        color: "black",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
      }),
      singleValue: (base) => ({
        ...base,
        color: "#222222",
        fontWeight: 600,
      }),
      control: (base) => ({
        ...base,
        borderRadius: 2,
        border: "1px solid #979797 !important",
        boxShadow: "0 !important",
        outline: "0 ",
        background: "white",
        height: 40,
        minHeight: 40,
      }),
    };
    if (isLoading) return <CommonPageLoader />;
    return (
      <section className="view-invoice">
        <div className="common-table">
          <div className="d-flex justify-content-between">
            <div className="d-flex align-items-end tab-head">
              <ManagementPageTitle
                title="INVOICE MANAGEMENT"
                titleStyle="mb-0"
              />
            </div>
          </div>
          <div className="bg-white product-inner-table">
            <div className="row px-3 py-2">
              <div className="col-6 text-left my-auto">
                <p className="fs-20 text-black-22 mb-0 fw-500">
                  Invoice Payment
                </p>
              </div>
              <div className="col-6 d-flex justify-content-end pr-5 align-items-center">
                <p className="text-black-22 fs-23 mb-0">Current:&nbsp;</p>
                <span
                  className={`fs-22 fw-800 pr-5 ${
                    paid ? "text-success" : "text-red"
                  }`}
                >
                  {" "}
                  {paid ? "Paid" : "Unpaid"}{" "}
                </span>
              </div>
            </div>
            <div className="custom-divider" />
            <div className="row px-3 pt-1">
              <div className="col-6 text-left my-auto">
                <p className="fs-18 text-black-22 mb-2 fw-700">
                  #{totalLoyalty} pts
                </p>
                <p className="fs-14 text-black-24 mb-0 fw-400">
                  Payment Amount
                </p>
                <p className="fs-16 text-black-224 mb-0 fw-700">
                  $ {total} CAD
                </p>
                <p className="fs-14 text-black-24 mb-1 fw-400">{created_at}</p>
              </div>
              <div className="col-6 d-flex justify-content-end pr-5 align-items-center mt-4 pt-3 mb-2">
                <div className="row w-100">
                  <div className="col-7 ml-auto ">
                    <div>
                      <label className="mb-1 text-black-22 fw-500">
                        {"Payment Status  "}
                      </label>
                    </div>
                    <Select
                      className={"select-form-control w-100"}
                      name={"paymentStatus"}
                      options={statusOptions}
                      onChange={this.handleInput}
                      placeholder={"Select Status"}
                      isSearchable={false}
                      styles={customStyles}
                      isDisabled={paid ? true : false}
                      value={
                        !!statusOptions && statusOptions.length > 0
                          ? statusOptions.find(
                              (data) => data.value === paymentStatus
                            )
                          : null
                      }
                      components={{ DropdownIndicator }}
                    />
                  </div>
                </div>
              </div>
            </div>
            <div className="custom-divider" />
            <div className="row px-3 py-2">
              <div className="col-6 ml-auto pt-4 pb-3 mt-2 pr-5">
                <div className="d-flex btn-container-one justify-content-end">
                  <div className="">
                    <NormalButton
                      label="Cancel"
                      greyBg
                      onClick={() => history.push("/admin/invoice-management")}
                      className="mr-4"
                    />
                  </div>
                  <div className="">
                    <NormalButton
                      label="Apply"
                      mainbg
                      className="ml-3"
                      onClick={() => this.handleSubmit()}
                      disabled={
                        paymentStatus === "paid" && !paid ? false : true
                      }
                    />
                  </div>
                </div>
              </div>
            </div>
            <div className="inovice-inner-table">
              <div className="row px-3 py-2">
                <div className="col-6 text-left my-auto">
                  <p className="fs-20 text-black-22 mb-0 fw-500">
                    Invoice Detail
                  </p>
                </div>
              </div>
              <div className="row py-2">
                <div className="col-12 text-left my-auto">
                  <hr className="my-0" />
                </div>
              </div>
              <div className="row px-3 pt-2 pb-1">
                <table class="table table-borderless inovice-table">
                  <thead>
                    <tr>
                      <th>
                        <p className="d-flex justify-content-center mb-0">
                          Product Name
                        </p>
                      </th>
                      <th>
                        <p className="d-flex justify-content-center mb-0">
                          Price
                        </p>
                      </th>
                      <th>
                        <p className="d-flex justify-content-center mb-0">
                          Quantity
                        </p>
                      </th>
                      <th colSpan="4"></th>
                      <th>
                        <p className="d-flex justify-content-center mb-0">
                          Total
                        </p>
                      </th>
                    </tr>
                  </thead>
                  <tbody>
                    {products.map((list, index) => (
                      <tr key={index}>
                        <td>
                          <p className="d-flex justify-content-center mb-0">
                            {list.productName ? list.productName : "-"}
                          </p>
                        </td>
                        <td>
                          <p className="d-flex justify-content-center mb-0">
                            {list.price ? list.price : "-"}
                          </p>
                        </td>
                        <td>
                          <p className="d-flex justify-content-center mb-0">
                            {list.quantity ? list.quantity : "-"}
                          </p>
                        </td>
                        <td colSpan="4"></td>
                        <td>
                          <p className="d-flex justify-content-center mb-0">
                            {list.total ? `$ ${list.total}` : "-"}
                          </p>
                        </td>
                      </tr>
                    ))}
                    <tr>
                      <td>
                        <p className="fs-21 text-black-24 mb-0">Total:</p>
                      </td>
                      <td />
                      <td />
                      <td colSpan="4"></td>
                      <td>
                        <p className="fs-26 text-black-22 mb-0">
                          $ {total} CAD
                        </p>
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      </section>
    );
  }
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators(
    {
      getInvoiceById,
      updateInvoiceById,
    },
    dispatch
  );
};

export const ViewDetailComp = connect(
  null,
  mapDispatchToProps
)(ViewDetailCompClass);
