import React, { Component } from "react";
// Redux Connection
import { connect } from "react-redux";
import {
  TableStripedWrapper,
  CommonPageLoader,
  DeletePopup,
} from "component/common";
import { buyerListHeader } from "Data/BuyerManagement";
import { ViewBuyerPopup } from "./ViewBuyerPopup/ViewBuyerPopup";
import { getBuyerList, deleteBuyer } from "action/BuyerAct";
import { bindActionCreators } from "redux";
import { debounceFunction } from "service/debounce";
import "./style.scss";
import ManagementPageTitle from "component/common/ManagementPageTitle";
import search from "assets/svg/search.svg";
import { history } from "service/helpers";
import { phoneFormat, getShortData } from "service/helperFunctions";
export class BuyerListClass extends Component {
  state = {
    search: "",
    headerDetails: buyerListHeader,
    placeholder: "",
    adminList: {},
    isViewPopupOpen: false,
    list: [],
    pageMetaData: {},
    id: "",
    sortCriteria: "",
    sortOrder: "",
    searchValue: "",
    isDeletePopup: false,
    isPopupButtonDisabled: false,
    currPage: 1,
  };

  //handle Toggle
  handleToggle = (name, id) => {
    if (id) {
      this.setState({ id });
    } else {
      this.setState({ id: "" });
    }
    this.setState((prevState) => ({
      [name]: !prevState[name],
    }));
  };

  //go to Update Route
  gotoUpdateRoute = (_id) => {
    history.push(`/admin/buyerManagement/editBuyer/${_id}`);
  };

  //handle Search
  handleSearch = (value, index) => {
    let { headerDetails } = this.state;

    headerDetails[index].searchValue = value;

    this.setState({ headerDetails }, () => {
      debounceFunction(() => {
        this._getBuyersList(1);
      }, 500);
    });
  };

  //lifecycles
  componentDidMount() {
    this._getBuyersList(1);
  }

  //get Buyer List
  _getBuyersList = (page) => {
    let { searchValue } = this.state;
    let payload = {
      page,
      search: searchValue,
    };
    this.setState({ isLoading: true });
    this.props
      .getBuyerList(payload)
      .then((data) => {
        this.setState({
          list: data.data,
          pageMetaData: data.pageMetaData,
          isLoading: false,
          currPage: page,
        });
      })
      .catch(() => {
        this.setState({
          isLoading: false,
        });
      });
  };

  //handle Change
  handleChange = ({ target: { name, value } }) => {
    this.setState({
      [name]: value,
    });
    debounceFunction(() => this.SearchFunction(value), 500);
  };

  //Search Function
  SearchFunction = () => {
    this._getBuyersList(1);
  };

  //handle delete
  handleDelete = () => {
    let { id, currPage } = this.state;
    let body = {
      _id: id,
    };
    this.setState({ isPopupButtonDisabled: true });
    this.props
      .deleteBuyer(body)
      .then(() => {
        this.setState({ id: "", isPopupButtonDisabled: false });
        this._getBuyersList(currPage);
        this.handleToggle("isDeletePopup");
      })
      .catch(() => {
        this.setState({ isPopupButtonDisabled: false });
      });
  };

  //handle Pagination
  handlePagination = (page) => {
    this._getBuyersList(page);
  };

  //handle Filter
  handleFilter = (sortKey, sortOrder) => {
    this.setState(
      {
        sortCriteria: sortKey,
        sortOrder,
      },
      () => {
        this._getBuyersList(1);
      }
    );
  };

  render() {
    let {
      headerDetails,
      placeholder,
      list,
      id,
      isLoading,
      pageMetaData,
      isViewPopupOpen,
      searchValue,
      isPopupButtonDisabled,
      isDeletePopup,
    } = this.state;
    return (
      <>
        {isLoading ? (
          <CommonPageLoader />
        ) : (
          <div className="common-table">
            <div className="d-flex justify-content-between">
              <div className="d-flex align-items-end tab-head">
                <ManagementPageTitle
                  title="CUSTOMER MANAGEMENT"
                  titleStyle="mb-0"
                  titleDesc="Add, Edit and Delete Customers"
                  titleDescStyle="mb-0"
                />
              </div>
            </div>
            <div className="bg-white product-inner-table">
              <div className="row px-3 py-1 align-item-center">
                <div className="col-6 text-left my-auto fs-20 text-black">
                  <p className="table-head mb-0">Customer's Table</p>
                </div>
                <div className="col-6 d-flex justify-content-end text-right">
                  <div className="search-container-area">
                    <div className="position-relative">
                      <img
                        className="mailIcon"
                        src={search}
                        alt="Logo"
                        height="15px"
                      />
                      <input
                        className="search-input"
                        placeholder="Search"
                        value={searchValue}
                        name="searchValue"
                        onChange={this.handleChange}
                      />
                    </div>
                  </div>
                </div>
              </div>
              <div className="custom-divider" />
              <div className="table-wrapper pt-2">
                <TableStripedWrapper
                  headerDetails={headerDetails}
                  handlePageChange={this._getBuyersList}
                  placeHolder={placeholder}
                  pageMeta={pageMetaData}
                  search={this.state.search}
                  handleSearch={this.handleSearch}
                  queryHandler={this.handlePagination}
                  handleFilter={this.handleFilter}
                >
                  {list.length > 0 ? (
                    <>
                      {list.map((item, index) => {
                        let {
                          businessName,
                          firstName,
                          lastName,
                          accountType,
                          businessPhone,
                          status,
                          _id,
                          slNo,
                        } = item;
                        return (
                          <tr key={index} className="text-center">
                            <td className="text-black-24 fs-14">{slNo}</td>
                            <td className="text-black-24 fs-14">
                              {businessName
                                ? getShortData(businessName, 20)
                                : "-"}
                            </td>
                            <td className="text-black-24 fs-14">
                              {firstName ? getShortData(firstName, 15) : "-"}
                            </td>
                            <td className="text-black-24 fs-14">
                              {lastName ? getShortData(lastName, 15) : "-"}
                            </td>
                            <td className="text-black-24 fs-14">
                              {accountType == 1
                                ? "Master Account"
                                : "Sub Account"}
                            </td>
                            <td className="text-black-24 fs-14">
                              {businessPhone ? phoneFormat(businessPhone) : "-"}
                            </td>

                            <td
                              className={`${
                                status == 3 || status == 4 || status == 2
                                  ? "text-red"
                                  : "text-success"
                              } fs-14 fw-700 word-break-normal`}
                            >
                              {status == 1
                                ? "Active"
                                : status == 3 || status == 4 || status == 2
                                ? "Inactive"
                                : "-"}
                            </td>
                            <td>
                              <div className="d-flex justify-content-center">
                                <i
                                  className="fs-18 icon-eye pr-2 cursor-pointer fw-500"
                                  onClick={() =>
                                    this.handleToggle("isViewPopupOpen", _id)
                                  }
                                />
                                <i
                                  className="fs-18 icon-edit pr-2 cursor-pointer fw-500"
                                  onClick={() => this.gotoUpdateRoute(_id)}
                                />
                                <i
                                  className="fs-18 icon-delete-danger cursor-pointer"
                                  onClick={() =>
                                    this.handleToggle("isDeletePopup", _id)
                                  }
                                />
                              </div>
                            </td>
                          </tr>
                        );
                      })}
                    </>
                  ) : searchValue !== "" && list.length === 0 ? (
                    <tr className="h3 text-center">
                      <td colSpan="8" className="py-4 text-center">
                        {"No search result found"}
                      </td>
                    </tr>
                  ) : (
                    <tr className="h3 text-center">
                      <td colSpan="8" className="py-4 text-center">
                        {"No Record Found"}
                      </td>
                    </tr>
                  )}
                </TableStripedWrapper>
              </div>
            </div>
          </div>
        )}
        <ViewBuyerPopup
          isOpen={isViewPopupOpen}
          toggle={() => this.handleToggle("isViewPopupOpen")}
          PersonId={id}
        />
        <DeletePopup
          isOpen={isDeletePopup}
          toggle={() => this.handleToggle("isDeletePopup")}
          handleChange={() => this.handleDelete()}
          isPopupButtonDisabled={isPopupButtonDisabled}
        />
      </>
    );
  }
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators(
    {
      getBuyerList,
      deleteBuyer,
    },
    dispatch
  );
};

export const BuyerList = connect(null, mapDispatchToProps)(BuyerListClass);
