import React, { Component } from "react";
import "./style.scss";
import SimpleReactValidator from "simple-react-validator";
import { addcategory, updatecategory } from "../../../action/CategoryAct";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import { history } from "service/helpers";
import { NormalButton } from "component/common";
import ManagementPageTitle from "component/common/ManagementPageTitle";
import { NormalInput } from "component/common/NormalInput";

export class AddEditCategoryClass extends Component {
  constructor(props) {
    super(props);
    this.state = {
      name: "",
      view: "add",
      isButtonClicked: false,
    };

    this.handleChange = this.handleChange.bind(this);
  }

  //validator
  validator = new SimpleReactValidator({
    element: (message) => (
      <span className="error-message text-danger fs-14">{message}</span>
    ),
    autoForceUpdate: this,
  });

  //lifecycles
  componentDidMount = () => {
    if (this.props.compType === "edit" && history.location.state) {
      this.setState({
        ...history.location.state,
        view: this.props.compType,
      });
    }
  };

  //handle Change
  handleChange = ({ target: { name, value } }) => {
    this.setState({
      [name]: value,
    });
  };

  //handle Submit
  handleSubmit = (_id) => {
    if (this.validator.allValid()) {
      let { name } = this.state;
      let body = {
        name: name,
      };
      this.setState({
        isButtonClicked: true,
      });
      if (this.state.view === "add") {
        this.props
          .addcategory(body)
          .then(() => {
            this.setState({ isButtonClicked: false }, () =>
              history.push("/admin/categoryManagement")
            );
          })
          .catch(() => {
            this.setState({ isButtonClicked: false });
          });
      }
      if (this.state.view === "edit") {
        this.props
          .updatecategory(body, _id)
          .then(() => {
            this.setState({ isButtonClicked: false }, () =>
              history.push("/admin/categoryManagement")
            );
          })
          .catch(() => {
            this.setState({ isButtonClicked: false });
          });
      }
    } else {
      this.validator.showMessages();
    }
  };

  render() {
    let { name, view, _id, isButtonClicked } = this.state;
    return (
      <div className="outer">
        <div className="w-100 category-form">
          <ManagementPageTitle
            title="CATEGORIES MANAGEMENT"
            titleStyle="mb-0"
            titleDesc="Add and Edit Categories"
            titleDescStyle="mb-0"
          />
          <div className="category-form-box">
            <p className="table-head mb-0 px-3 py-2 fs-20">
              {view === "add" ? "Add New Category" : "Edit Category"}
            </p>
            <div className="custom-divider" />
            <div className="p-0">
              <div className="px-4 ">
                <NormalInput
                  type="text"
                  label={null}
                  onChange={this.handleChange}
                  value={name}
                  placeholder={
                    view === "edit"
                      ? "Edit Category Name"
                      : "Enter New Category Name"
                  }
                  name="name"
                />
                {this.validator.message("name", name, "required|max:50|alpha_space", {
                  className: "text-danger",
                })}
              </div>
              <div className="px-4 py-3 my-2">
                <div className="row">
                  <div className="col-12 d-flex pr-1">
                    <div className="d-flex btn-container-one">
                      <span className="mx-2">
                        <NormalButton
                          label="Cancel"
                          blackBg
                          disabled={isButtonClicked}
                          onClick={() =>
                            history.push("/admin/categoryManagement")
                          }
                        />
                      </span>
                      <span className="mx-2">
                        <NormalButton
                          label={view === "add" ? "Create" : "Apply"}
                          disabled={isButtonClicked}
                          mainbg
                          onClick={() => this.handleSubmit(_id)}
                        />
                      </span>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators(
    {
      addcategory,
      updatecategory,
    },
    dispatch
  );
};

let component = AddEditCategoryClass;

export const AddEditCategoryComp = connect(null, mapDispatchToProps)(component);
