import React, { Component } from "react";
import { connect } from "react-redux";
import { CommonPageLoader, NormalInput, NormalButton } from "component/common";
import SimpleReactValidator from "simple-react-validator";
import { bindActionCreators } from "redux";
import { AddForms } from "component/PageManagement/AddForms";
import "./style.scss";
import ManagementPageTitle from "component/common/ManagementPageTitle";
import { phoneFormat } from "service/helperFunctions";
import {
  pageManagementDetailsAdd,
  getPageManagementDetails,
} from "action/AuthAct";
export class PageManagementCompClass extends Component {
  state = {
    headers: [
      { name: "FAQ" },
      { name: "Terms & Conditions" },
      { name: "Privacy Policy" },
      { name: "Copyright" },
      { name: "Customer Support" },
    ],
    selectedHeader: 1,
    isLoading: false,
    address: "",
    phoneNumber: "",
    email: "",
    isEdit: false,
  };

  //validation
  validator = new SimpleReactValidator({
    messages: {
      email: "Invalid Email address",
    },
    element: (message) => (
      <span className="error-message text-danger fs-14">{message}</span>
    ),
    autoForceUpdate: this,
    validators: {
      email: {
        message: "must be a valid Email.",
        rule: (val, params, validator) => {
          return validator.helpers.testRegex(
            val,
            /^[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,3}$/
          );
        },
        messageReplace: (message, params) =>
          message.replace("", this.helpers.toSentence(params)),
        required: true,
      },
    },
  });

  //handle change
  handleChange = ({ target: { name, value } }) => {
    this.setState({
      [name]: value,
    });
  };

  //select headet
  selectHeader = (index) => {
    this.setState(
      {
        selectedHeader: index,
      },
      () => {
        if (index == 5) {
          this._getPageManagementDetails();
        }
      }
    );
  };

  //handle edit
  handleEdit = () => {
    let { isEdit } = this.state;
    this.setState({
      isEdit: !isEdit,
    });
  };

  //get Page Management Details
  _getPageManagementDetails = () => {
    let { getPageManagementDetails } = this.props;
    let { phoneNumber, email, address } = this.state;
    this.validator.hideMessages();
    this.setState({
      isLoading: true,
    });
    getPageManagementDetails(5)
      .then((data) => {
        if (data) {
          phoneNumber = data.phoneNo;
          email = data.email;
          address = data.address;
        }
        this.setState({
          isLoading: false,
          phoneNumber,
          email,
          address,
        });
      })
      .catch(() => {
        this.setState({
          isLoading: false,
        });
      });
  };

  //handle update
  handleupdate = () => {
    if (this.validator.allValid()) {
      let { pageManagementDetailsAdd } = this.props;
      let { address, phoneNumber, email } = this.state;
      let body = {
        phoneNo: phoneNumber,
        email: email,
        address,
      };
      this.setState({
        isButtonClicked: true,
      });
      pageManagementDetailsAdd(5, body)
        .then(() => {
          this.setState({
            isButtonClicked: false,
          });
          this.handleEdit();
          this._getPageManagementDetails();
        })
        .catch(() => {
          this.setState({
            isButtonClicked: false,
          });
        });
    } else {
      this.validator.showMessages();
    }
  };

  render() {
    let {
      isLoading,
      headers,
      selectedHeader,
      address,
      phoneNumber,
      email,
      isEdit,
    } = this.state;

    if (isLoading) return <CommonPageLoader />;
    return (
      <div className="page-management">
        <div className="d-flex justify-content-between">
          <div className="d-flex align-items-end tab-head">
            <ManagementPageTitle
              title="PAGE MANAGEMENT"
              titleStyle="mb-0"
              titleDesc="Add FAQ, Terms & Conditions, Privacy Policy and Copyright"
            />
          </div>
        </div>
        <div className="bg-white product-inner-table">
          <div className="row pt-2 pb-4 px-3 my-3">
            <div className="col-12">
              <div className="d-flex align-items-center">
                {headers.map(({ name }, index) => (
                  <span
                    key={index}
                    name={index}
                    className={` ${
                      index + 1 === selectedHeader
                        ? "header-active"
                        : "header-inactive"
                    } fs-17 text-center cursor-pointer line-height-20 fw-500`}
                    onClick={() => this.selectHeader(index + 1)}
                  >
                    {name}
                  </span>
                ))}
              </div>
            </div>
          </div>
          <div className="row px-3 mb-4">
            <div className="col-12">
              {selectedHeader != 5 ? (
                <AddForms selectedHeader={selectedHeader} />
              ) : (
                <div className="">
                  <div className="row px-4 mt-4">
                    <div className="col-6 pr-4">
                      <p className="fs-18 fw-600 text-black-22 mb-1">Email:</p>
                      {isEdit ? (
                        <>
                          <NormalInput
                            type="text"
                            onChange={this.handleChange}
                            value={email}
                            placeholder="Enter email"
                            name="email"
                            max={100}
                          />
                          {this.validator.message(
                            "email",
                            email,
                            "required|email"
                          )}
                        </>
                      ) : (
                        <p className="fs-18 fw-500 mb-0">
                          {email ? email : "-"}
                        </p>
                      )}
                    </div>
                    <div className="col-6 pl-4">
                      <p className="fs-18 fw-600 text-black-22 mb-1">
                        Phone No:
                      </p>
                      {isEdit ? (
                        <>
                          <NormalInput
                            name="phoneNumber"
                            value={phoneFormat(phoneNumber)}
                            onChange={this.handleChange}
                            placeholder="Enter Phone No."
                            max={14}
                          />
                          {this.validator.message(
                            "phoneNumber",
                            phoneNumber,
                            "required|phone|min:10"
                          )}
                        </>
                      ) : (
                        <p className="fs-18 fw-500 mb-0">
                          {phoneNumber ? phoneFormat(phoneNumber) : "-"}
                        </p>
                      )}
                    </div>
                  </div>
                  <div className="custom-divider my-4" />
                  <div className="row px-4 pt-3">
                    <div className="col-12">
                      <p className="fs-18 fw-600 text-black-22 mb-1">
                        Address:
                      </p>
                      {isEdit ? (
                        <>
                          <NormalInput
                            type="text"
                            onChange={this.handleChange}
                            value={address}
                            placeholder="Enter Address"
                            name="address"
                          />
                          {this.validator.message(
                            "address",
                            address,
                            "required|max:100"
                          )}
                        </>
                      ) : (
                        <p className="fs-18 fw-500 mb-0">{address}</p>
                      )}
                    </div>
                  </div>
                  <div className="custom-divider my-4" />
                  <div className="row px-4">
                    <div className="col-3 ml-auto page-management-button">
                      <NormalButton
                        label={isEdit ? "Update" : "Edit"}
                        mainbg={true}
                        onClick={() =>
                          isEdit ? this.handleupdate() : this.handleEdit()
                        }
                        className="ml-4 ml-auto mr-4"
                      />
                    </div>
                  </div>
                </div>
              )}
            </div>
          </div>
        </div>
      </div>
    );
  }
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators(
    {
      pageManagementDetailsAdd,
      getPageManagementDetails,
    },
    dispatch
  );
};

export const PageManagementComp = connect(
  null,
  mapDispatchToProps
)(PageManagementCompClass);
