import React, { Component } from "react";
// Redux Connection
import { connect } from "react-redux";
import {
  TableStripedWrapper,
  CommonPageLoader,
  NormalButton,
  DeletePopup,
} from "component/common";
import Select, { components } from "react-select";
import { productListHeader } from "Data/ProductManagement";
import { UpdateSingleProductPopup } from "../ProductManagement/UpdateSingleProductPopup/UpdateSingleProductPopup";
import { ViewProductPopup } from "../ProductManagement/ViewProductPopup/ViewProductPopup";
import {
  getProductsList,
  deleteProducts,
  getSellerList,
  downloadCsvProduct,
} from "action/ProductAct";
import { bindActionCreators } from "redux";
import { debounceFunction } from "service/debounce";
import { currencyFormat, getShortData } from "service/helperFunctions";
import "./style.scss";
import ManagementPageTitle from "component/common/ManagementPageTitle";
import search from "assets/svg/search.svg";
export class ProductListClass extends Component {
  state = {
    search: "",
    headerDetails: productListHeader,
    placeholder: "",
    adminList: {},
    filter: true,
    isPopupOpen: false,
    isViewPopupOpen: false,
    list: [],
    pageMetaData: {},
    id: "",
    sortCriteria: "",
    sortOrder: "",
    searchValue: "",
    sellerId: "",
    sellerOptions: "",
    searchInputData: "",
    isFileDownloading: false,
    isDeletePopup: false,
    isPopupButtonDisabled: false,
    newValue: {},
  };

  //handle Toggle
  handleToggle = (name, id) => {
    if (id) {
      this.setState({ id });
    } else {
      this.setState({ id: "" });
    }
    this.setState((prevState) => ({
      [name]: !prevState[name],
    }));
  };

  //handle Search
  handleSearch = (value, index) => {
    let { headerDetails } = this.state;

    headerDetails[index].searchValue = value;

    this.setState({ headerDetails }, () => {
      debounceFunction(() => {
        this._getProductsList(1);
      }, 500);
    });
  };

  //lifecycles
  componentDidMount() {
    this._getProductsList(1);
    this._getSellerOptions();
  }
  // get Products List
  _getProductsList = (page) => {
    let { headerDetails, sortCriteria, sortOrder, searchValue, sellerId } =
      this.state;
    let payload = {
      productName: "",
      brand: "",
      categoryId: "",
      sku: "",
      page,
      sortOrder,
      sortCriteria,
      search: searchValue,
      sellerId,
    };
    headerDetails.map((list) => {
      if (list.searchValueKey === "productName") {
        payload.productName = list.searchValue;
      } else if (list.searchValueKey === "brand") {
        payload.brand = list.searchValue;
      } else if (list.searchValueKey === "categoryId") {
        payload.categoryId = list.searchValue;
      } else if (list.searchValueKey === "sku") {
        payload.sku = list.searchValue;
      }
    });
    this.setState({ isLoading: true });
    this.props
      .getProductsList(payload)
      .then((data) => {
        this.setState({
          list: data.list,
          pageMetaData: data.pageMetaData,
          isLoading: false,
        });
      })
      .catch(() => {
        this.setState({
          isLoading: false,
        });
      });
  };

  //handle Change
  handleChange = ({ target: { name, value } }) => {
    this.setState({
      [name]: value,
    });
    debounceFunction(() => this.searchFunction(value), 500);
  };

  //search Function
  searchFunction = () => {
    this._getProductsList(1);
  };

  //handle delete
  handleDelete = () => {
    let { id } = this.state;
    let body = {
      id,
    };
    this.setState({ isPopupButtonDisabled: true });
    this.props
      .deleteProducts(body)
      .then(() => {
        this.setState({ id: "", isPopupButtonDisabled: false });
        this._getProductsList(1);
        this.handleToggle("isDeletePopup");
      })
      .catch(() => {
        this.setState({ isPopupButtonDisabled: false });
      });
  };

  //handle Pagination
  handlePagination = (page) => {
    this._getProductsList(page);
  };

  //handle Filter
  handleFilter = (sortKey, sortOrder) => {
    this.setState(
      {
        sortCriteria: sortKey,
        sortOrder,
      },
      () => {
        this._getProductsList(1);
      }
    );
  };

  //handle Input
  handleInput = (newValue) => {
    let { searchInputData } = this.state;
    if (searchInputData !== "") {
      this.setState({
        newValue,
      });
    }
    this.setState(
      {
        sellerId: newValue.value,
      },
      () => {
        this._getProductsList(1);
      }
    );
  };

  //handle input Change
  handleinputChange = (value) => {
    this.setState({
      searchInputData: value,
    });
    debounceFunction(() => this.SearchFunction(value), 500);
  };

  //Search Function
  SearchFunction = (value) => {
    this._getSellerOptions(value);
  };

  //get Seller List
  _getSellerOptions = (search = "") => {
    let body = {
      search,
    };
    let { newValue } = this.state;
    this.props.getSellerList(body).then((data) => {
      let options = [];
      data.map((list) => {
        options.push({
          value: list._id,
          label: list.firstName + list.lastName,
        });
      });

      if (newValue.value) {
        options.push(newValue);
      }
      this.setState({
        sellerOptions: options,
      });
    });
  };

  //handle Download
  handleDownload = () => {
    let { downloadCsvProduct } = this.props;
    let { sellerId } = this.state;
    this.setState({ isFileDownloading: true });
    let body = {
      sellerId: sellerId !== "" ? sellerId : null,
    };
    downloadCsvProduct(body)
      .then(({ data }) => {
        this.setState({ isFileDownloading: false });
        var a = window.document.createElement("a");
        a.href = data;
        document.body.appendChild(a);
        a.click();
        document.body.removeChild(a);
      })
      .catch(() => {
        this.setState({ isFileDownloading: false });
      });
  };

  render() {
    let {
      headerDetails,
      placeholder,
      list,
      isPopupOpen,
      id,
      isLoading,
      pageMetaData,
      isViewPopupOpen,
      searchValue,
      sellerId,
      sellerOptions,
      searchInputData,
      isFileDownloading,
      isDeletePopup,
      isPopupButtonDisabled,
    } = this.state;
    const DropdownIndicator = (props) => {
      return (
        components.DropdownIndicator && (
          <components.DropdownIndicator {...props}>
            <div className="d-flex flex-column">
              <span className={`icon-up fs-6`}></span>
              <span className={`icon-down fs-6`}></span>
            </div>
          </components.DropdownIndicator>
        )
      );
    };
    const customStyles = {
      placeholder: (base) => ({
        ...base,
        fontSize: 12,
        color: "#24243a",
        fontWeight: 600,
      }),
      indicatorSeparator: (base) => ({
        ...base,
        display: "none",
      }),
      dropdownIndicator: (base) => ({
        ...base,
        padding: 2,
        height: "18px",
        width: "18px",
        color: "black",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
      }),
      singleValue: (base) => ({
        ...base,
        color: "#24243a",
        fontWeight: 600,
        fontSize: 12,
      }),
      control: (base) => ({
        ...base,
        borderRadius: 18,
        border: "1px solid #e5e5e5",
        boxShadow: "0 !important",
        outline: "0 ",
        background: "#fcfcfc",
        height: 31,
        minHeight: 31,
      }),
      valueContainer: (base) => ({
        ...base,
        height: 31,
        padding: "0 6px 0px 10px",
      }),
      indicatorsContainer: (base) => ({
        ...base,
        paddingRight: 5,
      }),
      container: (base) => ({
        ...base,
        width: 150,
      }),
    };
    if (isLoading) return <CommonPageLoader />;
    return (
      <>
        <div className="common-table">
          <div className="d-flex justify-content-between">
            <div className="d-flex align-items-end tab-head">
              <ManagementPageTitle
                title="PRODUCT MANAGEMENT"
                titleStyle="mb-0"
                titleDesc="Add, Edit and Delete Products"
                titleDescStyle="mb-0"
              />
            </div>
          </div>
          <div className="bg-white product-inner-table">
            <div className="row px-3 py-2">
              <div className="col-6 text-left my-auto">
                <p className="fs-20 text-black-22 mb-0 fw-500">
                  Seller Name - Product Table
                </p>
              </div>
              <div className="col-6 d-flex justify-content-end text-right">
                <div className="search-container-area">
                  <div className="position-relative">
                    <img
                      className="mailIcon"
                      src={search}
                      alt="Logo"
                      height="15px"
                    />
                    <input
                      className="search-input"
                      placeholder="Search"
                      value={searchValue}
                      name="searchValue"
                      onChange={this.handleChange}
                    />
                  </div>
                </div>
              </div>
            </div>
            <div className="custom-divider" />
            <div className="row pt-4 pb-4 px-3 ">
              <div className="col-6">
                <div className="d-flex align-items-center">
                  <p className="fs-14 text-black-24 pl-2 mb-0 mr-2 fw-500">
                    Select vendor for list
                  </p>
                  <Select
                    classNamePrefix="Select"
                    name={sellerId}
                    options={sellerOptions}
                    onChange={this.handleInput}
                    placeholder={"Seller Name"}
                    styles={customStyles}
                    inputValue={searchInputData}
                    isSearchable={true}
                    onInputChange={this.handleinputChange}
                    value={
                      !!sellerOptions && sellerOptions.length > 0
                        ? sellerOptions.find((data) => data.value === sellerId)
                          ? sellerOptions.find(
                            (data) => data.value === sellerId
                          )
                          : null
                        : null
                    }
                    components={{ DropdownIndicator }}
                  />
                </div>
              </div>
            </div>
            <div className="table-wrapper">
              <TableStripedWrapper
                headerDetails={headerDetails}
                placeHolder={placeholder}
                pageMeta={pageMetaData}
                search={this.state.search}
                handleSearch={this.handleSearch}
                queryHandler={this.handlePagination}
                handleFilter={this.handleFilter}
                handlePageChange={this._getProductsList}
              >
                {list.length > 0 ? (
                  <>
                    {list.map((item, index) => {
                      let {
                        productName,
                        brand,
                        category,
                        subCategory,
                        price,
                        status,
                        _id,
                        sku,
                      } = item;

                      return (
                        <tr key={index} className="text-center">
                          <td className="text-black-24 text-center fs-14">
                            {sku ? getShortData(sku, 10) : "-"}
                          </td>
                          <td className="text-black-24 text-center fs-14">
                            {productName ? getShortData(productName, 20) : "-"}
                          </td>
                          <td className="text-black-24 text-center fs-14">
                            {price ? "$" + currencyFormat(price) : "-"}
                          </td>
                          <td>{brand ? getShortData(brand, 20) : "-"}</td>
                          <td>
                            {category ? getShortData(category.name, 20) : "-"}
                          </td>
                          <td>
                            {subCategory
                              ? getShortData(subCategory.name, 20)
                              : "-"}
                          </td>

                          <td
                            className={`${status == 0 ? "text-red" : "text-success"
                              } fs-14 fw-700 word-break-normal`}
                          >
                            {status == 1
                              ? "Active"
                              : status == 0
                                ? "Inactive"
                                : "-"}
                          </td>
                          <td>
                            <div className="pl-3">
                              <i
                                className="fs-18 icon-eye pr-3 cursor-pointer fw-500"
                                onClick={() =>
                                  this.handleToggle("isViewPopupOpen", _id)
                                }
                              />
                              <i
                                className="fs-18 icon-edit pr-3 cursor-pointer fw-500"
                                onClick={() =>
                                  this.handleToggle("isPopupOpen", _id)
                                }
                              />
                              <i
                                className="fs-18 icon-delete-danger cursor-pointer"
                                onClick={() =>
                                  this.handleToggle("isDeletePopup", _id)
                                }
                              />
                            </div>
                          </td>
                        </tr>
                      );
                    })}
                  </>
                ) : searchValue !== "" && list.length === 0 ? (
                  <tr className="h3 text-center">
                    <td colSpan="8" className="py-4 text-center">
                      {"No search result found"}
                    </td>
                  </tr>
                ) : (
                  <tr className="h3 text-center">
                    <td colSpan="8" className="py-4 text-center">
                      {"No Record Found"}
                    </td>
                  </tr>
                )}
              </TableStripedWrapper>
            </div>
            <div className="row pb-4 pt-3">
              <div className="col-3 mx-auto pb-2">
                <p className="mb-2 fs-14 text-black-24 text-center fw-500">
                  Download Product Excel
                </p>
                <NormalButton
                  label="Download as Excel"
                  mainbg={true}
                  disabled={isFileDownloading}
                  onClick={() => this.handleDownload()}
                  className="mx-auto"
                />
              </div>
            </div>
          </div>
        </div>
        <UpdateSingleProductPopup
          _getProductsList={() => this._getProductsList(1)}
          isOpenPopup={isPopupOpen}
          toggle={() => this.handleToggle("isPopupOpen")}
          productId={id}
        />
        <ViewProductPopup
          isOpen={isViewPopupOpen}
          toggle={() => this.handleToggle("isViewPopupOpen")}
          singleProductId={id}
        />
        <DeletePopup
          isOpen={isDeletePopup}
          toggle={() => this.handleToggle("isDeletePopup")}
          handleChange={() => this.handleDelete()}
          isPopupButtonDisabled={isPopupButtonDisabled}
        />
      </>
    );
  }
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators(
    {
      getProductsList,
      deleteProducts,
      getSellerList,
      downloadCsvProduct,
    },
    dispatch
  );
};

export const ProductList = connect(null, mapDispatchToProps)(ProductListClass);
