import React, { Component } from "react";
import { connect } from "react-redux";
import { CommonPageLoader } from "component/common";
import { bindActionCreators } from "redux";
import "./style.scss";
import ManagementPageTitle from "component/common/ManagementPageTitle";
import Pagination from "react-js-pagination";
import { getNotificationList, deleteNotifications } from "action/Notification";
import { history } from "service/helpers";
export class NotificationsCompClass extends Component {
  state = {
    isLoading: false,
    unReadCount: 0,
    notificationList: [],
    pageMetaData: {},
  };

  //lifecycles
  componentDidMount() {
    this._getNotificationList(1);
  }

  //get Notification List
  _getNotificationList = (page) => {
    let { getNotificationList } = this.props;
    this.setState({ isLoading: true });
    let body = {
      page,
      limit: 6,
    };
    getNotificationList(body)
      .then((data) => {
        this.setState({
          isLoading: false,
          notificationList: data.data,
          pageMetaData: data.pageMetaData,
          unReadCount: data.unReadCount,
        });
      })
      .catch(() => {
        this.setState({ isLoading: false });
      });
  };

  handleDelete = (id) => {
    let { deleteNotifications } = this.props;
    deleteNotifications(id).then(() => {
      this._getNotificationList(1);
    });
  };

  //page Change
  pageChange = (page) => {
    this._getNotificationList(page);
  };

  render() {
    let { isLoading, notificationList, unReadCount, pageMetaData } = this.state;

    if (isLoading) return <CommonPageLoader />;
    return (
      <div className="common-table">
        <div className="d-flex justify-content-between">
          <div className="d-flex align-items-end tab-head">
            <ManagementPageTitle
              title="Notifications"
              titleStyle="mb-0"
              titleDesc={unReadCount !== 0 && `${unReadCount} Unseen`}
              titleDescStyle="ml-4"
            />
          </div>
        </div>
        <div className="notification-border-area"></div>
        <div className="notification-area">
          {notificationList.length !== 0 ? (
            <>
              {notificationList.map((list) => {
                let {
                  isRead = false,
                  message = "",
                  _id,
                  isLoyalty = false,
                  isOrder = false,
                  order = {},
                } = list;
                let { orderId = "", orderRef = "", status = 2 } = order;
                return (
                  <div
                    className={`notifcation-box ${isRead ? "read" : "unread"} ${
                      isLoyalty || isOrder ? "cursor-pointer" : ""
                    }`}
                    ///A82170112556251/60dcb98cd9fb7921cf614108/Pending
                    onClick={() =>
                      isLoyalty
                        ? history.push("/admin/loyalty-points-Management")
                        : isOrder &&
                          history.push(
                            `/admin/order-management/view-order/${orderRef}/${orderId}/${
                              status == 2
                                ? "Pending"
                                : status == 3
                                ? "Accepted"
                                : status == 4
                                ? "Completed"
                                : status == 5 || status == 6 || status == 7
                                ? "Cancelled"
                                : ""
                            }`
                          )
                    }
                  >
                    <div
                      className={`d-flex justify-content-between w-100 align-items-center`}
                    >
                      <div className="">
                        <p className="mb-0 fs-18 fw-500 text-black-22">
                          {message}
                        </p>
                      </div>
                      <div className="pl-5">
                        <i
                          className="icon-Delete fs-20 fw-800 cursor-pointer"
                          onClick={(e) => {
                            e.stopPropagation();
                            this.handleDelete(_id);
                          }}
                        ></i>
                      </div>
                    </div>
                  </div>
                );
              })}
            </>
          ) : (
            <div className="d-flex justify-content-center w-100 py-5">
              <p className="fw-600 fs-18 text-black-24 mb-0">No data found</p>
            </div>
          )}
        </div>
        <div className="row mt-4">
          <div className="col-3 ml-auto">
            {pageMetaData && pageMetaData.totalPages > 1 ? (
              <Pagination
                activePage={pageMetaData.page}
                itemsCountPerPage={pageMetaData.chunk}
                totalItemsCount={pageMetaData.total}
                pageRangeDisplayed={3}
                itemClass="page-item"
                linkClass="page-link"
                innerClass="custom-pagination pagination pt-2"
                nextPageText=""
                prevPageText=""
                hideFirstLastPages={true}
                onChange={(e) => this.pageChange(e)}
              />
            ) : null}
          </div>
        </div>
      </div>
    );
  }
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators(
    { getNotificationList, deleteNotifications },
    dispatch
  );
};

export const NotificationsComp = connect(
  null,
  mapDispatchToProps
)(NotificationsCompClass);
