import React, { Component } from "react";
import SimpleReactValidator from "simple-react-validator";
import { forgetPassword } from "../../../../action/AuthAct";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import { history } from "service/helpers";
import { NormalInput } from "component/common";
import forgotPass from "assets/svg/forgot_pass.svg";
export class ForgotPasswordClass extends Component {
  state = {
    email: "",
    sent: false,
    link: "",
    isButtonClicked: false,
  };

  //validation
  validator = new SimpleReactValidator({
    messages: {
      email: "Invalid Email address",
    },
    element: (message) => (
      <span className="error-message text-danger fs-14">{message}</span>
    ),
    autoForceUpdate: this,
    validators: {
      email: {
        message: "must be a valid Email.",
        rule: (val, params, validator) => {
          return validator.helpers.testRegex(
            val,
            /^[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,3}$/
          );
        },
        messageReplace: (message, params) =>
          message.replace("", this.helpers.toSentence(params)),
        required: true,
      },
    },
  });

  //handle Change
  handleChange = ({ target: { name, value } }) => {
    this.setState({
      [name]: value,
    });
  };

  //handle Submit
  handleSubmit = (e) => {
    e.preventDefault();
    if (this.validator.allValid()) {
      this.setState({ isButtonClicked: true }, () => {
        let { email } = this.state;
        let body = {
          email: email,
        };
        this.props
          .forgetPassword(body)
          .then(() => {
            this.setState(
              {
                isButtonClicked: false,
                sent: true,
              },
              () => {
                setTimeout(() => history.push("/"), 3000);
              }
            );
          })
          .catch(() => {
            this.setState({ isButtonClicked: false });
          });
      });
    } else {
      this.validator.showMessages();
      this.forceUpdate();
    }
  };

  render() {
    const { email, sent, isButtonClicked } = this.state;
    return (
      <div className="w-100 mt-3 d-flex align-items-center flex-column">
        <label className="font-weight-bold text-black mb-4 h4">
          {"Forgot Password"}
        </label>
        {!sent ? (
          <>
            <label className="font-weight-bold mb-3 text-center text-break px-3">
              {"Enter your email id to get the reset password link"}
            </label>
            <div className="w-100">
              <NormalInput
                type="text"
                label="Email Address"
                name="email"
                value={email}
                onChange={this.handleChange}
                max={100}
              />
              {this.validator.message("email", email, "required|email")}
            </div>
            <div className="d-flex justify-content-center mt-5 pt-2">
              <input
                className="btn py-2 login-btn rounded-0 text-white-f4 bg-black"
                type="submit"
                value="Next"
                onClick={this.handleSubmit}
                disabled={isButtonClicked}
              />
            </div>
          </>
        ) : (
          <div>
            <label className="font-weight-bold mb-4 text-center text-break">
              We have sent reset link to your registered {email}
              {" email id"}
            </label>

            <div className="w-100 d-flex justify-content-center mb-4">
              <img src={forgotPass} alt="forgot" width="100" height="100" />
            </div>
            {/* <div
              className="mb-4 d-flex justify-content-center"
              onClick={this.props.toggleForgot}
            >
              {"If you didnt recevie a link!"}
              <div className="pass-login-link ml-2" onClick={this.handleLink}>
                {" Resend"}
              </div>
            </div> */}
          </div>
        )}
      </div>
    );
  }
}
const mapDispatchToProps = (dispatch) => {
  return bindActionCreators(
    {
      forgetPassword,
    },
    dispatch
  );
};

let component = ForgotPasswordClass;

export const ForgotPassword = connect(null, mapDispatchToProps)(component);
