import React, { Component } from "react";
import DatePicker from "react-datepicker";
import "./date.scss";

export class NormalDate extends Component {
  render() {
    let {
      className = "form-control w-100",
      placeholder = "DD/MM/YY",
      onChange,
      value = "",
      name,
      disabled = false,
      isReadOnly = false,
      showYearPicker = false,
      showFullMonthYearPickerStatus = false,
      showMonthYearPickerStatus = false,
      showYearDropdown = false,
      showMonthDropdown = false,
      maxdate = false,
      mindate = false,
      timeInput = false,
      startDate = "",
      endDate = "",
      label = "",
    } = this.props;

    return (
      <div className="normal-date-input">
        {label !== "" ? (
          <div className="fs-17 text-black font-Gilroy pb-2 mb-1 line-height-20">
            {label}
          </div>
        ) : null}
        {!isReadOnly ? (
          <DatePicker
            timeInputLabel={timeInput && "Time:"}
            showTimeInput={timeInput}
            className={className}
            disabled={disabled}
            placeholderText={placeholder}
            selected={value}
            showYearPicker={showYearPicker}
            showMonthYearPicker={showMonthYearPickerStatus}
            showFullMonthYearPicker={showFullMonthYearPickerStatus}
            showMonthDropdown={showMonthDropdown}
            showYearDropdown={showYearDropdown}
            dateFormat={"dd/MM/yyyy"}
            maxDate={maxdate}
            minDate={mindate}
            startDate={startDate}
            endDate={endDate}
            autoComplete="off"
            onChange={(date) => {
              let body = {};

              body = {
                target: {
                  name: name,
                  value: date,
                },
              };

              onChange(body);
            }}
            name={name}
          />
        ) : (
          <p>{value}</p>
        )}
      </div>
    );
  }
}
