import React, { Component } from "react";
import "./style.scss";
import SimpleReactValidator from "simple-react-validator";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import { history } from "service/helpers";
import { NormalButton, CommonPageLoader, NormalInput } from "component/common";
import { getBuyerById, updateBuyer } from "action/BuyerAct";
import ManagementPageTitle from "component/common/ManagementPageTitle";
import { phoneFormat, numberFormat } from "service/helperFunctions";
import { Modal, ModalBody } from "reactstrap";

export class EditBuyerClass extends Component {
  constructor(props) {
    super(props);
    this.state = {
      id: "",
      firstName: "",
      lastName: "",
      businessName: "",
      businessPhone: "",
      businessLicense: "",
      email: "",
      referralCode: "",
      status: "",
      isButtonClicked: false,
      refCountInput: "",
      isPopupOpen: false,
    };

    this.handleChange = this.handleChange.bind(this);
  }

  //validation
  validator = new SimpleReactValidator({
    messages: {
      email: "Invalid Email address",
    },
    element: (message) => (
      <span className="error-message text-danger fs-14">{message}</span>
    ),
    autoForceUpdate: this,
    validators: {
      email: {
        message: "must be a valid Email.",
        rule: (val, params, validator) => {
          return validator.helpers.testRegex(
            val,
            /^[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,3}$/
          );
        },
        messageReplace: (message, params) =>
          message.replace("", this.helpers.toSentence(params)),
        required: true,
      },
    },
  });

  //handle Change
  handleChange = ({ target: { name, value } }) => {
    this.setState({
      [name]: value,
    });
  };

  //handle Check Box
  handleCheckBox = (name, value) => {
    this.setState({ [name]: value });
  };

  //toggle
  toggle = (key) => {
    this.setState((prevState) => ({
      [key]: !prevState[key],
    }));
  };

  //handle Submit
  handleSubmit = () => {
    let location = window.location.pathname.split("/");
    if (this.validator.allValid()) {
      let {
        firstName,
        lastName,
        businessName,
        referralCode,
        businessPhone,
        businessLicense,
        status,
        refCountInput,
      } = this.state;
      let body = {
        _id: location[location.length - 1],
        firstName,
        lastName,
        businessName,
        businessPhone,
        businessLicense,
        referralCode,
        status: status == "active" ? 1 : 4,
        adminRef: {},
      };
      body.adminRef.refCount = refCountInput;

      if (refCountInput <= 7) {
        this.setState({ isButtonClicked: true });
        this.props
          .updateBuyer(body)
          .then(() => {
            history.push("/admin/buyerManagement");
          })
          .catch(() => {
            this.setState({ isButtonClicked: false });
          });
      } else {
        this.toggle("isPopupOpen");
      }
    } else {
      this.validator.showMessages();
    }
  };

  //lifecycles
  componentDidMount = () => {
    let address = window.location.pathname.split("/");
    let id = address[address.length - 1];
    this._getDetailsById(id);
  };

  //get Buyer By Id
  _getDetailsById = (id) => {
    let body = {
      id,
    };
    this.setState({ isLoading: true });
    this.props.getBuyerById(body).then((data) => {
      if (data !== null) {
        this.setState({
          firstName: data.firstName,
          accountType: data.accountType,
          lastName: data.lastName,
          email: data.email,
          businessName: data.businessName,
          businessLicense: data.businessLicense,
          businessPhone: data.businessPhone,
          referralcode: data.referralcode,
          status:
            data.status == 1
              ? "active"
              : data.status == 4 || data.status == 2 || data.status == 3
              ? "inactive"
              : "",
          isLoading: false,
          refCountInput: data.adminRef ? data.adminRef.refCount : 0,
        });
      }
    });
  };

  render() {
    let {
      firstName,
      lastName,
      referralCode,
      businessName,
      businessPhone,
      businessLicense,
      email,
      status,
      isLoading,
      isButtonClicked,
      refCountInput,
      isPopupOpen,
    } = this.state;

    if (isLoading) return <CommonPageLoader />;

    return (
      <div className="outer">
        <div className="w-100 buyer-form">
          <ManagementPageTitle
            title="CUSTOMER MANAGEMENT"
            titleStyle="mb-0"
            titleDesc="Add, Edit and Delete Customers"
            titleDescStyle="mb-0"
          />
          <div className="buyer-form-box">
            <p className="table-head mb-0 px-3 py-2">Edit Customer</p>
            <div className="custom-divider" />
            <div className="p-0">
              <div className="px-4">
                <NormalInput
                  label="First Name *"
                  type="text"
                  onChange={this.handleChange}
                  value={firstName}
                  placeholder="Enter First Name"
                  name="firstName"
                />
                {this.validator.message(
                  "firstName",
                  firstName,
                  "required|max:30|alpha_space",
                  {
                    className: "text-danger",
                  }
                )}
                <NormalInput
                  label="Last Name *"
                  type="text"
                  onChange={this.handleChange}
                  value={lastName}
                  placeholder="Enter Last Name"
                  name="lastName"
                />
                {this.validator.message(
                  "lastName",
                  lastName,
                  "required|max:30|alpha_space",
                  {
                    className: "text-danger",
                  }
                )}
                <NormalInput
                  label="Email ID"
                  type="text"
                  onChange={this.handleChange}
                  value={email}
                  disabled
                  placeholder="Enter Email"
                  name="email"
                  max={100}
                />
                {this.validator.message("email", email, "required|email", {
                  className: "text-danger",
                })}
                <NormalInput
                  label="Business Name *"
                  type="text"
                  onChange={this.handleChange}
                  value={businessName}
                  placeholder="Enter Business Name "
                  name="businessName"
                />
                {this.validator.message(
                  "businessName",
                  businessName,
                  "required",
                  {
                    className: "text-danger",
                  }
                )}
                <div className="row">
                  <div className="col-6">
                    <NormalInput
                      label="Corporation License Number(9 Digits) *"
                      max={9}
                      onChange={this.handleChange}
                      value={numberFormat(businessLicense)}
                      placeholder="Enter Corporation License Number"
                      name="businessLicense"
                    />
                    {this.validator.message(
                      "corportion license",
                      businessLicense,
                      "required|numeric|min:9|max:9",
                      {
                        className: "text-danger",
                      }
                    )}
                  </div>
                  <div className="col-6">
                    <NormalInput
                      label="Business Phone Number *"
                      name="businessPhone"
                      placeholder="Enter Business Phone Number"
                      value={phoneFormat(businessPhone)}
                      onChange={this.handleChange}
                      max={14}
                    />
                    {this.validator.message(
                      "businessPhone",
                      businessPhone,
                      "required|phone|min:10",
                      {
                        className: "text-danger",
                      }
                    )}
                  </div>
                </div>
                <div className="row">
                  <div className="col-6">
                    <NormalInput
                      label="Referral count"
                      max={2}
                      onChange={this.handleChange}
                      value={numberFormat(refCountInput)}
                      placeholder="Enter Referral count"
                      name="refCountInput"
                    />
                  </div>
                </div>
                <NormalInput
                  label="Referral Code (Optional) "
                  type="text"
                  onChange={this.handleChange}
                  value={referralCode}
                  placeholder="Enter Referral Code"
                  name="referralCode"
                />
              </div>
              <div className="custom-divider mt-5 mb-2 px-0 mx-0" />
              <div className="px-4 py-3">
                <div className="row">
                  <div className="col-12 d-flex pr-1 justify-content-between">
                    <div className="d-flex flex-column">
                      <div className="d-flex">
                        <div className="custom-control custom-checkbox mt-2">
                          <label className="position-checkbox">
                            <input
                              type="checkbox"
                              name="status"
                              value="active"
                              checked={status === "active" ? true : false}
                              onChange={(e) =>
                                this.handleCheckBox(
                                  e.target.name,
                                  e.target.value
                                )
                              }
                            />
                            <span className="checkbox-tick ">
                              <i className="icon-tick-transparent "></i>
                            </span>
                            <span className="label-txt fs-16">Active</span>
                          </label>
                        </div>
                        <div className="custom-control custom-checkbox mt-2">
                          <label className="position-checkbox">
                            <input
                              type="checkbox"
                              name="status"
                              value="inactive"
                              checked={status === "inactive" ? true : false}
                              onChange={(e) =>
                                this.handleCheckBox(
                                  e.target.name,
                                  e.target.value
                                )
                              }
                            />
                            <span className="checkbox-tick ">
                              <i className="icon-tick-transparent "></i>
                            </span>
                            <span className="label-txt fs-16">Inactive</span>
                          </label>
                        </div>
                      </div>
                      <div className="">
                        {this.validator.message("status", status, "required")}
                      </div>
                    </div>
                    <div className="d-flex btn-container-one">
                      <div>
                        <NormalButton
                          label="Cancel"
                          blackBg
                          className="mr-4"
                          onClick={() => history.push("/admin/buyerManagement")}
                        />
                      </div>
                      <div>
                        <NormalButton
                          label="Update"
                          mainbg
                          className="ml-3"
                          onClick={() => this.handleSubmit()}
                          disabled={isButtonClicked}
                        />
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <Modal
          isOpen={isPopupOpen}
          className={"modal-dialog-centered error-popup"}
          size="md"
        >
          <ModalBody>
            <h4 className="text-black fw-600 fs-18 text-center">
              You cannot exceed the limit of 7 referrals per user.
            </h4>
            <div className="d-flex justify-content-end mt-5 pt-3 w-100">
              <div className="mx-auto d-flex">
                <NormalButton
                  label="Got it"
                  mainbg={true}
                  onClick={() => this.toggle("isPopupOpen")}
                />
              </div>
            </div>
          </ModalBody>
        </Modal>
      </div>
    );
  }
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators(
    {
      updateBuyer,
      getBuyerById,
    },
    dispatch
  );
};

let component = EditBuyerClass;

export const EditBuyerComp = connect(null, mapDispatchToProps)(component);
