import React, { Component } from "react";
import SimpleReactValidator from "simple-react-validator";
// Redux Connection
import ManagementPageTitle from "component/common/ManagementPageTitle";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import { history } from "service/helpers";
import {
  downloadMultipleProduct,
  uploadMultipleProduct,
} from "action/ProductAct";
import "./style.scss";
import { NormalButton } from "component/common";
import { getShortData } from "service/helperFunctions";
import multiple_upload from "assets/images/multiple_upload.png";
import xlsxIcon from "assets/images/xlsx_icon.png";
export class AddMultipleProductClass extends Component {
  state = {
    formFields: {
      fileName: "",
      fileUrl: "",
    },
    isDataUploading: false,
    isFileDownloading: false,
  };

  //validation
  validator = new SimpleReactValidator({
    element: (message) => (
      <span className="error-message text-danger fs-14">{message}</span>
    ),
    autoForceUpdate: this,
  });

  //handle input
  handleInput = ({ target: { name, value } }) => {
    let { formFields } = this.state;
    formFields[name] = value;

    this.setState({
      formFields,
    });
  };

  //upload File
  uploadFile = (e) => {
    let file = e.target.files[0];
    if (file !== undefined) {
      this.setState({
        formFields: {
          fileName: file.name,
          fileUrl: file,
        },
      });
    }
  };

  //handle Delete Image
  handleDeleteImage = () => {
    this.setState({
      formFields: {
        fileName: "",
        fileUrl: "",
      },
    });
  };

  // handle Submit
  handleSubmit = () => {
    if (this.validator.allValid()) {
      this.setState({ isDataUploading: true }, () => {
        let { formFields } = this.state;
        let { fileUrl } = formFields;
        const formData = new FormData();
        formData.append("file", fileUrl);
        this.props
          .uploadMultipleProduct(formData)
          .then(() => {
            history.push("/admin/product-management");
            this.setState({ isDataUploading: false });
          })
          .catch(() => {
            this.setState({ isDataUploading: false });
          });
      });
    } else {
      this.validator.showMessages();
    }
  };

  //handle Excel Download
  handleExcelDownload = () => {
    let { downloadMultipleProduct } = this.props;
    this.setState({ isFileDownloading: true });
    downloadMultipleProduct()
      .then(({ data }) => {
        this.setState({ isFileDownloading: false });
        var a = window.document.createElement("a");
        a.href = data;
        document.body.appendChild(a);
        a.click();
        document.body.removeChild(a);
      })
      .catch(() => {
        this.setState({ isFileDownloading: false });
      });
  };

  render() {
    let { formFields, isDataUploading, isFileDownloading } = this.state;
    let { fileName, fileUrl } = formFields;
    return (
      <section>
        <div className="d-flex justify-content-between">
          <div className="d-flex align-items-end tab-head">
            <ManagementPageTitle
              title="PRODUCT MANAGEMENT"
              titleStyle="mb-0"
              titleDesc="Add, Edit and Delete roducts"
              titleDescStyle="mb-0"
            />
          </div>
        </div>
        <div className="add-multiple-product">
          <h1 className="fs-30 text-black-22 fw-400 text-uppercase mb-0">
            Creat Mutiple <br />
            Products
          </h1>

          <div className="row mt-5 align-items-end pb-4">
            <div className="col-8 pl-md-5 pt-3">
              <h1 className="fs-26 text-black-22 fw-800 mb-5 text-uppercase">
                Download the EXCEL template, add your products and upload it,
                just that easy!
              </h1>
              <div className="d-flex justify-content-end align-items-center">
                <div>
                  <NormalButton
                    label="DOWNLOAD"
                    mainbg={true}
                    onClick={() => this.handleExcelDownload()}
                    disabled={isFileDownloading}
                  />
                </div>
              </div>
            </div>
            <div className="col-4">
              <img
                alt="upload"
                className="w-100"
                height="225"
                src={multiple_upload}
              />
            </div>
          </div>
        </div>
        <div className="row mt-4">
          <div className="col-5 ml-auto">
            <div className="multiple-upload-product">
              <p className="text-black-22 fs-28 text-uppercase mb-1 text-right mb-2 pb-1 fw-400">
                Upload file
              </p>
              <div className="d-flex justify-content-end align-items-end pb-2">
                <div className="text-right pr-3">
                  <p className="text-black-22 fs-18 mb-0 text-uppercase">
                    File Name
                  </p>
                  <p className="text-black-22 fs-16 fw-500 text-underline mb-1">
                    {getShortData(fileName, 20)}
                  </p>
                </div>
                <div className="upload-image-card">
                  {fileUrl === "" ? (
                    <div className="image-card btn-file-input">
                      <button>
                        {"+"}
                        <input
                          type="file"
                          className=""
                          accept=".xlsx,.xls"
                          onChange={this.uploadFile}
                        ></input>
                      </button>
                    </div>
                  ) : (
                    <div className="image-card position-relative">
                      <img
                        src={xlsxIcon}
                        alt={fileName}
                        className="w-100 h-100"
                      />
                      <div className="overlay"></div>
                      <div
                        className="image-delete-area"
                        onClick={() => this.handleDeleteImage()}
                      >
                        <i className="icon-Delete fs-16 text-white cursor-pointer"></i>
                      </div>
                    </div>
                  )}
                </div>
              </div>
            </div>
            {this.validator.message("fileUrl", fileUrl, "required")}
          </div>
        </div>
        <div className="d-flex justify-content-end my-4 py-3 w-100">
          <div className="ml-auto d-flex">
            <NormalButton
              label="Cancel"
              blackBg={true}
              disabled={isDataUploading}
              onClick={() => history.push("/admin/product-management")}
              className="mr-4"
            />
            <NormalButton
              label="Save New Products"
              mainbg={true}
              disabled={isDataUploading}
              onClick={() => this.handleSubmit()}
              className="ml-4"
            />
          </div>
        </div>
      </section>
    );
  }
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators(
    {
      downloadMultipleProduct,
      uploadMultipleProduct,
    },
    dispatch
  );
};

export const AddMultipleProduct = connect(
  null,
  mapDispatchToProps
)(AddMultipleProductClass);
