import React, { Component } from "react";
// Redux Connection
import { connect } from "react-redux";
import { TableStripedWrapper, CommonPageLoader } from "component/common";
import {
  InvoiceManagementHeader,
  selectOptionsList,
} from "Data/InvoiceManagement";
import { getInvoiceList, invoiceStatusChange } from "action/InvoiceAct";
import { bindActionCreators } from "redux";
import { debounceFunction } from "service/debounce";
import Select, { components } from "react-select";
import { getShortData } from "service/helperFunctions";
import "./style.scss";
import ManagementPageTitle from "component/common/ManagementPageTitle";
import search from "assets/svg/search.svg";
export class InvoiceManagementListUpdateClass extends Component {
  state = {
    searchValue: "",
    headerDetails: InvoiceManagementHeader,
    list: [],
    pageMetaData: {},
    selectOptions: selectOptionsList,
    selectedName: "",
  };

  //lifecycles
  componentDidMount() {
    this._getInvoiceList(1);
  }

  //get Invoice List
  _getInvoiceList = (page) => {
    let { searchValue } = this.state;
    let payload = {
      page,
      search: searchValue,
    };
    this.setState({ isLoading: true });
    this.props
      .getInvoiceList(payload)
      .then((data) => {
        this.setState({
          list: data.list,
          pageMetaData: data.pageMetaData,
          isLoading: false,
        });
      })
      .catch(() => {
        this.setState({
          isLoading: false,
        });
      });
  };

  //handle Change
  handleChange = ({ target: { value } }) => {
    this.setState({
      searchValue: value,
    });
    debounceFunction(() => this.searchFunction(), 500);
  };

  //search Function
  searchFunction = () => {
    this._getInvoiceList(1);
  };

  //handle Input
  handleInput = (newValue, _id) => {
    this.setState({
      selectedName: newValue.value,
    });
    let body = {
      _id,
      status:
        newValue.value == "activate" ? 2 : newValue.value == "rejected" ? 0 : 3,
    };
    this.setState({ isLoading: true });
    this.props
      .invoiceStatusChange(body)
      .then(() => {
        this._getInvoiceList(1);
        this.setState({ selectedName: "" });
      })
      .catch(() => {
        this.setState({ isLoading: false });
      });
  };

  //handle Pagination
  handlePagination = (page) => {
    this._getInvoiceList(page);
  };

  render() {
    let {
      headerDetails,
      list,
      isLoading,
      pageMetaData,
      searchValue,
      selectOptions,
      selectedName,
    } = this.state;
    const DropdownIndicator = (props) => {
      return (
        components.DropdownIndicator && (
          <components.DropdownIndicator {...props}>
            <div className="d-flex flex-column">
              <span className={`icon-up fs-6`}></span>
              <span className={`icon-down fs-6`}></span>
            </div>
          </components.DropdownIndicator>
        )
      );
    };
    const customStyles = {
      placeholder: (base) => ({
        ...base,
        fontSize: 12,
        color: "#24243a",
        fontWeight: 600,
      }),
      indicatorSeparator: (base) => ({
        ...base,
        display: "none",
      }),
      dropdownIndicator: (base) => ({
        ...base,
        padding: 2,
        height: "18px",
        width: "18px",
        color: "black",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
      }),
      singleValue: (base) => ({
        ...base,
        color: "#24243a",
        fontWeight: 600,
        fontSize: 12,
      }),
      control: (base) => ({
        ...base,
        borderRadius: 18,
        border: "1px solid #e5e5e5",
        boxShadow: "0 !important",
        outline: 0,
        background: "#fcfcfc",
        height: 31,
        minHeight: 31,
      }),
      valueContainer: (base) => ({
        ...base,
        height: 31,
        padding: "0 6px 0px 10px",
      }),
      indicatorsContainer: (base) => ({
        ...base,
        paddingRight: 5,
      }),
      container: (base) => ({
        ...base,
        width: 120,
      }),
      option: (base, { isFocused }) => ({
        ...base,
        backgroundColor: isFocused ? "#fff" : "#d8d8d8",
        color: isFocused ? "#24243a" : "24243a",
        lineHeight: 1.93,
        fontSize: 14,
        padding: "2px 10px",
        textAlign: "left",
      }),
      menu: (base) => ({
        ...base,
        borderTopLeftRadius: 0,
        borderTopRightRadius: 0,
        borderBottomLeftRadius: 4,
        borderBottomRightRadius: 4,
      }),
      menuList: (base) => ({
        ...base,
        padding: 0,
      }),
    };
    if (isLoading) return <CommonPageLoader />;
    return (
      <div className="common-table">
        <div className="d-flex justify-content-between">
          <div className="d-flex align-items-end tab-head">
            <ManagementPageTitle title="INVOICE MANAGEMENT" titleStyle="mb-0" />
          </div>
        </div>
        <div className="bg-white product-inner-table">
          <div className="row px-3 py-2">
            <div className="col-6 text-left my-auto">
              <p className="fs-20 text-black-22 mb-0 fw-500">
                Update Request/Status
              </p>
            </div>
            <div className="col-6 d-flex justify-content-end text-right">
              <div className="search-container-area">
                <div className="position-relative">
                  <img
                    className="mailIcon"
                    src={search}
                    alt="Logo"
                    height="15px"
                  />
                  <input
                    className="search-input"
                    placeholder="Search"
                    value={searchValue}
                    name="searchValue"
                    onChange={this.handleChange}
                  />
                </div>
              </div>
            </div>
          </div>
          <div className="custom-divider" />
          <div className="table-wrapper ">
            <TableStripedWrapper
              headerDetails={headerDetails}
              pageMeta={pageMetaData}
              placeHolder={""}
              search={this.state.search}
              queryHandler={this.handlePagination}
              handlePageChange={this._getInvoiceList}
              isInvoiceManagement={true}
            >
              {list.length !== 0 ? (
                <>
                  {list.map((item, index) => {
                    let { userDetails = {}, status, _id } = item;

                    let { accountType, businessName = "" } = userDetails;

                    return (
                      <tr key={index} className="text-center">
                        <td className="text-black-24 text-center fs-14">
                          {index + 1}
                        </td>
                        <td className="text-black-24 text-center fs-14">
                          {businessName !== ""
                            ? getShortData(businessName, 30)
                            : "-"}
                        </td>
                        <td className="text-black-24 text-center fs-14">
                          {accountType && accountType == 1
                            ? "Master Account"
                            : accountType == 2
                            ? "Sub Account"
                            : "-"}
                        </td>
                        <td
                          className={`${
                            status == 0 || status == 3
                              ? "text-red"
                              : status == 2
                              ? "text-success"
                              : "text-yellow"
                          } fs-14 fw-700 word-break-normal`}
                        >
                          {status == 2
                            ? "Active"
                            : status == 3
                            ? "Inactive"
                            : status == 0
                            ? "Rejected"
                            : status == 1
                            ? "Request"
                            : "-"}
                        </td>
                        <td className="text-black-24 text-center fs-14 ">
                          <div className="d-flex justify-content-center">
                            <Select
                              classNamePrefix="Select"
                              name={selectedName}
                              options={
                                status == 2
                                  ? selectOptions.filter(
                                      (s) =>
                                        s.value != "activate" &&
                                        s.value != "rejected"
                                    )
                                  : status == 0
                                  ? selectOptions.filter(
                                      (s) => s.value != "rejected"
                                    )
                                  : status == 3
                                  ? selectOptions.filter(
                                      (s) => s.value != "deactivate"
                                    )
                                  : selectOptions
                              }
                              onChange={(e) => this.handleInput(e, _id)}
                              placeholder={"Actions"}
                              styles={customStyles}
                              isSearchable={false}
                              value={
                                !!selectOptions && selectOptions.length > 0
                                  ? selectOptions.find(
                                      (data) => data.value === selectedName
                                    )
                                    ? selectOptions.find(
                                        (data) => data.value === selectedName
                                      )
                                    : null
                                  : null
                              }
                              components={{ DropdownIndicator }}
                            />
                          </div>
                        </td>
                      </tr>
                    );
                  })}
                </>
              ) : searchValue !== "" && list.length === 0 ? (
                <tr className="h3 text-center">
                  <td colSpan="8" className="py-4 text-center">
                    {"No search result found"}
                  </td>
                </tr>
              ) : (
                <tr className="h3 text-center">
                  <td colSpan="8" className="py-4 text-center">
                    {"No Record Found"}
                  </td>
                </tr>
              )}
            </TableStripedWrapper>
          </div>
        </div>
      </div>
    );
  }
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators(
    {
      getInvoiceList,
      invoiceStatusChange,
    },
    dispatch
  );
};

export const InvoiceManagementListUpdate = connect(
  null,
  mapDispatchToProps
)(InvoiceManagementListUpdateClass);
