import React, { Component } from "react";
// Redux Connection
import { connect } from "react-redux";
import { CommonPageLoader, NormalInput, NormalButton } from "component/common";
import { bindActionCreators } from "redux";
import "./style.scss";
import ManagementPageTitle from "component/common/ManagementPageTitle";
import {
  getLoyaltyDetailsById,
  getUserDetailsById,
  getUserLoyaltyPointsById,
  getLoyaltyPointsHistoryById,
  addLoyaltyPoints,
  getRedeemListById,
  redeemAccept,
} from "action/LoyaltyPointsAct";
import { phoneFormat } from "service/helperFunctions";
import Pagination from "react-js-pagination";
import InfiniteScroll from "react-infinite-scroll-component";
import { Toast } from "service/toast";
import { ConfirmationPopup } from "./ConfirmationPopup";
export class EditLoyaltyPointsClass extends Component {
  state = {
    loyaltyRequestList: [],
    loyaltyPointsHistory: [],
    loyaltyHistoryPageMetaData: {},
    redeemPoints: "",
    addPoints: "",
    isLoading: false,
    userDetails: {},
    loyaltyPoints: 0,
    hasMore: true,
    isOpenPopup: false,
    isRedeemPopup: false,
    redeemId: "",
  };

  //life cycles
  componentDidMount() {
    this._getUserDetailsById();
    this._getUserLoyaltyPointsById();
    this._getLoyaltyPointsHistoryById(1);
    this._getRedeemListById(1);
  }

  //page Change
  pageChange = (page) => {
    this._getRedeemListById(page);
  };

  //get User Details By Id
  _getUserDetailsById = () => {
    let { id } = this.props.params;
    let payload = {
      id,
    };
    this.setState({ isLoading: true });
    this.props
      .getUserDetailsById(payload)
      .then((data) => {
        this.setState({
          userDetails: data,
          isLoading: false,
        });
      })
      .catch(() => {
        this.setState({ isLoading: false });
      });
  };

  //get User Loyalty Points By Id
  _getUserLoyaltyPointsById = () => {
    let { id } = this.props.params;
    let payload = {
      id,
    };
    this.setState({ isLoading: true });
    this.props
      .getUserLoyaltyPointsById(payload)
      .then((data) => {
        this.setState({
          loyaltyPoints: data.loyaltyPoints,
          isLoading: false,
        });
      })
      .catch(() => {
        this.setState({ isLoading: false });
      });
  };

  //get Loyalty Points History By Id
  _getLoyaltyPointsHistoryById = (page) => {
    let { id } = this.props.params;
    let payload = {
      id,
      page,
    };

    if (this.state.loyaltyPointsHistory.length == 0)
      this.setState({ isLoading: true });
    this.props
      .getLoyaltyPointsHistoryById(payload)
      .then((response) => {
        this.setState({
          loyaltyPointsHistory: [
            ...this.state.loyaltyPointsHistory,
            ...response.data,
          ],
          loyaltyHistoryPageMetaData: response.pageMetaData,
          isLoading: false,
        });
        if (
          this.state.loyaltyPointsHistory.length >=
          this.state.loyaltyHistoryPageMetaData.total
        ) {
          this.setState({ hasMore: false });
        }
      })
      .catch(() => {
        this.setState({ isLoading: false, hasMore: false });
      });
  };

  //get Redeem List By Id
  _getRedeemListById = (page) => {
    let { id } = this.props.params;
    let payload = {
      id,
      page,
    };
    this.setState({ isLoading: true });
    this.props
      .getRedeemListById(payload)
      .then((data) => {
        this.setState({
          loyaltyRequestList: data.list,
          pageMetaData: data.pageMetaData,
          isLoading: false,
        });
      })
      .catch(() => {
        this.setState({ isLoading: false });
      });
  };

  //handle Change
  handleChange = ({ target: { name, value } }) => {
    this.setState({
      [name]: value,
    });
  };

  //handle Submit
  handleSubmit = () => {
    let { redeemId } = this.state;
    let payload = {
      id: redeemId,
    };
    this.setState({ isPopupButtonDisabled: true });
    this.props
      .redeemAccept(payload)
      .then(() => {
        this.setState({ isPopupButtonDisabled: false });
        this._getRedeemListById(1);
        this.handleToggle("isRedeemPopup");
      })
      .catch(() => {
        this.setState({ isPopupButtonDisabled: false });
      });
  };

  //handle Add Loyalty Points
  handleAddLoyaltyPoints = () => {
    let { id: _id } = this.props.params;
    let { addPoints } = this.state;
    let body = {
      points: addPoints,
    };
    this.setState({ isPopupButtonDisabled: true });
    this.props
      .addLoyaltyPoints(body, _id)
      .then(() => {
        this.setState({
          isPopupButtonDisabled: false,
          addPoints: "",
          loyaltyPointsHistory: [],
          hasMore: true,
          isOpenPopup: false,
        });
        this._getUserLoyaltyPointsById();
        this._getLoyaltyPointsHistoryById(1);
        // this.handleToggle("isOpenPopup");
      })
      .catch(() => {
        this.setState({ isPopupButtonDisabled: false });
      });
  };

  //fetch Data
  fetchData = () => {
    let { loyaltyHistoryPageMetaData, loyaltyPointsHistory } = this.state;
    if (loyaltyPointsHistory.length >= loyaltyHistoryPageMetaData.total) {
      this.setState({ hasMore: false });
      return;
    }
    this._getLoyaltyPointsHistoryById(loyaltyHistoryPageMetaData.page + 1);
  };

  //handle Toggle
  handleToggle = (name, id) => {
    let { addPoints, isOpenPopup } = this.state;
    if (name === "isOpenPopup") {
      if (addPoints !== "") {
        this.setState((prevState) => ({
          [name]: !prevState[name],
        }));
      } else {
        if (!isOpenPopup) {
          Toast({
            type: "error",
            message: "Please enter the value",
            time: 1000,
          });
        }
      }
    } else {
      this.setState((prevState) => ({
        [name]: !prevState[name],
      }));
    }
    if (id) {
      this.setState({ redeemId: id });
    } else {
      this.setState({ redeemId: "" });
    }
  };

  render() {
    let {
      isLoading,
      addPoints,
      userDetails,
      loyaltyPoints,
      loyaltyPointsHistory,
      loyaltyRequestList,
      pageMetaData,
      hasMore,
      isOpenPopup,
      isPopupButtonDisabled,
      isRedeemPopup,
    } = this.state;

    const {
      firstName = "",
      lastName = "",
      email = "",
      businessPhone = "",
      businessLicense = "",
      businessName = "",
      accountType,
    } = userDetails;
    if (isLoading) return <CommonPageLoader />;
    return (
      <>
        <div className="common-table loyalty">
          <div className="d-flex justify-content-between">
            <div className="d-flex align-items-end tab-head">
              <ManagementPageTitle
                title="LOYALTY POINTS MANAGEMENT"
                titleStyle="mb-0"
              />
            </div>
          </div>
          <div className="bg-white product-inner-table">
            <div className="row px-3 py-2">
              <div className="col-6 text-left my-auto">
                <p className="fs-20 text-black-22 mb-0 fw-500">
                  {businessName ? businessName : "-"}
                </p>
              </div>
            </div>
            <div className="custom-divider" />
            <div className="row px-3">
              <div className="col-6">
                <NormalInput
                  label="Name: "
                  value={firstName + " " + lastName}
                  readOnly={true}
                  ismarginZero={true}
                />
              </div>
              <div className="col-6">
                <NormalInput
                  label="Email ID: "
                  value={email}
                  readOnly={true}
                  ismarginZero={true}
                />
              </div>
            </div>
            <div className="row px-3">
              <div className="col-6">
                <NormalInput
                  label="Account type* "
                  value={
                    accountType && accountType == 1
                      ? "Master Account"
                      : accountType == 2
                      ? "Sub Account"
                      : "-"
                  }
                  readOnly={true}
                />
              </div>
              <div className="col-6">
                <NormalInput
                  label="Business Loyality Points* "
                  value={loyaltyPoints}
                  readOnly={true}
                />
              </div>
            </div>
            <div className="row px-3 pb-3">
              <div className="col-6">
                <NormalInput
                  label="Corporation License Number (9 digits)* "
                  value={businessLicense}
                  readOnly={true}
                />
              </div>
              <div className="col-6">
                <NormalInput
                  label="Business Phone Number* "
                  value={businessPhone ? phoneFormat(businessPhone) : "-"}
                  readOnly={true}
                />
              </div>
            </div>
            <div className="loyalty-inner-table">
              <div className="row px-3 py-2">
                <div className="col-6 text-left my-auto">
                  <p className="fs-22 text-black-22 mb-0 fw-500">
                    Loyality Points: {loyaltyPoints}
                  </p>
                </div>
              </div>
              <div className="custom-divider" />
              <div className="px-3 text-center loyalty-inputs">
                <div className="row px-3 d-flex align-items-center loyalty-inputs">
                  <div className="col-7 my-3 d-flex align-items-center">
                    <span className="fw-500 pr-3">Add Points</span>
                    <NormalInput
                      value={addPoints}
                      name="addPoints"
                      type="number"
                      placeholder="Enter Amount"
                      onChange={this.handleChange}
                    />
                  </div>
                  <div className="col-2 pr-0 ">
                    <div className="d-flex btn-container-one justify-content-end">
                      <NormalButton
                        label="Apply"
                        mainbg
                        className="ml-auto"
                        onClick={() => this.handleToggle("isOpenPopup")}
                      />
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div className="loyalty-inner-table">
              <div className="row px-3 py-2">
                <div className="col-6 text-left my-auto">
                  <p className="fs-22 text-black-22 mb-0 fw-500">
                    Redeem Request
                  </p>
                </div>
              </div>
              <div className="custom-divider" />
              <div className="px-3 text-center loyalty-inputs">
                {loyaltyRequestList ? (
                  loyaltyRequestList.length != 0 ? (
                    loyaltyRequestList.map((item, index) => {
                      return (
                        <div
                          className="row d-flex align-items-center px-3 mt-3"
                          key={index}
                        >
                          <div className="col-7 mb-2 pb-4">
                            <p className="fs-16 text-black-22 text-left mb-1">
                              <b>Requested points:</b> {item.requestedPoints}{" "}
                              {" pts"}
                            </p>
                            <p className="fs-16 text-black-22 text-left mb-0">
                              <b>Requested Date:</b> {item.created_at}
                            </p>
                          </div>
                          <div className="col-2 pr-0 mt-2">
                            <div className="d-flex btn-container-one align-items-center">
                              <NormalButton
                                label="Apply"
                                mainbg
                                className="ml-auto"
                                onClick={() =>
                                  this.handleToggle("isRedeemPopup", item._id)
                                }
                              />
                            </div>
                          </div>
                          <div className="col-3 pl-0 mt-2">
                            {pageMetaData && pageMetaData.totalPages > 1 ? (
                              <Pagination
                                activePage={pageMetaData.page}
                                itemsCountPerPage={pageMetaData.chunk}
                                totalItemsCount={pageMetaData.total}
                                pageRangeDisplayed={3}
                                itemClass="page-item"
                                linkClass="page-link"
                                innerClass="custom-pagination pagination pt-3 pb-0"
                                nextPageText=""
                                prevPageText=""
                                hideFirstLastPages={true}
                                onChange={(e) => this.pageChange(e)}
                              />
                            ) : null}
                          </div>
                        </div>
                      );
                    })
                  ) : (
                    <div className="my-3">
                      <span className="h6">No Record Found</span>
                    </div>
                  )
                ) : (
                  <div className="my-3">
                    <span className="h6">No Record Found</span>
                  </div>
                )}
              </div>
            </div>
            <div className="loyalty-inner-table">
              <div className="row px-3 pt-2 pb-1">
                <div className="col-6 text-left my-auto">
                  <p className="fs-22 text-black-22 mb-0 fw-500">
                    Loyalty Points History
                  </p>
                </div>
              </div>
              <div className="custom-divider" />
              <div className="row px-3 pt-2 pb-3">
                <InfiniteScroll
                  dataLength={loyaltyPointsHistory.length}
                  height={150}
                  next={this.fetchData}
                  hasMore={hasMore}
                  loader={<h6 className="text-center">Loading...</h6>}
                  className="scrollbar w-100"
                >
                  <table className="table table-borderless loyalty-table">
                    <thead>
                      <tr>
                        <th>Actions</th>
                        <th>Date</th>
                        <th>Points</th>
                        <th>Transferred By</th>
                        <th>Transferred To</th>
                        <th colSpan="3"></th>
                      </tr>
                    </thead>
                    <tbody>
                      {loyaltyPointsHistory.length !== 0 ? (
                        <>
                          {loyaltyPointsHistory.map((item, index) => {
                            let {
                              type,
                              loyaltyPoints,
                              transferredBy,
                              created_at,
                              transferredTo,
                            } = item;
                            return (
                              <tr key={index}>
                                <td>{type == 1 ? "Transfer" : "Redeem"}</td>
                                <td>{created_at}</td>
                                <td>{loyaltyPoints}</td>
                                <td>{transferredBy ? transferredBy : "-"}</td>
                                <td>{transferredTo ? transferredTo : "-"}</td>
                              </tr>
                            );
                          })}
                        </>
                      ) : (
                        <tr className="fs-14 fw-500 mx-auto">
                          <td colSpan="8" className="py-4 text-center">
                            {"No Record Found"}
                          </td>
                        </tr>
                      )}
                    </tbody>
                  </table>
                </InfiniteScroll>
              </div>
            </div>
          </div>
        </div>
        <ConfirmationPopup
          isOpen={isOpenPopup}
          toggle={() => this.handleToggle("isOpenPopup")}
          handleChange={() => this.handleAddLoyaltyPoints()}
          isPopupButtonDisabled={isPopupButtonDisabled}
          title="Are you sure you want to add loyalty points ?"
        />
        <ConfirmationPopup
          isOpen={isRedeemPopup}
          toggle={() => this.handleToggle("isRedeemPopup")}
          handleChange={() => this.handleSubmit()}
          isPopupButtonDisabled={isPopupButtonDisabled}
          title="Are you sure you want to redeem the loyalty points ?"
        />
      </>
    );
  }
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators(
    {
      getLoyaltyDetailsById,
      getUserDetailsById,
      getUserLoyaltyPointsById,
      getLoyaltyPointsHistoryById,
      addLoyaltyPoints,
      getRedeemListById,
      redeemAccept,
    },
    dispatch
  );
};

export const EditLoyaltyPoints = connect(
  null,
  mapDispatchToProps
)(EditLoyaltyPointsClass);
