import React, { Component } from "react";
// import { Pagination } from '../index';
import Pagination from "react-js-pagination";
import PropTypes from "prop-types";
import { NormalButton } from "component/common";
import "./table.scss";

export class TableStripedWrapper extends Component {
  state = {
    currentSortKeyDetails: null,
  };

  getPlaceHolder = () => {
    let { placeHolder = "No data found" } = this.props;
    //   if (typeof placeHolder === 'function') {
    //     return placeHolder();
    //   }

    return <p className="text-center">{placeHolder}</p>;
  };

  handleChange = (e, index) => {
    this.props.handleSearch(e.target.value, index);
  };

  pageChange = (e) => {
    this.props.handlePageChange(e);
  };

  render() {
    let {
      headerDetails,
      children,
      pageMeta,
      isEmpty = false,
      className = "",
      overFlow = true,
      handleFilter,
      tbodyClass,
      isInvoiceManagement = false,
    } = this.props;
    return (
      <div className="maintableStriped table-container">
        <div
          className={`maintable-content ${
            overFlow ? "table-responsive" : ""
          } ${className}`}
        >
          <table
            className={`table table-striped ${
              isInvoiceManagement ? "mb-5" : ""
            } ${className}`}
          >
            <thead>
              <tr>
                {headerDetails.map(
                  (
                    {
                      label,
                      className = "",
                      divClass = "",
                      sortKey = "",
                      isSearchNeeded = false,
                      searchValue = "",
                      element,
                      isSort = false,
                      placeHolder = "",
                      classNameForInput = "",
                      isSpaceBetween = false,
                      isCenter = false,
                      isButtonRequired = false,
                      buttonDesc = "",
                      onClickAction = {},
                    },
                    index
                  ) => {
                    return (
                      <th className={className} key={index}>
                        {!isButtonRequired ? (
                          <div
                            className={`d-flex align-items-center ${
                              isSpaceBetween
                                ? "justify-content-between"
                                : isCenter
                                ? "justify-content-center"
                                : "justify-content-start"
                            }  text-center fs-16 ${divClass}`}
                          >
                            {label}
                            {element && element()}
                            {isSort ? (
                              <div
                                className={`d-flex table-filter align-items-center flex-column ml-2 `}
                              >
                                <span
                                  className={`icon-up cursor-pointer fs-9`}
                                  onClick={() => handleFilter(sortKey, -1)}
                                />
                                <span
                                  className={`icon-down cursor-pointer fs-9`}
                                  onClick={() => handleFilter(sortKey, 1)}
                                />
                              </div>
                            ) : null}
                          </div>
                        ) : null}
                        {isSearchNeeded ? (
                          <div className="pt-1">
                            <input
                              type={"text"}
                              className={`form-control ${classNameForInput}`}
                              name="password"
                              value={searchValue}
                              onChange={(e) => this.handleChange(e, index)}
                              placeholder={placeHolder}
                            />
                          </div>
                        ) : null}
                        {isButtonRequired ? (
                          <div
                            className={`d-flex align-items-center justify-content-end table-button text-center fs-16 ${divClass}`}
                          >
                            <NormalButton
                              label={buttonDesc}
                              mainbg
                              onClick={() => onClickAction()}
                              className="ml-auto"
                            />
                          </div>
                        ) : null}
                      </th>
                    );
                  }
                )}
              </tr>
            </thead>
            <tbody className={`tbody-class ${tbodyClass}`}>{children}</tbody>
          </table>
          {!isEmpty ? this.getPlaceHolder() : ""}
        </div>
        {pageMeta && pageMeta.totalPages > 1 ? (
          <Pagination
            activePage={pageMeta.page}
            itemsCountPerPage={pageMeta.chunk}
            totalItemsCount={pageMeta.total}
            pageRangeDisplayed={3}
            itemClass="page-item"
            linkClass="page-link"
            innerClass="custom-pagination pagination pt-2"
            nextPageText=""
            prevPageText=""
            hideFirstLastPages={true}
            onChange={(e) => this.pageChange(e)}
          />
        ) : null}
      </div>
    );
  }
}

TableStripedWrapper.propTypes = {
  placeHolder: PropTypes.oneOfType([PropTypes.string, PropTypes.func]),
  isEmpty: PropTypes.bool,
  headerDetails: PropTypes.array.isRequired,
  pageMeta: PropTypes.object,
};
