import React, { Component } from "react";
// Redux Connection
import { connect } from "react-redux";

import { LoginComp } from "../../component/auth/login/index";
import { ForgotPassword } from "component/auth/login/forgot_password";
import "../../component/auth/button.scss";

export class LoginClass extends Component {
  state = {
    otp_login: true,
    forgot_pass: false,
  };

  toggleState = (val1, val2) => {
    this.setState({
      otp_login: val1,
      forgot_pass: val2,
    });
  };

  render() {
    let { otp_login, forgot_pass } = this.state;

    return (
      <>
        <form>
          <div className="shadow-lg rounded mt-1 p-3 d-flex align-items-center flex-column">
            {otp_login && (
              <LoginComp
                toggleOtpAuth={() => this.toggleState(true, false)}
                toggleForgot={() => this.toggleState(false, true)}
              />
            )}

            {forgot_pass && <ForgotPassword />}
          </div>
        </form>
        <div>
          {!forgot_pass ? (
            <p
              className="mb-0 mt-4 fs-16 font-weight-bold text-center cursor-pointer forget-password pt-3"
              onClick={() => this.toggleState(false, true)}
            >
              FORGOT PASSWORD
            </p>
          ) : (
            <p
              className="mb-0 mt-4 fs-16 font-weight-bold text-center cursor-pointer forget-password pt-3"
              onClick={() => this.toggleState(true, false)}
            >
              Back to login
            </p>
          )}
        </div>
      </>
    );
  }
}

export const Login = connect(null, null)(LoginClass);
