import React, { Component } from 'react';
import './select.scss';
export class NormalSelect extends Component {
  render() {
    let {
      className = 'form-control select-form-control w-100',
      options = [],
      keyName = 'label',
      valueName = 'value',
      handleChange,
      value = '',
      name = '',
      placeholder = 'Select',
      disabled = false,
      arrow = false,
      isReadOnly = false,
      label = '',
      requried = false,
    } = this.props;
    return (
      <>
        {!isReadOnly ? (
          <div className={`${arrow ? 'select-section' : 'Select'}`}>
            {label !== '' ? (
              <div>
                <label className='mb-1'>{label}</label>
                {requried ? <span>*</span> : null}
              </div>
            ) : null}
            <select
              className={className}
              value={value}
              disabled={disabled}
              onChange={(e) => {
                let body = {
                  target: {
                    name: e.target.name,
                    value: e.target.value,
                  },
                };
                handleChange(body);
              }}
              name={name}
            >
              <option value='' disabled defaultValue>
                {placeholder}
              </option>
              {options.length !== 0 ? (
                options.map((option, index) => (
                  <option value={option[valueName]} key={index}>
                    {option[keyName]}
                  </option>
                ))
              ) : (
                <option value='' disabled>
                  No Options
                </option>
              )}
            </select>
            {arrow && <span className='icon-arrow-down down-icon'></span>}
          </div>
        ) : (
          <p>{value}</p>
        )}
      </>
    );
  }
}
