import React, { Component } from "react";
import { Navbar } from "component/common";
import Sidebar from "component/common/Sidebar";
import "assets/scss/layouts/AdminLayout.scss";
import "react-perfect-scrollbar/dist/css/styles.css";
import "react-datepicker/dist/react-datepicker.css";
import "react-datepicker/dist/react-datepicker-cssmodules.css";

export class AdminLayout extends Component {
  state = {
    profilemenu: false,
    menuOpenClass: false,
    authToken: "",
  };

  //handle Profile Menu
  handleProfileMenu = (event, active) => {
    event.stopPropagation();
    this.setState({
      profilemenu: active,
    });
  };

  //handle Side nav
  handleSidenav = () => {
    let { menuOpenClass } = this.state;
    this.setState({
      menuOpenClass: !menuOpenClass,
    });
  };

  render() {
    let { children, history } = this.props;

    let { menuOpenClass } = this.state;
    return (
      <div id="main-content" onClick={(e) => this.handleProfileMenu(e, false)}>
        <div className="content-wrapper">
          <Sidebar
            menuOpen={menuOpenClass}
            handleSidenav={this.handleSidenav}
            location={history.location}
          />
          <Navbar location={history.location} />
          <div className="content">{children}</div>
        </div>
      </div>
    );
  }
}
