import { categoryManagement } from "../service/apiVariables";
import { addQuery } from "service/helperFunctions";

//add category
export const addcategory = (body) => (dispatch, getState, { api, Toast }) => {
  return new Promise((resolve, reject) => {
    api({
      ...categoryManagement.addCategory,
      body,
    })
      .then(({ data, message }) => {
        resolve(data);
        Toast({ type: "success", message, time: 5000 });
      })
      .catch(({ message }) => {
        reject(Toast({ type: "error", message }));
      });
  });
};

//list Category
export const listCategory = (query) => (dispatch, getState, { api, Toast }) => {
  return new Promise((resolve, reject) => {
    addQuery(query, categoryManagement.listCategory);
    api({
      ...categoryManagement.listCategory,
    })
      .then(({ data }) => {
        resolve(data);
      })
      .catch(({ message }) => {
        reject(Toast({ type: "error", message }));
      });
  });
};

//update category
export const updatecategory = (body, _id) => (
  dispatch,
  getState,
  { api, Toast }
) => {
  let finalObj = {
    ...categoryManagement.updateCategory,
    api: categoryManagement.updateCategory.api + "/" + _id,
  };
  return new Promise((resolve, reject) => {
    api({
      ...finalObj,
      body,
    })
      .then(({ data, message }) => {
        resolve(data);
        Toast({ type: "success", message, time: 5000 });
      })
      .catch(({ message }) => {
        reject(Toast({ type: "error", message }));
      });
  });
};

