import React, { Component } from "react";
import { connect } from "react-redux";
import { TableStripedWrapper, CommonPageLoader } from "component/common";
import Select, { components } from "react-select";
import { orderListHeader } from "Data/OrderManagement";
import { getSellerList } from "action/ProductAct";
import { getOrdersList } from "action/OrderAct";
import { bindActionCreators } from "redux";
import { debounceFunction } from "service/debounce";
import "./style.scss";
import ManagementPageTitle from "component/common/ManagementPageTitle";
import search from "assets/svg/search.svg";
import { history } from "service/helpers";
import { getShortData } from "service/helperFunctions";

export class OrderListClass extends Component {
  state = {
    search: "",
    headerDetails: orderListHeader,
    list: [],
    pageMetaData: {},
    searchValue: "",
    sellerId: "",
    sellerOptions: "",
    searchInputData: "",
    headers: [
      { name: "Pending", status: 2 },
      { name: "Accepted", status: 3 },
      { name: "Cancelled", status: 5 },
      { name: "Completed", status: 4 },
    ],
    newValue: {},
    selectedHeader: 0,
  };

  //go to View
  gotoView = (_id, num, tabName) => {
    history.push(`order-management/view-order/${_id}/${num}/${tabName}`);
  };

  //lifecycles
  componentDidMount() {
    let body = {
      search: "",
      sellerId: "",
      page: 1,
    };
    this._getOrderList(2, body);
    this._getSellerOptions();
  }

  //get Orders List
  _getOrderList = (status, body) => {
    this.setState({ isLoading: true });
    let query = {
      page: body.page,
      search: body.search,
      sellerId: body.sellerId,
    };
    this.props
      .getOrdersList(status, query)
      .then((data) => {
        this.setState({
          list: data.list,
          pageMetaData: data.pageMetaData,
          isLoading: false,
        });
      })
      .catch(() => {
        this.setState({
          isLoading: false,
        });
      });
  };

  //handle Change
  handleChange = ({ target: { name, value } }) => {
    this.setState({
      [name]: value,
    });
    debounceFunction(() => this.searchOrder(value), 500);
  };

  //search Order
  searchOrder = (value) => {
    const { headers, selectedHeader, sellerId } = this.state;
    let body = {
      search: value,
      sellerId: sellerId,
      page: 1,
    };
    this._getOrderList(headers[selectedHeader].status, body);
  };

  //handle Input
  handleInput = (newValue) => {
    const { headers, selectedHeader, searchValue } = this.state;
    let body = {
      search: searchValue,
      sellerId: newValue.value,
      page: 1,
    };
    let { searchInputData } = this.state;
    if (searchInputData !== "") {
      this.setState({
        newValue,
      });
    }
    this.setState(
      {
        sellerId: newValue.value,
      },
      () => {
        this._getOrderList(headers[selectedHeader].status, body);
      }
    );
  };

  //handle input Change
  handleinputChange = (value) => {
    this.setState({
      searchInputData: value,
    });
    debounceFunction(() => this.SearchSeller(value), 500);
  };

  //Search Seller
  SearchSeller = (value) => {
    this._getSellerOptions(value);
  };

  // get Seller List
  _getSellerOptions = (search = "") => {
    let body = {
      search,
    };
    let { newValue } = this.state;
    this.props.getSellerList(body).then((data) => {
      let options = [];
      data.map((list) => {
        options.push({
          value: list._id,
          label: list.firstName + list.lastName,
        });
      });
      if (newValue.value) {
        options.push(newValue);
      }
      this.setState({
        sellerOptions: options,
      });
    });
  };

  //select Header
  selectHeader = (idx) => {
    const { headers, searchValue, sellerId } = this.state;
    let body = {
      search: searchValue,
      sellerId: sellerId,
      page: 1,
    };
    this.setState(
      {
        selectedHeader: idx,
      },
      () => this._getOrderList(headers[idx].status, body)
    );
  };

  //handle Page Change
  handlePageChange = (page) => {
    const { headers, selectedHeader, searchValue, sellerId } = this.state;
    let body = {
      search: searchValue,
      sellerId: sellerId,
      page,
    };
    this._getOrderList(headers[selectedHeader].status, body);
  };

  render() {
    let {
      headerDetails,
      list,
      isLoading,
      pageMetaData,
      searchValue,
      sellerId,
      sellerOptions,
      searchInputData,
      headers,
      selectedHeader,
    } = this.state;
    const DropdownIndicator = (props) => {
      return (
        components.DropdownIndicator && (
          <components.DropdownIndicator {...props}>
            <div className="d-flex flex-column">
              <span className={`icon-up fs-6`}></span>
              <span className={`icon-down fs-6`}></span>
            </div>
          </components.DropdownIndicator>
        )
      );
    };
    const customStyles = {
      placeholder: (base) => ({
        ...base,
        fontSize: 12,
        color: "#24243a",
        fontWeight: 600,
      }),
      indicatorSeparator: (base) => ({
        ...base,
        display: "none",
      }),
      dropdownIndicator: (base) => ({
        ...base,
        padding: 2,
        height: "18px",
        width: "18px",
        color: "black",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
      }),
      singleValue: (base) => ({
        ...base,
        color: "#24243a",
        fontWeight: 600,
        fontSize: 12,
      }),
      control: (base) => ({
        ...base,
        borderRadius: 18,
        border: "1px solid #e5e5e5",
        boxShadow: "0 !important",
        outline: "0 ",
        background: "#fcfcfc",
        height: 31,
        minHeight: 31,
      }),
      valueContainer: (base) => ({
        ...base,
        height: 31,
        padding: "0 6px 0px 10px",
      }),
      indicatorsContainer: (base) => ({
        ...base,
        paddingRight: 5,
      }),
      container: (base) => ({
        ...base,
        width: 150,
      }),
    };

    return (
      <>
        {isLoading ? (
          <CommonPageLoader />
        ) : (
          <div className="common-table">
            <div className="d-flex justify-content-between">
              <div className="d-flex align-items-end tab-head">
                <ManagementPageTitle
                  title="ORDER MANAGEMENT"
                  titleStyle="mb-0"
                />
              </div>
            </div>
            <div className="bg-white product-inner-table">
              <div className="row px-3 py-2">
                <div className="col-6 text-left my-auto">
                  <p className="fs-20 text-black-22 mb-0 fw-500">
                    {headers[selectedHeader].name} Orders Table
                  </p>
                </div>
                <div className="col-6 d-flex justify-content-end text-right">
                  <div className="search-container-area">
                    <div className="position-relative">
                      <img
                        className="mailIcon"
                        src={search}
                        alt="Logo"
                        height="15px"
                      />
                      <input
                        className="search-input"
                        placeholder="Search"
                        value={searchValue}
                        name="searchValue"
                        onChange={this.handleChange}
                      />
                    </div>
                  </div>
                </div>
              </div>
              <div className="custom-divider" />
              <div className="row pt-2 pb-4 px-3 ">
                <div className="col-12">
                  <div className="d-flex align-items-center">
                    <p className="fs-14 text-black-24 pl-2 mb-0 pr-3 fw-500">
                      Select Seller for list
                    </p>
                    <Select
                      classNamePrefix="Select"
                      name={sellerId}
                      options={sellerOptions}
                      onChange={this.handleInput}
                      placeholder={"Seller Name"}
                      styles={customStyles}
                      inputValue={searchInputData}
                      isSearchable={true}
                      onInputChange={this.handleinputChange}
                      value={
                        !!sellerOptions && sellerOptions.length > 0
                          ? sellerOptions.find(
                              (data) => data.value === sellerId
                            )
                            ? sellerOptions.find(
                                (data) => data.value === sellerId
                              )
                            : null
                          : null
                      }
                      components={{ DropdownIndicator }}
                    />
                    {headers.map(({ name }, idx) => (
                      <span
                        key={idx}
                        name={idx}
                        className={
                          idx === selectedHeader
                            ? "header-active fs-18 text-center cursor-pointer"
                            : "header-inactive fs-18 text-center cursor-pointer"
                        }
                        onClick={() => this.selectHeader(idx)}
                      >
                        {name}
                      </span>
                    ))}
                  </div>
                </div>
              </div>
              <div className="table-wrapper">
                <TableStripedWrapper
                  headerDetails={headerDetails}
                  placeHolder={""}
                  pageMeta={pageMetaData}
                  handlePageChange={this.handlePageChange}
                >
                  {list.length > 0 ? (
                    <>
                      {list.map((item, index) => {
                        let {
                          _id,
                          created_at,
                          buyer,
                          seller,
                          orderRef,
                          status,
                          cardType,
                        } = item;

                        return (
                          <tr key={index} className="text-center">
                            <td className="text-black-22 fs-14">
                              {orderRef ? getShortData(orderRef, 20) : "-"}
                            </td>
                            <td className="fs-14">
                              {created_at ? created_at : "-"}
                            </td>
                            <td className="text-black-24 fs-14">
                              {buyer
                                ? `${getShortData(
                                    buyer.firstName,
                                    20
                                  )} ${getShortData(buyer.lastName, 20)}`
                                : "-"}
                            </td>
                            <td className="text-black-24 fs-14">
                              {seller
                                ? `${getShortData(
                                    seller.firstName,
                                    20
                                  )} ${getShortData(seller.lastName, 20)}`
                                : "-"}
                            </td>
                            <td className={`text-black-24 fs-14`}>
                              {cardType}
                            </td>
                            <td>
                              <div
                                className="d-flex text-black-22 justify-content-center"
                                onClick={() =>
                                  this.gotoView(
                                    orderRef,
                                    _id,
                                    status == 2
                                      ? "Pending"
                                      : status == 3
                                      ? "Accepted"
                                      : status == 4
                                      ? "Completed"
                                      : "Cancelled"
                                  )
                                }
                              >
                                <i className="fs-18 icon-eye pr-2 cursor-pointer fw-500" />
                              </div>
                            </td>
                          </tr>
                        );
                      })}
                    </>
                  ) : searchValue !== "" && list.length === 0 ? (
                    <tr className="h3 text-center">
                      <td colSpan="8" className="py-4 text-center">
                        {"No search result found"}
                      </td>
                    </tr>
                  ) : (
                    <tr className="h3 text-center">
                      <td colSpan="8" className="py-4 text-center">
                        {`There are no ${headers[
                          selectedHeader
                        ].name.toLowerCase()} orders at this moment`}
                      </td>
                    </tr>
                  )}
                </TableStripedWrapper>
              </div>
            </div>
          </div>
        )}
      </>
    );
  }
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators(
    {
      getOrdersList,
      getSellerList,
    },
    dispatch
  );
};

export const OrderList = connect(null, mapDispatchToProps)(OrderListClass);
