import { addQuery } from "service/helperFunctions";
import { subcategoryManagement } from "../service/apiVariables";

//add subcategory
export const addsubcategory = (body) => (
  dispatch,
  getState,
  { api, Toast }
) => {
  return new Promise((resolve, reject) => {
    api({
      ...subcategoryManagement.addSubcategory,
      body,
    })
      .then(({ data, message }) => {
        resolve(data);
        Toast({ type: "success", message, time: 5000 });
      })
      .catch(({ message }) => {
        reject(Toast({ type: "error", message }));
      });
  });
};

// update subcategory
export const updatesubcategory = (body, _id) => (
  dispatch,
  getState,
  { api, Toast }
) => {
  let finalObj = {
    ...subcategoryManagement.updateSubcategory,
    api: subcategoryManagement.updateSubcategory.api + "/" + _id,
  };
  return new Promise((resolve, reject) => {
    api({
      ...finalObj,
      body,
    })
      .then(({ data, message }) => {
        resolve(data);
        Toast({ type: "success", message, time: 5000 });
      })
      .catch(({ message }) => {
        reject(Toast({ type: "error", message }));
      });
  });
};

//list SubCategory
export const listSubCategory = (query) => (
  dispatch,
  getState,
  { api, Toast }
) => {
  return new Promise((resolve, reject) => {
    addQuery(query, subcategoryManagement.listSubCategory);
    api({
      ...subcategoryManagement.listSubCategory,
    })
      .then(({ data }) => {
        resolve(data);
      })
      .catch(({ message }) => {
        reject(Toast({ type: "error", message }));
      });
  });
};

