import React, { Component } from "react";
import { NavLink } from "react-router-dom";
import PerfectScrollbar from "react-perfect-scrollbar";
import "./sidebar.scss";
import logo from "../../../assets/svg/logo.svg";
import { history } from "service/helpers";
export default class Sidebar extends Component {
  state = {
    navLinks: [
      {
        to: "/admin/buyerManagement",
        label: "Customer Management",
        children: [
          {
            to: "/admin/buyerManagement/addBuyer",
            label: "Add New Customer",
            iconName: "add-single-products",
          },
        ],
      },
      {
        to: "/admin/sellerManagement",
        label: "Seller management",
        children: [
          {
            to: "/admin/sellerManagement/addSeller",
            label: "Add New Seller",
            iconName: "add-single-products",
          },
        ],
      },
      {
        to: "/admin/product-management",
        label: "Product Management",
        children: [
          {
            to: "/admin/product-management/add-single-products",
            label: "Add New Product",
            iconName: "add-single-products",
          },
          {
            to: "/admin/product-management/add-multiple-products",
            label: "Upload Multiple Products",
            iconName: "add-multiple-products",
          },
        ],
      },
      {
        to: "/admin/categoryManagement",
        label: "Categories Management",
        children: [
          {
            to: "/admin/categoryManagement/addCategory",
            label: "Add New Category",
            iconName: "add-single-products",
          },
          {
            to: "/admin/categoryManagement/viewCategory",
            label: "View Category",
            iconName: "add-single-products",
          },
        ],
      },
      {
        to: "/admin/subcategoryManagement",
        label: "Sub-Category Management",
        children: [
          {
            to: "/admin/subcategoryManagement/addSubcategory",
            label: "Add New Sub-Category",
            iconName: "add-single-products",
          },
          {
            to: "/admin/subcategoryManagement/viewSubcategory",
            label: "View Sub-Category",
            iconName: "add-single-products",
          },
        ],
      },
      {
        to: "/admin/order-management",
        label: "Order Management",
        children: [],
      },
      {
        to: "/admin/loyalty-points-Management",
        label: "Loyalty Points Management",
      },
      {
        to: "/admin/invoice-management",
        label: "Invoice Management",
        children: [
          {
            to: "/admin/invoiceManagement/update",
            label: "Update Request/ Status",
            iconName: "add-single-products",
          },
        ],
      },
      {
        to: "/admin/page-management",
        label: "Page Management",
      },
      {
        to: "/admin/referals-management",
        label: "Referrals Management",
      },
      {
        to: "/admin/report/salesReport",
        label: "Report",
        children: [
          {
            to: "/admin/report/salesReport",
            label: "Sales Report",
            iconName: "add-single-products",
          },
          {
            to: "/admin/report/referralsReport",
            label: "Referrals Report",
            iconName: "add-single-products",
          },
          {
            to: "/admin/report/invoiceReport",
            label: "Invoice Report",
            iconName: "add-single-products",
          },
        ],
      },
    ],
    selectedMenu: 0,
  };

  //lifecycles
  componentDidMount = () => {
    let { navLinks } = this.state;
    let { location } = this.props;
    navLinks.map((list, index) => {
      if (list.to.includes(location.pathname)) {
        this.setState({
          selectedMenu: index,
        });
      }
      list.children &&
        list.children.map((data) => {
          if (data.to.includes(location.pathname)) {
            this.setState({
              selectedMenu: index,
            });
          }
        });
    });
  };

  //select Menu
  selectMenu = (index) => {
    this.setState({
      selectedMenu: index,
    });
  };

  render() {
    let { selectedMenu } = this.state;
    let { menuOpen, handleSidenav } = this.props;
    let navLocation = history.location.pathname.split("/");
    let navLinks = this.state.navLinks;
    let ordersChildren;
    if (navLocation[navLocation.length - 4] === "view-order") {
      let id = navLocation[navLocation.length - 3];
      let orderNo = navLocation[navLocation.length - 2];
      let orderType = navLocation[navLocation.length - 1];
      ordersChildren = [
        {
          to: `/admin/order-management/view-order/${id}/${orderNo}/${orderType}`,
          label: `Order: #${id}`,
        },
      ];
      let withChild = { ...navLinks[5], children: ordersChildren };
      navLinks[5] = { ...withChild };
    } else {
      navLinks = this.state.navLinks;
    }

    return (
      <>
        <div className={`left-menu bg-site-primary  ${menuOpen ? "open" : ""}`}>
          <div className="sidebar-logo">
            <img
              src={logo}
              alt="logo"
              className="cursor-pointer"
              onClick={() => history.push("/admin/buyerManagement")}
            />
          </div>
          <PerfectScrollbar>
            <ul>
              {navLinks.map(({ to, label, children }, index) => (
                <li key={index}>
                  <NavLink
                    to={to}
                    className={`nav-link py-2`}
                    onClick={() => this.selectMenu(index)}
                  >
                    <div className="sidebar-menu">
                      <span className="oval"></span>
                      <span className="sidebar-menu-desc">{label}</span>
                    </div>
                  </NavLink>
                  <div>
                    <div className="my-1">
                      {selectedMenu === index
                        ? children
                          ? children.map(
                              ({ to, label, iconName }, subindex) => {
                                return (
                                  <li key={subindex}>
                                    <NavLink
                                      to={to}
                                      className={`nav-sub-link sub-menu py-2`}
                                    >
                                      <div className="sidebar-menu">
                                        <i
                                          className={`icon-${iconName} fs-20`}
                                        />
                                        <span className="sidebar-menu-desc pl-2">
                                          {label}
                                        </span>
                                      </div>
                                    </NavLink>
                                  </li>
                                );
                              }
                            )
                          : null
                        : null}
                    </div>
                  </div>
                </li>
              ))}
            </ul>
          </PerfectScrollbar>
        </div>
        <div
          className={`sidemenu-overlay ${menuOpen ? "open" : ""}`}
          onClick={() => handleSidenav()}
        ></div>
      </>
    );
  }
}
