import React, { Component } from "react";
// Redux Connection

import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import { getProductById } from "action/ProductAct";
import { CommonPageLoader } from "component/common";
import { Modal, ModalBody } from "reactstrap";
import "./style.scss";

export class ModifyProductPopupClass extends Component {
  state = {
    products: [],
    isLoading: false,
  };

  //get Orders Product List
  _getOrderProductsList = (id) => {
    this.setState({ isLoading: true });
    this.props
      .getOrdersProductList(id)
      .then((data) => {
        if (data !== null) {
          this.setState({
            products: data,
            isLoading: false,
          });
        }
      })
      .catch(() => {
        this.setState({
          isLoading: false,
        });
      });
  };

  //life cycles
  componentDidUpdate(prevProps) {
    if (prevProps !== this.props) {
      if (this.props.singleProductId !== "" && this.props.isOpen) {
        this._getOrderProductsList(this.props.sellerId);
      }
    }
  }

  //add Item
  addItem = (body) => {
    let tempArray = this.props.products;
    let obj = null;
    obj = tempArray.find((key) => key._id === body._id);
    if (obj === null || obj === undefined) {
      this.props.addProductToList({
        ...body,
        quantity: body.quantity ? body.quantity : 1,
      });
    } else {
      let selectedIndex = "";
      tempArray.map((list, index) => {
        if (list._id === body._id) {
          selectedIndex = index;
        }
      });
      if (tempArray[selectedIndex]) {
        tempArray[selectedIndex].quantity =
          tempArray[selectedIndex].quantity + 1;
        this.props.addProductToList([...tempArray]);
      }
    }
    this.props.toggle();
  };

  render() {
    let { products, isLoading } = this.state;

    let {
      className = "modal-dialog-centered view-product-popup",
      isOpen = true,
      toggle,
    } = this.props;

    return (
      <Modal isOpen={isOpen} toggle={toggle} className={className} size="lg">
        <ModalBody>
          <div className="d-flex justify-content-between w-100 align-items-center mb-4 pb-3">
            <h1 className="fs-30 text-black-22 fw-400 mb-0 text-uppercase">
              Add Product
            </h1>
            <i
              className="fs-22 icon-close-round cursor-pointer"
              onClick={() => toggle()}
            />
          </div>
          <div className="outer-area">
            {!isLoading ? (
              <>
                <div className="head-collapse font-weight-bold fs-12 w-100">
                  Select Items{" "}
                  <div className="d-inline-block float-right">
                    <span className="icon-arrow-down fs-26"></span>
                  </div>
                </div>

                {products.length !== 0 ? (
                  products.map((data, index) => (
                    <div
                      className="head-collapse font-weight-bold fs-12 w-100"
                      key={index}
                    >
                      Item no # {data.sku} -{" "}
                      {data.productName ? data.productName : "-"}
                      <div className="d-inline-block float-right">
                        <span
                          className="cursor-pointer icon-plus fs-16 pr-1"
                          onClick={() => this.addItem(data)}
                        ></span>
                      </div>
                    </div>
                  ))
                ) : (
                  <p className="py-5 text-center fw-600 fs-20 text-black-22">
                    No data found !!!
                  </p>
                )}
              </>
            ) : (
              <CommonPageLoader />
            )}
          </div>
        </ModalBody>
      </Modal>
    );
  }
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators(
    {
      getProductById,
    },
    dispatch
  );
};

export const ModifyProductPopup = connect(
  null,
  mapDispatchToProps
)(ModifyProductPopupClass);
