import React, { Component } from "react";
import SimpleReactValidator from "simple-react-validator";
// Redux Connection

import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import { getProductById } from "action/ProductAct";
import { CommonPageLoader } from "component/common";
import { Modal, ModalBody } from "reactstrap";
import { currencyFormat } from "service/helperFunctions";
import "./style.scss";

export class ViewProductPopupClass extends Component {
  state = {
    formFields: {
      productName: "",
      sku: "",
      price: "",
      size: "",
      brandName: "",
      gstHst: "",
      description: "",
      category: "",
      province: "",
      subCategory: "",
      seller: {},
      commisions: "",
    },
    addImages: [],
    isLoading: false,
  };

  //validation
  validator = new SimpleReactValidator({
    element: (message) => (
      <span className="error-message text-danger fs-14">{message}</span>
    ),
    autoForceUpdate: this,
  });

  //get Product By Id
  _getDetilsById = (id) => {
    let body = {
      id,
    };
    this.setState({ isLoading: true });
    this.props.getProductById(body).then((data) => {
      if (data !== null) {
        this.setState({
          formFields: {
            productName: data.productName,
            sku: data.sku ? data.sku : "",
            price: data.price ? data.price.toString() : "",
            size: data.size ? data.size.toString() : "",
            brandName: data.brand ? data.brand : "",
            gstHst: data.taxes ? data.taxes : "",
            description: data.desc ? data.desc : "",
            category: data.category !== null ? data.category.name : "",
            province: data.province ? data.province : "",
            subCategory: data.subCategory !== null ? data.subCategory.name : "",
            seller: data.seller ? data.seller : {},
            commisions: data.commisions !== null ? data.commisions : "",
          },
          addImages: data.productImg ? data.productImg : [],
          isLoading: false,
        });
      }
    });
  };

  //lifecycles
  componentDidUpdate(prevProps) {
    if (prevProps !== this.props) {
      if (this.props.singleProductId !== "" && this.props.isOpen) {
        this._getDetilsById(this.props.singleProductId);
      }
    }
  }

  render() {
    let { formFields, addImages, isLoading } = this.state;
    let {
      productName,
      sku,
      price,
      size,
      brandName,
      gstHst,
      category,
      subCategory,
      province,
      description,
      seller,
      commisions,
    } = formFields;

    let {
      className = "modal-dialog-centered view-product-popup",
      isOpen = true,
      toggle,
    } = this.props;

    return (
      <Modal isOpen={isOpen} toggle={toggle} className={className} size="lg">
        <ModalBody>
          <div className="d-flex justify-content-between w-100 align-items-center mb-5">
            <h1 className="fs-30 text-black-22 fw-400 mb-0 text-uppercase">
              View
            </h1>
            <i
              className="fs-22 icon-close-round cursor-pointer"
              onClick={() => toggle()}
            />
          </div>
          {!isLoading ? (
            <>
              <div className="row">
                <div className="col-12">
                  <div className="row">
                    <div className="col-6">
                      <div>
                        <label className="font-weight-normal mb-1">
                          {"Seller Name:"}
                        </label>
                      </div>
                      <p className="text-black-22 fs-16 fw-500 ">
                        {seller
                          ? seller.firstName + " " + seller.lastName
                          : "-"}
                      </p>
                    </div>
                    <div className="col-6">
                      <div>
                        <label className="font-weight-normal mb-1">
                          {"Commissions"}
                        </label>
                      </div>
                      <p className="text-black-22 fs-16 fw-500 ">
                        {commisions !== null ? commisions : "-"}
                      </p>
                    </div>
                  </div>
                </div>
              </div>
              <div className="row">
                <div className="col-12">
                  <div className="row">
                    <div className="col-6">
                      <div>
                        <label className="font-weight-normal mb-1">
                          {"Product Name:"}
                        </label>
                      </div>
                      <p className="text-black-22 fs-16 fw-500 ">
                        {productName ? productName : "-"}
                      </p>
                    </div>
                    <div className="col-3">
                      <div>
                        <label className="font-weight-normal mb-1">
                          {"SKU #"}
                        </label>
                      </div>
                      <p className="text-black-22 fs-16 fw-500 ">
                        {sku ? sku : "-"}
                      </p>
                    </div>
                    <div className="col-3">
                      <div>
                        <label className="font-weight-normal mb-1">
                          {"Price"}
                        </label>
                      </div>
                      <p className="text-black-22 fs-16 fw-500 ">
                        {price ? "$" + currencyFormat(price) : "-"}
                      </p>
                    </div>
                  </div>
                </div>
              </div>
              <div className="row">
                <div className="col-12">
                  <div className="row">
                    <div className="col-6">
                      <div>
                        <label className="font-weight-normal mb-1">
                          {"Brand Name"}
                        </label>
                      </div>
                      <p className="text-black-22 fs-16 fw-500 ">
                        {brandName ? brandName : "-"}
                      </p>
                    </div>
                    <div className="col-3">
                      <div>
                        <label className="font-weight-normal mb-1">
                          {"Size"}
                        </label>
                      </div>
                      <p className="text-black-22 fs-16 fw-500 ">
                        {size ? size : "-"}
                      </p>
                    </div>
                    <div className="col-3">
                      <div>
                        <label className="font-weight-normal mb-1">
                          {"Province"}
                        </label>
                      </div>
                      <p className="text-black-22 fs-16 fw-500 ">
                        {province ? province : "-"}
                      </p>
                    </div>
                  </div>
                </div>
              </div>
              <div className="row">
                <div className="col-12">
                  <div className="row">
                    <div className="col-6">
                      <div>
                        <label className="font-weight-normal mb-1">
                          {"Category"}
                        </label>
                      </div>
                      <p className="text-black-22 fs-16 fw-500 ">
                        {category ? category : "-"}
                      </p>
                    </div>
                    <div className="col-3">
                      <div>
                        <label className="font-weight-normal mb-1">
                          {"Sub-Category"}
                        </label>
                      </div>
                      <p className="text-black-22 fs-16 fw-500 ">
                        {subCategory ? subCategory : "-"}
                      </p>
                    </div>
                    <div className="col-3">
                      <div>
                        <label className="font-weight-normal mb-1">
                          {"GST, HST %"}
                        </label>
                      </div>
                      <p className="text-black-22 fs-16 fw-500 ">
                        {gstHst ? gstHst : "-"}
                      </p>
                    </div>
                  </div>
                </div>
              </div>
              <div className="row pt-2 bottom-area">
                <div className="col-12">
                  <div className="row">
                    <div className="col-6 pr-md-5">
                      <div>
                        <label className="font-weight-normal text-black-22 mb-4 text-uppercase">
                          {"Images"}
                        </label>
                      </div>
                      <div className="row">
                        <div className="col-11 px-3">
                          {addImages.length !== 0 ? (
                            <div className="d-flex flex-wrap">
                              {addImages.map((list, index) => (
                                <div className="upload-image-card" key={index}>
                                  <div className="image-card position-relative">
                                    <img
                                      src={list}
                                      alt={"productImage"}
                                      className="w-100 h-100"
                                    />
                                  </div>
                                </div>
                              ))}
                            </div>
                          ) : (
                            <div className="d-flex flex-wrap fs-20 fw-600">
                              -
                            </div>
                          )}
                        </div>
                      </div>
                    </div>
                    <div className="col-6 pr-md-4">
                      <div>
                        <label className="font-weight-normal fs-30 text-black-22 mb-4 text-uppercase">
                          {"Description"}
                        </label>
                        <p className="text-black-22 fs-16 pb-0">
                          {description ? description : "-"}
                        </p>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </>
          ) : (
            <CommonPageLoader />
          )}
        </ModalBody>
      </Modal>
    );
  }
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators(
    {
      getProductById,
    },
    dispatch
  );
};

export const ViewProductPopup = connect(
  null,
  mapDispatchToProps
)(ViewProductPopupClass);
