import { authApi, profile } from "../service/apiVariables";
import { profileType } from "service/actionType";

// common login
export const login = (body) => (dispatch, getState, { api, Toast }) => {
  return new Promise((resolve, reject) => {
    api({ ...authApi.adminLogin, body })
      .then(({ message, data }) => {
        localStorage.setItem("AuthToken", data.token);

        resolve(data);

        Toast({ type: "success", message, time: 5000 });
      })
      .catch(({ message }) => {
        reject(Toast({ type: "error", message }));
      });
  });
};

//forgot password
export const forgetPassword = (body) => (
  dispatch,
  getState,
  { api, Toast }
) => {
  return new Promise((resolve, reject) => {
    api({ ...authApi.forgetPassword, body })
      .then(({ data }) => {
        resolve(data);
      })
      .catch(({ message }) => {
        reject(Toast({ type: "error", message }));
      });
  });
};

//forgot password validation
export const forgotPasswordTokeValidation = () => (
  dispatch,
  getState,
  { api, Toast }
) => {
  return new Promise((resolve, reject) => {
    api({ ...authApi.forgotPasswordTokeValidation })
      .then(({ status }) => {
        resolve(status);
      })
      .catch(({ message, status }) => {
        resolve(status);
        Toast({ type: "error", message });
      });
  });
};

//change Password
export const changePassword = (body) => (
  dispatch,
  getState,
  { api, Toast }
) => {
  return new Promise((resolve, reject) => {
    api({ ...authApi.changePassword, body })
      .then(({ status, message }) => {
        Toast({ type: "success", message, time: 5000 });
        resolve(status);
      })
      .catch(({ message, status }) => {
        resolve(status);
        Toast({ type: "error", message });
      });
  });
};
//get Profile Details
export const getProfileDetails = (body) => (
  dispatch,
  getState,
  { api, Toast }
) => {
  return new Promise((resolve, reject) => {
    api({ ...profile.getProfileDetails, body })
      .then(({ data }) => {
        dispatch({ type: profileType.profileDetails, payload: data });
        resolve(data);
      })
      .catch(({ message, status }) => {
        resolve(status);
        Toast({ type: "error", message });
      });
  });
};

