//page Management Details Add
export const pageManagementDetailsAdd = (id, body) => (
  dispatch,
  getState,
  { api, Toast }
) => {
  return new Promise((resolve, reject) => {
    let { pageManagementDetailsAdd } = profile;
    pageManagementDetailsAdd.id = id;
    api({ ...profile.pageManagementDetailsAdd, body })
      .then(({ data, message }) => {
        resolve(data);
        Toast({ type: "success", message });
      })
      .catch(({ message }) => {
        Toast({ type: "error", message });
      });
  });
};

